import React from "react";
import axios from 'axios';
import NotificationAlert from "react-notification-alert";
import Select from "react-select";

// reactstrap components
import {
  Row,
  Col,
  Card,
  Input,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Label,
  Button,
  CardBody,
} from "reactstrap";
import { SizeCPO } from "../components/PoComponents/NewCPO";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: '#e14eca'
  })
}

class PoManual extends React.Component {
  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;

  constructor() {
    super()
    this.state = {
      loading: false,
      productOrder: null,
      selectedClient: [],
      newCPO: {
        client: undefined,
        newStyleColor: [1,2]
      }
    }
  }

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  _loadingStyles = false;
  getStyles = () => {
    axios.get(
      this.costingURL + '/bom_header/get_account_approve_list/?flag=D',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        styles: response.data.map(style => {
          return {
            value: style.id,
            label: `${style.bom_style.name}`,
            costing_id: style.bom_style.costing_id,
            name: style.bom_style.costing,
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'costing History');
    }.bind(this));
  }

  getCallendar = () => {
    let days = [];
    let months = [];
    let years = [];
    for (let step = 1; step < 32; step++) {
      days.push({
        label: step
      })
    }
    for (let step = 1; step < 13; step++) {
      months.push({
        label: step
      })
    }
    for (let step = 2021; step < 2121; step++) {
      years.push({
        label: step
      })
    }
    this.state.days = days;
    this.state.months = months;
    this.state.years = years;
  }

  //_ClientsLoaded = false;
  getClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/get_all_clients/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._ClientsLoaded = true;
      this._isMounted && this.setState({
        clients: response.data,
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getClients');
    }.bind(this));
  }

  _userClientsLoaded = false;
  getUserClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._userClientsLoaded = true;
      this._isMounted && this.setState({
        clientsOptions: response.data.map(client => {
          return {
            value: client.id,
            label: `${client.company} / ${client.name} / ${client.abbreviation}`
          }
        })
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getUserClients');
    }.bind(this));
  }

  _loadingStyleColors = false;
  getStyleColors = (headerId) => {
    this.toggleLoading();
    axios.get(
      this.costingURL + `/bom_header/${headerId}/`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._selectedHeader = true;
      const {
        header
      } = this.state;
      const {
        data
      } = response;
      this._isMounted && this.setState({
        header: {
          ...header,
          approvalStatus: data.approvalStatus,
          bom_style: data.bom_style,
          description: data.description,
          embellishment_description: data.embellishment_description.map(embellishment_description => {
            return {
              id: embellishment_description.id,
              label: embellishment_description.color,
              value: `${embellishment_description.color_id}`
            }
          })
        },
      });
      this._loadingStyleColors = true;
      this.toggleLoading();
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, 'selected');
    }.bind(this));
  }

  _scaleLoaded = false;
  getScale = () => {
    this.toggleLoading();
    axios.get(
      this.costingURL + `/material_scale/`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._scaleLoaded = true;
      this._isMounted && this.setState({
        scales: response.data.map(scales => {
          return {
            label: `${scales.name} - ${scales.client.name}`,
            value: scales.id,
          }
        })
      });
      this.toggleLoading();
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, 'selected');
    }.bind(this));
  }

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getUserClients();
    this.getCallendar();
    this.getStyles();
    this.getScale();
  }

  render() {
    const {
      loading,
      months,
      days,
      years
    } = this.state;

    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';

    return (
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content"><div className="ui text loader">Loading</div></div>
            </div>
            <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
          </div>
        </div>
        <Card >
          <CardBody>
            <h2>
              New CPO
              <hr></hr>
            </h2>
            <Row>
              <Col sm={4}>
                <Label>No. CPO</Label>
                <Input
                  type='text'
                  placeholder='no. CPO'
                  name='name'
                  onChange={e => {
                    this.setState({
                      newCPO: {
                        cpo_number: e.target.value
                      }
                    })
                  }}
                />
              </Col>
              <Col sm={4}>
                <Label>Select Client</Label>
                {this._ClientsLoaded &&
                  <Select
                    options={this.state.clientsOptions}
                    onChange={o => {
                      this.setState({
                        newCPO: {
                          client: o.value
                        }
                      })
                    }}
                  />}
              </Col>
              <Col sm={4}>
                <Label>Date of Entry</Label>
                <Row>
                  <Col sm={4}>
                    <Select
                      placeholder="mm"
                      styles={customStyles}
                      options={months}
                      onChange={e => { this.setState({ newCPO: { entryMonth: e } }) }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="dd"
                      styles={customStyles}
                      options={days}
                      onChange={e => { this.setState({ newCPO: { entryDay: e } }) }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="yyyy"
                      styles={customStyles}
                      options={years}
                      onChange={e => { this.setState({ newCPO: { entryYear: e } }) }}
                    />
                  </Col>
                </Row>
              </Col>
              <Col sm={4}>
                <Label>Select Style</Label>
                {this._loadingStyles &&
                  <Select
                    options={this.state.styles}
                    onChange={o => {
                      this.setState({
                        newCPO: {
                          style: o.value
                        }
                      })
                      this.getStyleColors(o.value)
                    }}
                  />
                }
              </Col>
              <Col sm={4}>
                <Label>Select Color Style</Label>
                {this._loadingStyleColors &&
                  <Select
                    options={this.state.header.embellishment_description}
                    onChange={o => {
                      this.setState({
                        newCPO: {
                          SelectedColor: o.value
                        }
                      })
                    }}
                  />
                }
              </Col>
              <Col sm={4}>
                <Label>Export Date</Label>
                <Row>
                  <Col sm={4}>
                    <Select
                      placeholder="mm"
                      styles={customStyles}
                      options={months}
                      onChange={e => { this.setState({ newCPO: { exportMonth: e } }) }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="dd"
                      styles={customStyles}
                      options={days}
                      onChange={e => { this.setState({ newCPO: { exportDay: e } }) }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="yyyy"
                      styles={customStyles}
                      options={years}
                      onChange={e => { this.setState({ newCPO: { exportYear: e } }) }}
                    />
                  </Col>
                </Row>
              </Col>
              <Col sm={4}>
                <Label>Select Size Scale</Label>
                {this._scaleLoaded &&
                  <Select
                    options={this.state.scales}
                    onChange={o => {
                      this.setState({
                        newCPO: {
                          SelectedScale: o.value
                        }
                      })
                    }}
                  />
                }
              </Col>
              <Col sm={4}>
                <Label>NO TENGO ENDPOINT Select Delivery Place</Label>
                {//this._PlacesLoaded &&
                  <Select
                    options={this.state.scales}
                    onChange={o => {
                      this.setState({
                        newCPO: {
                          SelectedScale: o.value
                        }
                      })
                    }}
                  />
                }
              </Col>
              <Col sm={1}>
                <Label>Agregar</Label>
                {this.state.newCPO &&
                  <Button
                    color="info"
                    size="sm"
                    className="btn-icon"
                    onClick={() => {
                      this.setState({
                        /*newCPO: {
                          ...this.state.newCPO.newStyleColor,
                          newStyleColor: 'hola'
                        }*/
                      })
                    }}>
                    <i class="fas fa-plus"></i>
                  </Button>
                }
              </Col>
            </Row>
            {
              <Button>
                Save CPO
              </Button>
            }
            <Row>
              {this.state.newCPO.newStyleColor.map((sizes, i) => {
                return (
                  <>
                  <SizeCPO 
                  //selectedScale={}
                  //selectedStyle={}
                  //SelectedColor={}
                  //exportDate={}
                  />
                  </>
                )
              })}
            </Row>
          </CardBody>
        </Card>

      </div>
    )
  }


}

export default PoManual