import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";
import UPCResume from "../components/upc/tableUPC";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  Nav,
  Label,
  Input,
  CardHeader,
  NavItem,
  NavLink,
  CardBody,
  Button,
  TabContent,
  TabPane,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};
const CheckStyle = {
  opacity: 100,
  visibility: "visible",
};

class upcPo extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      tab: "1",
      styles: [],
      clientId: null,
      productOrder: null,
      styles_po: null,
      scaleSelected: null,
      filesForm: ["1"],
      selectedFile: null,
      uploadPO: false,
      resumeModal: false,
      uploadAllPO: false,
      resumePO: false,
      clientList: [],
      fileList: [],
      upcSelected: null,
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  toggleresumeModal = () => {
    this.setState({
      resumeModal: !this.state.resumeModal,
    });
  };

  deleteUPC = (upc_id) => {
    this.toggleLoading();
    const formData = new FormData();
    formData.append("upc_id", upc_id);
    axios
      .post(process.env.REACT_APP_IP + `/upc/change_status/`, formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function() {
          this.toggleLoading();
          this.componentDidMount();
          this.notify("tr", "success", "upc deleted!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "upc not deleted!");
        }.bind(this)
      );
  };

  getScales = (client) => {
    axios
      .get(
        this.costingURL +
          `/material_scale/get_scale_by_client/?client=${client}`,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function(response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              scales: response.data.map((scale) => {
                return {
                  value: scale.id,
                  label: `${scale.name} / ${scale.client.name}`,
                  name: scale.name,
                  sizes: scale.sizes,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Failed Load Sizes Scales");
        }.bind(this)
      );
  };

  getUPCs = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + `/upc/get_all_upcs/`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            productOrder: response.data.map((upc) => {
              return {
                upc_id: upc.id,
                client: upc.client.name,
                style: upc.style_name,
                color: upc.color.name,
                sizes: upc.sizes,
                actions: (
                  <>
                    <Button
                      onClick={() => {
                        this.toggleresumeModal();
                        this.setState({
                          setName: upc.style_name,
                          setColor: upc.color.name,
                          setID: upc.id,
                          upcSelected: upc,
                        });
                      }}
                      color="info"
                      size="sm"
                    >
                      <i className="fa fa-search" />
                    </Button>
                    <Button
                      onClick={() => {
                        this.deleteUPC(upc.id);
                      }}
                      color="danger"
                      size="sm"
                    >
                      <i className="fa fa-times" />
                    </Button>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  uploadChangeFileUPC = (event) => {
    this.toggleLoading();
    for (const i in this.state.fileList) {
      const formData = new FormData();
      formData.append("file", this.state.fileList[0]);
      formData.append("clients", this.state.clientList[i]);
      axios
        .post(process.env.REACT_APP_IP + `/upc/create_upc/`, formData, {
          headers: {
            Authorization: "Token " + this.userToken,
            "Content-Type": "multipart/form-data",
          },
        })
        .then(
          function() {
            this.toggleLoading();
            this.componentDidMount();
            this.getUPCs()
            this.notify("tr", "success", "Upload File PO ");
          }.bind(this)
        )
        .catch(
          function(error) {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "warning", "Bad document format ");
          }.bind(this)
        );
    }
    this.toggleUploadPOModal();
    this.state.selectedClientPo.label = "";
  };

  getColorsFromStyle = (id) => {
    if (null) {
      this.notify("tr", "warning", "Please select a Style before");

      return;
    } else {
      axios
        .get(
          this.costingURL + `/po/get_colors_for_style_po/?bom_id=${id}`,

          {
            headers: {
              Authorization: "Token " + this.userToken,
            },
          }
        )
        .then(
          function(response) {
            //this._loadingStyles = true;

            this.setState({
              //colorsInBom: [],

              colorInBom: response.data.map((color) => {
                return {
                  value: color.id,

                  label: `${color.name}`,

                  name: color.name,
                };
              }),
            });
          }.bind(this)
        )
        .catch(
          function(error) {
            this.handleError(error, "Failed Load Colors");
          }.bind(this)
        );
    }
  };

  getStylesFromBOM = (clientId) => {
    axios
      .get(this.costingURL + `/po/get_styles_from_bom/?client_id=${clientId}`, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this.toggleLoading();
          this.notify("tr", "success", "Client data has been found");
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              styles: response.data.map((costsheet) => {
                return {
                  value: costsheet.id,
                  label: `${costsheet.name}`,
                  costing_id: costsheet.costing_id,
                  name: costsheet.name,
                  id_style: costsheet.id_style,
                  id: costsheet.id,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Failed Load Colors");
        }.bind(this)
      );
  };

  getPoResume = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/get_resume_upc/?client=${this.state.selectedClientPo}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
              `/admin/get_resume_upc/?client=${this.state.selectedClientPo}`
          );
          this.notify("tr", "success", "Resume downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  approveUpc = (upc_id) => {
    this.toggleLoading();
    const formData = new FormData();
    formData.append("upc_id", upc_id);
    axios
      .post(process.env.REACT_APP_IP + `/upc/approve_upc/`, formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function() {
          this.toggleLoading();
          this.componentDidMount();
          this.notify("tr", "success", "upc Approved!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Approved");
        }.bind(this)
      );
  };

  approveAllUpc = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/po/approve_all_upc/?client=${this.state.selectedClientPo.value}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function() {
          this.notify("tr", "success", "upc Approved!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Approved");
        }.bind(this)
      );
  };

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this.toggleLoading();
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  toggleUploadAllupcModal = () => {
    this.setState({
      uploadAllPO: !this.state.uploadAllPO,
    });
  };

  toggleUploadPOModal = () => {
    this.setState({
      uploadPO: !this.state.uploadPO,
    });
  };

  toggleGetResumeModal = () => {
    this.setState({
      resumePO: !this.state.resumePO,
    });
  };

  addEvent = () => {
    const listForm = [...this.state.filesForm];
    listForm.push("1");
    this.setState({
      filesForm: listForm,
    });
  };

  addFileToList = (event) => {
    this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  addClientToList = (clientId) => {
    const list = [...this.state.clientList];
    list.push(clientId.value);
    this.setState({
      clientList: list,
    });
  };

  resetModal = () => {
    const listForm = ["1"];
    this.setState({
      filesForm: listForm,
    });
  };

  componentDidMount() {
    this._isMounted = true;
    this.getUPCs();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  createUPC = () => {
    this.toggleLoading();
  
    const final = {
      client: this.state.clientId,
      scale: this.state.scaleId,
      sizes: this.state.scaleSelected.sizes,
      color: this.state.colorId,
      style: this.state.styleSelected
    };
    axios
      .post(this.costingURL + "/upc/", final, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function() {
          this.notify("tr", "success", "UPC Created!");
          this.toggleLoading();
          this.componentDidMount();
          this.setState({
            isApproved: true
          })
          //window.location.reload();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "UPC not Created");
          this.toggleLoading();
        }.bind(this)
      );
  };

  render() {
    const {
      loading,
      productOrder,
      styles,
      tab,
      uploadPO,
      filesForm,
      clients,
      scales,
      colorInBom,
      scaleSelected,
    } = this.state;
    const pageMode = this.checkPageMode();
    const colorsMod = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Modal
          isOpen={uploadPO}
          toggle={this.toggleUploadPOModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <h3>Upload PO Files</h3>
            <>
              <Row>
                {filesForm &&
                  filesForm.map((newFile) => {
                    return (
                      <>
                        <Col sm={7}>
                          <Select
                            placeholder="Select Client..."
                            styles={customStyles}
                            options={clients}
                            onChange={(e) => {
                              this.setState({
                                selectedClientPo: e,
                              });
                              this.addClientToList(e);
                            }}
                          />
                        </Col>
                        <Col sm={3}>
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                            >
                              <input
                                type="file"
                                onChange={this.addFileToList}
                                name="pathToCP_POS"
                                id="input_pathToTechpack"
                                multiple="multiple"
                              />
                              <i className="tim-icons icon-cloud-upload-94" />
                              &nbsp;Po File
                            </Button>
                          </FormGroup>
                        </Col>
                      </>
                    );
                  })}
                <Col sm={2}>
                  {this.state.selectedClientPo &&
                    this.state.selectedClientPo.label === "VANS" && (
                      <Button
                        className="btn-simple"
                        color="primary"
                        size="sm"
                        onClick={this.addEvent}
                      >
                        <i class="fas fa-plus"></i>
                      </Button>
                    )}
                </Col>
              </Row>
            </>
            <ModalFooter>
              <Button
                color="secondary"
                onClick={() => {
                  this.toggleUploadPOModal();
                  this.resetModal();
                }}
              >
                Close
              </Button>
              <Button
                color="primary"
                onClick={(event) => this.uploadChangeFileUPC(event)}
              >
                Save Changes
              </Button>
            </ModalFooter>
          </ModalBody>
        </Modal>
        <Modal
          isOpen={this.state.resumeModal}
          toggle={this.toggleresumeModal}
          size={"lg"}
          style={{ margin: "1vh auto 0px" }}
        >
          <ModalHeader toggle={this.toggleresumeModal} tag={"h2"}>
            {this.state.setName} / {this.state.setColor}
          </ModalHeader>
          <ModalBody>
            <UPCResume
              resume={true}
              data={(this.state.productOrder, this.state.upcSelected)}
            />
            <Button
              style={{ marginTop: "2vh" }}
              color="success"
              onClick={() => {
                this.approveUpc(this.state.setID);
                this.toggleresumeModal();
              }}
            >
              Approve
            </Button>
          </ModalBody>
        </Modal>

        <Nav tabs>
          <NavItem>
            <NavLink
              className={tab === "1" ? "active" : ""}
              onClick={() => {
                this.toggle("1");
              }}
              style={
                pageMode.bg === "bg-ligh"
                  ? {
                      color: "black",
                      borderColor: "black",
                    }
                  : {}
              }
            >
              UPCs
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={tab === "2" ? "active" : ""}
              onClick={() => {
                this.toggle("2");
                this.getClients();
              }}
              style={
                pageMode.bg === "bg-ligh"
                  ? {
                      color: "black",
                      borderColor: "black",
                    }
                  : {}
              }
            >
              Create UPC
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={tab}>
          <TabPane tabId="1">
            <Row className="justify-content-center mt-5">
              <Col md={{ size: 10, order: 2 }}>
                <Card>
                  <CardHeader>
                    <CardBody>
                      <Row>
                        <Col sm="9">
                          <h1>PO UPCs</h1>
                        </Col>
                        <Col sm="3">
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <h4>&nbsp; &nbsp;Add File for UPCs</h4>
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                              onClick={() => {
                                this.getClients();
                                this.toggleUploadPOModal();
                              }}
                            >
                              {/*<input
                          type="file"
                          onChange={this.uploadChangeFilePO}
                          name="pathToCP_POS"
                          id="input_pathToTechpack"
                        />*/}
                              <i className="tim-icons icon-cloud-upload-94" />
                              &nbsp; Upload PO File
                            </Button>
                          </FormGroup>
                        </Col>

                        <br></br>
                        {productOrder && (
                          <>
                            <Card className={`${pageMode.bg} border`}>
                              <CardBody>
                                <ReactTable
                                  responsive
                                  defaultPageSize={5}
                                  data={productOrder}
                                  filterable
                                  showPaginationTop
                                  showPaginationBottom={false}
                                  className="-striped -highlight primary-pagination"
                                  columns={[
                                    {
                                      Header: "Client",
                                      accessor: "client",
                                      sortable: true,
                                      filterMethod: (filter, row) =>
                                        matchSorter(row, filter.value, {
                                          keys: ["client"],
                                        }),
                                      filterAll: true,
                                      width: 350,
                                    },
                                    {
                                      Header: "Style",
                                      accessor: "style",
                                      sortable: true,
                                      filterMethod: (filter, row) =>
                                        matchSorter(row, filter.value, {
                                          keys: ["style"],
                                        }),
                                      filterAll: true,
                                      width: 400,
                                    },
                                    {
                                      Header: "Color",
                                      accessor: `color`,
                                      sortable: true,
                                      filterMethod: (filter, row) =>
                                        matchSorter(row, filter.value, {
                                          keys: ["color"],
                                        }),
                                      filterAll: true,
                                      width: 400,
                                    },
                                    {
                                      Header: "Actions",
                                      accessor: "actions",
                                      sortable: false,
                                      filterable: false,
                                    },
                                  ]}
                                />

                                {this.Role === "IT Admin" && (
                                  <>
                                    <Button
                                      sm={5}
                                      color="primary"
                                      onClick={() => {
                                        this.toggleUploadAllupcModal();
                                      }}
                                    >
                                      Approve All UPCs
                                    </Button>
                                    <Modal
                                      isOpen={this.state.uploadAllPO}
                                      toggle={this.toggleUploadAllupcModal}
                                      modalClassName={this.checkPageMode().mc}
                                    >
                                      <ModalBody>
                                        <h3>Approve All UPCs</h3>
                                        <>
                                          <Row>
                                            <Col sm={7}>
                                              <Select
                                                placeholder="Select Client"
                                                styles={customStyles}
                                                options={this.state.clients}
                                                onChange={(e) => {
                                                  this.setState({
                                                    selectedClientPo: e,
                                                  });
                                                }}
                                              />
                                            </Col>
                                            <Col sm={3}>
                                              <FormGroup
                                                id="formGroup_pathToTechpack"
                                                className="text-center"
                                              >
                                                <Button
                                                  className="btn-simple"
                                                  color="info"
                                                  size="sm"
                                                  onClick={this.approveAllUpc}
                                                >
                                                  Approve
                                                </Button>
                                              </FormGroup>
                                            </Col>
                                          </Row>
                                        </>
                                        <ModalFooter>
                                          <Button
                                            color="secondary"
                                            onClick={() => {
                                              this.toggleUploadAllupcModal();
                                              this.resetModal();
                                            }}
                                          >
                                            Close
                                          </Button>
                                        </ModalFooter>
                                      </ModalBody>
                                    </Modal>
                                  </>
                                )}
                              </CardBody>
                            </Card>
                          </>
                        )}
                      </Row>
                    </CardBody>
                  </CardHeader>
                </Card>
              </Col>
            </Row>
          </TabPane>
        </TabContent>

        <TabContent activeTab={tab}>
          <TabPane tabId="2">
            <Row className="justify-content-center mt-5">
              <Col md={{ size: 10, order: 2 }}>
                <Card>
                  <CardHeader>
                    <CardBody>
                      <Col>
                        <h1>Create UPCs</h1>
                        <hr />
                      </Col>
                      <br></br>
                      <Col>
                        <Col>
                          <FormGroup row>
                            <Label
                              for="exampleEmail"
                              className="text-primary"
                              sm={2}
                            >
                              <Select
                                placeholder="Select Client..."
                                styles={customStyles}
                                isDisabled={this.state.clientId ? true : false}
                                options={clients}
                                onChange={(e) => {
                                  this.setState({
                                    clientId: e.value,
                                  });
                                  this.getScales(e.value);
                                  this.getStylesFromBOM(e.value);
                                  this.notify("tr", "info", "Searching client data...");
                                  this.toggleLoading();
                                  
                                }}
                              />
                            </Label>
                            <Label
                              for="exampleEmail"
                              className="text-primary"
                              sm={2}
                            >
                              <Select
                                placeholder="Select Scale"
                                styles={customStyles}
                                isDisabled={this.state.scaleId ? true : false}
                                options={scales}
                                onChange={(e) => {
                                  this.setState({
                                    scaleId: e.value,
                                    scaleSelected: e,
                                  });
                                }}
                              />
                            </Label>
                            <Label
                              for="exampleEmail"
                              className="text-primary"
                              sm={4}
                            >
                              <Select
                                placeholder="Search Style"
                                styles={customStyles}
                                options={styles}
                                onChange={(option) => {
                                  // console.log(option.value);
                                  this.setState({
                                    newStyle: option,
                                    styleHasCHange: true,
                                    styleSelected: option.id,
                                    styleNameSel: option.label,
                                  });
                                  this.getColorsFromStyle(option.id);
                                  //console.log(this.state.newStyle)
                                }}
                                //value={this.state.selectedStyle}
                              />
                            </Label>
                            <Label
                              for="exampleEmail"
                              className="text-primary"
                              sm={4}
                            >
                              <Select
                                placeholder="Select Color..."
                                styles={customStyles}
                                options={colorInBom}
                                onChange={(e) => {
                                  this.setState({
                                    colorId: e.value,
                                  });
                                  // this.getStylesFromBOM(e.value);
                                }}
                              />
                            </Label>
                          </FormGroup>
                        </Col>
                      </Col>
                    </CardBody>
                  </CardHeader>
                </Card>
                <Card className={`${colorsMod.bg} border`}>
                  <CardBody>
                    <Row>
                      {scaleSelected &&
                        scaleSelected.sizes &&
                        scaleSelected.sizes.map((index, key) =>
                        
                       (   index.si_code !== "00" ? (
                        scaleSelected.sizes[key].upc ?  scaleSelected.sizes[key].upc : (scaleSelected.sizes[key].upc = 0),
                            <Col>
                              {index.name}
                              <Input
                                type="number"
                                 maxLength="20"
                                // value={
                                //   poDetail[id].scalepoDetail.sizes[idsize].unitprice ?  poDetail[id].scalepoDetail.sizes[idsize].unitprice : 0
                                // }
                                defaultValue = {scaleSelected.sizes[key].upc ?  scaleSelected.sizes[key].upc : 0}
                                onChange={(e) => {
                                  scaleSelected.sizes[key].upc = e.target.value
                                }}
                              />
                            </Col>
                          ) : (
                            <></>
                          )
                          )
                        )}
                    </Row>
                    <br />
                    <Button
                                      sm={5}
                                      color="primary"
                                      onClick={() => {
                                        this.createUPC();
                                      }}
                                    >
                                      Create UPC
                                    </Button>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </div>
    );
  }
}
export default upcPo;
