import React from "react";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import axios from "axios";
import VacationsTable from "../components/RRHH/VacationsTable";


import {
  Form,
  Button,
  TabContent,
  TabPane,
  Col,
  Row,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
  CardHeader,
  Nav,
  NavItem,
  NavLink,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  CardTitle
} from "reactstrap";
import Select from "react-select";
import moment from 'moment'

import { matchSorter } from "match-sorter";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";



const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};
const CheckStyle = {
  opacity: 100,
  visibility: "visible",
};
class Permissions extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  userName = localStorage.getItem("user_name") || "";
  userID = localStorage.getItem("user_id") || "";
  userRole = localStorage.getItem("role") || "";

  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;

  constructor() {
    super();
    this.state = {
      loading: false,
      tab: "1",
      code: "",
      cycle: 0,
      dateS: "",
      dateF: "",
      texsun: "",
      texfor: "",
      openModal: false,

      innotex: "",
      years: [],
      date: "",
      initDate: "",
      finalDate: "",
      vacationsData: [],
      db: [
        {
          'label': 'TEXSUN',
          'value': 'TEXSUN',
        },
        {
          'label': 'TEXFOR',
          'value': 'TEXFOR',
        },
        {
          'label': 'INNOTEX',
          'value': 'INNOTEX',
        },
      ],
      dbNomina: [
        {
          'label': 'NOMINA TEXSUN',
          'value': 'NOMINA TEXSUN',
        },
        {
          'label': 'NOMINA TEXFOR',
          'value': 'NOMINA TEXFOR',
        },
        {
          'label': 'NOMINA INNOTEX',
          'value': 'NOMINA INNOTEX',
        },
      ],
      dbSelected: null,
      payroll: null,
    };
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  toggleModal = () => {
    this.setState({
      openModal: !this.state.openModal,
    });
  };



  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };
  toggleLoadingWAIT = () => {
    setTimeout(() => {
      this.toggleLoading();
    }, 1000);
    this.toggleLoading()
  };

  deletePermission = (code, date, dbSelected) => {
    axios
      .get(this.costingURL + `/users_it/delete_permission/?code=${code}&date=${date}&db=${dbSelected}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          console.log(data, 'data')
          this.notify("tr", "success", "Permission Deleted!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Deleted Permission");
        }.bind(this)
      );
  };

  updateSuspension = (code, dateS, dateF, param) => {
    axios
      .get(this.costingURL + `/users_it/update_suspencion/?code=${code}&dateS=${dateS}&dateF=${dateF}&flag=${param}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          console.log(data, 'data')
          this.notify("tr", "success", "Suspension Edited!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Edited Suspension");
        }.bind(this)
      );
  };

  extraHours = (date, db, payroll) => {
    axios
      .get(this.costingURL + `/users_it/extra_hours/?init_date=${date}&db=${db}&planilla=${payroll}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  Deletevacations = (date, db, code) => {
    const formData = new FormData();
    formData.append('cod_empleado',code)
    formData.append('fecha',date)
    formData.append('empresa',db)
    axios
      .delete(this.costingURL + `/rrhh/vacation/`, formData,{
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error");
        }.bind(this)
      );
  };

  getVacations = (code, cycle, db, initDate, finalDate) => {
    axios
      .get(this.costingURL + `/rrhh/vacation/?cod_empleado=${code}&ciclo=${cycle}&empresa=${db}&fecha_inicial=${initDate}&fecha_final=${finalDate}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({ vacationsData: data });
          this.toggleModal()
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  CreateVacations = (code, cycle, db, date) => {
    axios
      .get(this.costingURL + `/rrhh/add_vacation/?cod_empleado=${code}&ciclo_vacaciones=${cycle}&empresa=${db}&fecha=${date}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Vacaciones Creadas Exitosamente!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  componentDidMount() {
    this._isMounted = true;
  }

  render() {
    const { tab, loading, code, dateF, dateS, texsun, texfor, innotex, date, dbSelected, payroll, initDate, finalDate, cycle } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    var valid = function (current) {
      return current.day() !== 0 && current.day() !== 6 && current.day() !== 1 && current.day() !== 2 && current.day() !== 4 && current.day() !== 5;
    };
    const today = new Date();
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <Card>
          <CardHeader>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Nav tabs>
                {<NavItem>
                  <NavLink
                    className={tab === "1" ? "active" : ""}
                    onClick={() => {
                      this.toggle("1");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                          color: "black",
                          borderColor: "black",
                        }
                        : {}
                    }
                  >
                    Delete Permissions
                  </NavLink>
                </NavItem>}
                {
                  <NavItem>
                    <NavLink
                      className={tab === "2" ? "active" : ""}
                      onClick={() => {
                        this.toggle("2");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"Delete Vacations"}
                    </NavLink>
                  </NavItem>}

                {
                  <NavItem>
                    <NavLink
                      className={tab === "3" ? "active" : ""}
                      onClick={() => {
                        this.toggle("3");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"Consult Employee Vacations"}
                    </NavLink>
                  </NavItem>

                }

                {
                  <NavItem>
                    <NavLink
                      className={tab === "4" ? "active" : ""}
                      onClick={() => {
                        this.toggle("4");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"Create Vacations"}
                    </NavLink>
                  </NavItem>

                }


              </Nav>
              <br></br>
              <TabContent activeTab={tab}>
                <TabPane tabId="1">
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Fecha</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              date: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Empresa</Label>
                        <Select
                          options={this.state.db}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.deletePermission(code, date, dbSelected)
                  }
                  }>
                    Delete Suspension
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="2">
                  <h3>Information</h3>
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Fecha</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              date: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Empresa</Label>
                        <Select
                          options={this.state.db}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.Deletevacations(date, dbSelected, code)
                  }
                  }>
                    Update Suspension
                  </Button>
                </TabPane>
              </TabContent>

              <TabContent activeTab={tab}>
                <TabPane tabId="3">
                  <h3>Information</h3>
                  <Row>
                    <Col md={2}>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>

                    <Col md={2}>
                      <FormGroup>
                        <Label>Ciclo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              cycle: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>

                    <Col md={3}>
                      <FormGroup>
                        <Label>Empresa</Label>
                        <Select
                          options={this.state.dbNomina}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>

                    <Col md={2}>
                      <FormGroup>
                        <Label>Fecha Inicial</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          //isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              initDate: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>

                    <Col md={2}>
                      <FormGroup>
                        <Label>Fecha Final</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          //isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              finalDate: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.getVacations(code, cycle, dbSelected, initDate, finalDate)
                  }
                  }>
                    Get Vacations
                  </Button>
                </TabPane>
              </TabContent>

              <TabContent activeTab={tab}>
                <TabPane tabId="4">
                  <h3>Information</h3>
                  <Row>
                    <Col md={3}>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>

                    <Col md={3}>
                      <FormGroup>
                        <Label>Empresa</Label>
                        <Select
                          options={this.state.dbNomina}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>

                    <Col md={3}>
                      <FormGroup>
                        <Label>Fecha</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          //isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              date: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>

                    <Col md={3}>
                      <FormGroup>
                        <Label>Ciclo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              cycle: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>

                  <Button color="info" onClick={() => {
                    this.CreateVacations(code, cycle, dbSelected, date)
                  }
                  }>
                    Create Vacations
                  </Button>
                </TabPane>
              </TabContent>



            </CardBody>
          </CardHeader>
        </Card>

        <Modal
          isOpen={this.state.openModal}
          toggle={this.toggleModal}
          modalClassName={this.checkPageMode().mc}

        >
          <ModalBody

          >
            <h3>Vacaciones de Empleado #{code} en {dbSelected}</h3>


            <VacationsTable props={this.state.vacationsData} />
            <ModalFooter>
              <Button
                color="secondary"
                onClick={() => {
                  this.toggleModal();

                }}
              >
                Close
              </Button>
            </ModalFooter>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}
export default Permissions;
