import React from "react";
import Select from "react-select";

import {
  Col,
  Table,
  FormGroup,
  Card,
  CardBody,
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  CardHeader,
  Row,
} from "reactstrap";

import axios from "axios";
import { POCard } from "../components/PoComponents/POCard";
import NotificationAlert from "react-notification-alert";
import ConsumptionCard from "../components/QuotationElements/ConsumptionCard";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};
 

class PoCreator extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      poNumber: null,
      poValid: false,
      deliveryPlace: null,
      deliveryPlacePo: null,
      styles_bom_for_po: null,
      styles_per_po: [],
      unique_styles_po: [],
      loadingCard: false,
      editModal: false,
      isApproved: false,
      editModalStyle: false,
      editModalColor: false,
      editModalColor1: false,
      editModalPrice: false,
      editModalScale: false,
      editModalPlace: false,
      editModalDateClient: false,
      newModalPlace: false,
      styleHasCHange: false,
      style_costing: [],
      days: [],
      months: [],
      year: [],
      styles: [],
      styles1: [],
      colors: [],
      styleName: "",
      colorName: "",
      colorClient: "",
      unitprice: "",
      selectedMonth: "",
      selectedDay: "",
      selectedYear: "",
      poDetail: [],
      cardSelected: null,
      datePO: "",
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  createPlace = () => {
    this.toggleLoading();
    if (this.state.newPlacename !== undefined || this.state.newPlaceClient !== undefined
      || this.state.newPlaceMin !== undefined || this.state.newPlaceMax !== undefined
      || this.state.newPlaceCode !== undefined
    ) {
      let newPlace = new FormData();
      newPlace.append('name', this.state.newPlacename);
      newPlace.append('client', this.state.newPlaceClient.value);
      newPlace.append('min', this.state.newPlaceMin);
      newPlace.append('max', this.state.newPlaceMax);
      newPlace.append('clients_code', this.state.newPlacename);
      newPlace.append('sales_person', this.state.newPlacePerson ? this.state.newPlacePerson : 'None');
      newPlace.append('termination_string', this.state.newPlacename);
      newPlace.append('commission', this.state.newPlaceCommision ? this.state.newPlaceCommision : '0.0');
      axios.post(
        this.costingURL + '/destionation/',
        newPlace,
        { headers: { "Authorization": "Token " + this.userToken } }
      ).then(function () {
        this.notify("tr", "success", "Place Created!");
        this.toggleLoading();
        this.getDestinationCLient(this.state.clientId)
        this.componentDidMount();
      }.bind(this)).catch(function (error) {
        this.handleError(error, "PO not Deleted")
        this.toggleLoading();
      }.bind(this))

    }
    else {
      this.notify("tr", "warning", "Please fill all before create");
      return;
    }

  }

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getPlaces = () => {
    axios
      .get(this.costingURL + "/destionation/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              places: response.data.map((place) => {
                return {
                  value: place.id,
                  label: `${place.name} / ${place.clients}`,
                  name: place.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Failed Load Sizes Scales");
        }.bind(this)
      );
  };

  getStylesFromBOM = (clientId) => {
    this.toggleLoading()
    axios
      .get(this.costingURL + `/po/get_styles_from_bom/?client_id=${clientId}`, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              styles: response.data.map((costsheet) => {
                return {
                  value: costsheet.id,
                  label: `${costsheet.name}`,
                  costing_id: costsheet.costing_id,
                  name: costsheet.name,
                  id_style: costsheet.id_style,
                  id: costsheet.id,
                };
              }),
            });
            this.toggleLoading()
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Failed Load Colors");
        }.bind(this)
      );
  };

  getScales = (client) => {
    axios
      .get(this.costingURL + `/material_scale/get_scale_by_client/?client=${client}`, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              scales: response.data.map((scale) => {
                return {
                  value: scale.id,
                  label: `${scale.name} / ${scale.client.name}`,
                  name: scale.name,
                  sizes: scale.sizes
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Failed Load Sizes Scales");
        }.bind(this)
      );
  };

  getColorsFromStyle = (id) => {
    this.toggleLoading()
    if (null) {
      this.notify("tr", "warning", "Please select a Style before");

      return;
    } else {
      axios
        .get(
          this.costingURL + `/po/get_colors_for_style_po/?bom_id=${id}`,

          {
            headers: {
              Authorization: "Token " + this.userToken,
            },
          }
        )
        .then(
          function(response) {
            //this._loadingStyles = true;
            this.toggleLoading()
            this.setState({
              //colorsInBom: [],

              colorInBom: response.data.map((color) => {
                return {
                  value: color.id,

                  label: `${color.name}`,

                  name: color.name,
                };
              }),
            });
          }.bind(this)
        )
        .catch(
          function(error) {
            this.handleError(error, "Failed Load Colors");
          }.bind(this)
        );
    }
  };
  getDatePO = (id, month, day, year) => {
    let date = new Date(
      parseInt(year) + "-" + parseInt(month) + "-" + parseInt(day)
    );
    date.setDate(date.getDate() - 7);
    // console.log((date.getMonth()+1) +'/'+date.getDate()+'/'+date.getFullYear(), 'dateaeda')
    this.setState({
      datePO: date,
    });
    this.state.poDetail[id].exportDatepoDetail =
      date.getMonth() + 1 + "/" + date.getDate() + "/" + date.getFullYear();
  };
  getCallendar = () => {
    let days = [];
    let months = [];
    let years = [];
    for (let step = 1; step < 32; step++) {
      days.push({
        label: step,
      });
    }
    for (let step = 1; step < 13; step++) {
      months.push({
        label: step,
      });
    }
    for (let step = 2023; step < 2030; step++) {
      years.push({
        label: step,
      });
    }
    this.state.days = days;
    this.state.months = months;
    this.state.years = years;
  };

  getDestinationCLient = (client) => {
    axios
      .get(this.costingURL + `/destionation/get_destination_by_client/?client=${client}`, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              places: response.data.map((place) => {
                return {
                  value: place.id,
                  label: `${place.name} / ${place.clients}`,
                  name: place.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Failed Load Sizes Scales");
        }.bind(this)
      );
  };

  componentDidMount() {
    this._isMounted = true;
    this.getCallendar();
    this.getClients();
    //this.getPlaces();
    //this.getScales();
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  toggleEditModal = (id) => {
    this.setState({
      editModal: !this.state.editModal,
      cardSelected: id,
    });
  };

  toggleEditModalStyle = (id) => {
    this.setState({
      editModalStyle: !this.state.editModalStyle,
      cardSelected: id,
    });
  };

  toggleEditModalColor = (id) => {
    this.setState({
      editModalColor: !this.state.editModalColor,
      cardSelected: id,
    });
  };

  toggleEditModalColor1 = (id) => {
    this.setState({
      editModalColor: !this.state.editModalColor,
      cardSelected: id,
    });
  };

  toggleEditModalPrice = (id) => {
    this.setState({
      editModalPrice: !this.state.editModalPrice,
      cardSelected: id,
    });
  };

  toggleEditModalScale = (id) => {
    this.setState({
      editModalScale: !this.state.editModalScale,
      cardSelected: id,
    });
  };

  toggleEditModalPlace = (id) => {
    this.setState({
      editModalPlace: !this.state.editModalPlace,
      cardSelected: id,
    });
  };
  
  toggleEditModalDate = (id, date) => {
    this.setState({
      editModalDateClient: !this.state.editModalDateClient,
      cardSelected: id,
    });
  };

  toggleModalNewPlace = (id) => {
    this.setState({
      newModalPlace: !this.state.newModalPlace,
      cardSelected: id,
    });
  };

  removeCard = (id) => {
    console.log(this.state.poDetail, 'remove')
    this.toggleLoading()
    this.state.poDetail = this.state.poDetail.filter((object) => {
      return object.id !== id;
    });
    
    this.setState({
      loadingCard: !this.state.loadingCard
    })
    //this.state.poDetail[id] = null
    //var res = this.state.poDetail.filter(elements => {
    //  return elements !== null;
    // });
    // this.state.poDetail = res
    console.log(this.state.poDetail, 'removeFinish')
    
    setTimeout(
      function() {
        this.toggleLoading()
        this.setState({
          loadingCard: !this.state.loadingCard
        })
      }
      .bind(this),
      1000
  );
    
  };

  addNewCard = () => {
    this.toggleLoading()
    this.setState({
      loadingCard: !this.state.loadingCard
    })
    console.log(this.state.poDetail.length, 'length')
    this.state.poDetail[this.state.poDetail.length - 1].status = 'old'
    this.state.poDetail.push({
      stylepoDetail: "",
      styleClientpoDetail: "",
      colorpoDetail: null,
      colorClientpoDetail: "",
      exportDatepoDetail: "",
      exportDateClient: "",
      scalepoDetail: "",
      status: 'new',
     id: this.state.poDetail.length
    });
    setTimeout(
      function() {
        this.toggleLoading()
        this.setState({
          loadingCard: !this.state.loadingCard
        })
      }
      .bind(this),
      1000
  );
  };

  CopyNewCard = (id) => {
    this.toggleLoading()
    this.setState({
      loadingCard: !this.state.loadingCard
    })


    this.state.poDetail.push({
      id: this.state.poDetail.length,
      stylepoDetail: this.state.poDetail[id].stylepoDetail,
      styleClientpoDetail: this.state.poDetail[id].styleClientpoDetail,
      colorpoDetail: null,
      colorClientpoDetail: "",
      exportDatepoDetail: this.state.poDetail[id].exportDatepoDetail,
      exportDateClient: this.state.poDetail[id].exportDateClient,
      scalepoDetail: this.state.poDetail[id].scalepoDetail,
      status: 'old'
      //scalepoDetail: this.state.selectedScale
    });
    setTimeout(
      function() {
        this.toggleLoading()
        this.setState({
          loadingCard: !this.state.loadingCard
        })
      }
      .bind(this),
      1000
  );
  };

  getPOValid = () => {
    let process = new FormData();
    process.append("po_number", this.state.poNumber);
    axios
      .post(this.costingURL + "/po/check_po/", process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._loadingSelectedComponentStyles = true;
          this._isMounted && this.setState({ poValid: response.data.flag });
          if(response.data.flag === false){
          this.notify("tr", "warning", response.data.message);
          } else{
          this.notify("tr", "success", response.data.message);
          }
          //this.handleError(response, response.data.message);
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Error Loading");
        }.bind(this)
      );
  };
  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  approvePo = (id) => {
    //console.log(this.state.selectedPO.details)
    this.toggleLoading();
    let toapprove = [...this.state.poDetail];
    //console.log('toapprove',toapprove)
    let details = [];
    for (const i in toapprove) {
      //console.log(inputObj2)
      if (toapprove[i].stylepoDetail === "") {
        //console.log('no hay estilo')
        this.notify("tr", "warning", "Please fill all styles");
        return;
      } else {
        if (
          toapprove[i].colorpoDetail === "" ||
          toapprove[i].colorpoDetail === null
        ) {
          this.notify("tr", "warning", "Please fill all colors");
          return;
        } else {
          if (toapprove[i].exportDatepoDetail === "") {
            this.notify("tr", "warning", "Please fill all exportation dates");
            return;
          } else {
            if (toapprove[i].scale === "None") {
              this.notify("tr", "warning", "Please fill all scales");
              return;
            } else {
              if (this.state.deliveryPlace === null) {
                this.notify("tr", "warning", "Please fill the Delivery Place");
                return;
              } else {
                // console.log('imprimo antes del final', toapprove[i].sizes[0].clients_unit_price)
                let inputObj = {
                  //detail_id: toapprove[i].id,
                  style: `${toapprove[i].stylepoDetail &&
                    toapprove[i].stylepoDetail.id}`,
                  color: `${toapprove[i].colorpoDetail &&
                    toapprove[i].colorpoDetail.value}`,
                  exportation_date: toapprove[i].exportDatepoDetail,
                  //unit_price: `${toapprove[i].scalepoDetail && toapprove[i].scalepoDetail.sizes[i].unitprice}`,
                  scale: `${toapprove[i].scalepoDetail &&
                    toapprove[i].scalepoDetail.value}`,
                  sizes:
                    toapprove[i].scalepoDetail &&
                    toapprove[i].scalepoDetail.sizes,
                  //client info
                  clients_color: toapprove[i].colorClientpoDetail,
                  clients_export_date: toapprove[i].exportDateClient,
                  clients_style: toapprove[i].styleClientpoDetail,
                };
                details.push(inputObj);
                console.log("cada for", inputObj);
              }
            }
          }
        }
      }
    }
    const final = {
      po_number: this.state.poNumber,
      real_delivery_place: this.state.deliveryPlacePo,
      details: details,
      client: this.state.clientId,
      clients_delivery_place: this.state.deliveryPlace,
    };
    //console.log('DELIVERY PLACE',final)

    axios
      .post(this.costingURL + "/po/create_po_fenix/s", final, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function() {
          this.notify("tr", "success", "Po Created!");
          this.toggleLoading();
          this.componentDidMount();
          this.setState({
            isApproved: true
          })
          //window.location.reload();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Created");
          this.toggleLoading();
        }.bind(this)
      );
  };



  render() {
    const {
      loading,
      poNumber,
      colors,
      scales,
      styles,
      selectedPO,
      deliveryPlaceInfo,
      productOrder,
      editModal,
      editModalStyle,
      editModalColor,
      editModalColor1,
      editModalPrice,
      editModalDateClient,
      editModalScale,
      editModalPlace,
      newModalPlace,
      isApproved,
      days,
      months,
      years,
      places,
      clients,
      colorsAll,
      cardSelected,
      poValid,
    } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    console.log(this.state.poDetail, 'detatils')
    return (
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        {/**modal destination */}
        <Modal
          isOpen={newModalPlace}
          toggle={this.toggleModalNewPlace}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Create New Delivery Place
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>
                  Create New Delivery Place
                </Label>
                <Row>
                  <Col sm={12}>
                    Place Name
                    <Input
                      type='text'
                      placeholder="Place Name"
                      onChange={e => { this.setState({ newPlacename: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={6}>
                    Client
                    <Select
                      placeholder="Select Client "
                      styles={customStyles}
                      options={clients}
                      onChange={e => { this.setState({ newPlaceClient: e }) }}
                    />
                  </Col>
                  <Col sm={3}>
                    Min
                    <Input
                      type='text'
                      placeholder="Min"
                      onChange={e => { this.setState({ newPlaceMin: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={3}>
                    Max
                    <Input
                      type='text'
                      placeholder="Max"
                      onChange={e => { this.setState({ newPlaceMax: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={6}>
                    Sales Person
                    <Input
                      type='text'
                      placeholder="Sales Person"
                      onChange={e => { this.setState({ newPlacePerson: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={6}>
                    Commission
                    <Input
                      type='text'
                      placeholder="Commission"
                      onChange={e => { this.setState({ newPlaceCommision: e.target.value }) }}
                    />
                  </Col>
                  {/* <Col sm={12}>
                    Termination Client
                    <Input
                      type='text'
                      placeholder="Code for the place"
                      onChange={e => { this.setState({ newPlaceCode: e.target.value }) }}
                    />
                  </Col> */}

                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleModalNewPlace()}>
              Close
            </Button>
            <Button color="primary" onClick={() => {
              this.createPlace()
              this.toggleModalNewPlace()
              this.toggleEditModalPlace()
            }
            }>
              Create Place
            </Button>
          </ModalFooter>
        </Modal>
        {/** CLient style Modal */}
        <Modal
          isOpen={editModalStyle}
          toggle={this.toggleEditModalStyle}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
            // toggle={this.toggleImportModal}
          >
            Style Name
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Insert Style Name:</Label>
                <Row>
                  <Col sm={12}>
                    <Input
                      type="text"
                      maxLength="20"
                      onChange={(e) => {
                        this.setState({
                          styleName: e.target.value,
                        });
                        this.state.poDetail[cardSelected].styleClientpoDetail =
                          e.target.value;
                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => this.toggleEditModalStyle()}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                //  this.poolProcessModal();
                // this.addOldColorProcess();
                this.toggleEditModalStyle();
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        {/**Color client Modal */}
        <Modal
          isOpen={editModalColor1}
          toggle={this.toggleEditModalColor1}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
            // toggle={this.toggleImportModal}
          >
            Color Name
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Insert Color:</Label>
                <Row>
                  <Col sm={12}>
                    <Input
                      type="text"
                      maxLength="20"
                      onChange={(e) => {
                        this.setState({ colorName: e.target.value });
                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => this.toggleEditModalColor()}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                //  this.poolProcessModal();
                // this.addOldColorProcess();
                this.toggleEditModalColor();
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        {/**Color client Modal */}
        <Modal
          isOpen={editModalPrice}
          toggle={this.toggleEditModalPrice}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
            // toggle={this.toggleImportModal}
          >
            Color Client
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Insert Color Client:</Label>
                <Row>
                  <Col sm={12}>
                    <Input
                      type="text"
                      maxLength="20"
                      onChange={(e) => {
                        this.setState({ colorClient: e.target.value });
                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => this.toggleEditModalPrice()}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                //  this.poolProcessModal();
                // this.addOldColorProcess();
                this.toggleEditModalPrice();
                this.state.poDetail[
                  cardSelected
                ].colorClientpoDetail = this.state.colorClient;
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        {/**Select Style Modal */}
        <Modal
          isOpen={editModal}
          className="justify-content-center"
          toggle={this.toggleEditModal}
        >
          <ModalHeader>Select Style PO</ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label> Select a Style for the PO</Label>
                <Row>
                  <Col sm={12}>
                    {this._loadingStyles ? (
                      <Select
                        placeholder="Search Style"
                        styles={customStyles}
                        options={styles}
                        onChange={(option) => {
                          // console.log(option.value);
                          this.setState({
                            newStyle: option,
                            styleHasCHange: true,
                            styleSelected: option,
                            styleNameSel: option.label,
                          });

                          //console.log(this.state.newStyle)
                        }}
                        //value={this.state.selectedStyle}
                      />
                    ) : (
                      <div className={"BOMSloader"}></div>
                    )}
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleEditModal}>
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                this.toggleEditModal();
                this.state.poDetail[
                  cardSelected
                ].stylepoDetail = this.state.newStyle;
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        {/**Date client Modal */}
        <Modal
          isOpen={editModalDateClient}
          toggle={this.toggleEditModalDate}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
            toggle={this.toggleImportModal}
          >
            Edit Exportation Date
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Exportation Date</Label>
                <Row>
                  <Col sm={4}>
                    <Select
                      placeholder="mm"
                      styles={customStyles}
                      options={months}
                      onChange={(e) => {
                        this.setState({ selectedMonth: e });
                      }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="dd"
                      styles={customStyles}
                      options={days}
                      onChange={(e) => {
                        this.setState({ selectedDay: e });
                      }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="yyyy"
                      styles={customStyles}
                      options={years}
                      onChange={(e) => {
                        this.setState({ selectedYear: e });
                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => this.toggleEditModalDate()}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                this.toggleEditModalDate();
                this.getDatePO(
                  cardSelected,
                  this.state.selectedMonth && this.state.selectedMonth.label,
                  this.state.selectedDay && this.state.selectedDay.label,
                  this.state.selectedYear && this.state.selectedYear.label
                );
                this.state.poDetail[cardSelected].exportDateClient =
                  (this.state.selectedMonth && this.state.selectedMonth.label) +
                  "/" +
                  (this.state.selectedDay && this.state.selectedDay.label) +
                  "/" +
                  (this.state.selectedYear && this.state.selectedYear.label);
                //this.state.poDetail[cardSelected].exportDatepoDetail = this.state.datePO
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={editModalScale}
          toggle={this.toggleEditModalScale}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
            toggle={this.toggleImportModal}
          >
            Edit Scale
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Select Scale</Label>
                <Row>
                  <Col sm={12}>
                    <Select
                      placeholder="Select Scale"
                      styles={customStyles}
                      options={scales}
                      onChange={(e) => {
                        this.setState({
                          selectedScale: e,
                          selectedNameScale: e.label,
                        });
                        //this.state.poDetail[cardSelected].scalepoDetail.push(e);
                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => this.toggleEditModalScale()}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                this.toggleEditModalScale();
                this.state.poDetail[cardSelected].scalepoDetail = []
                this.state.poDetail[cardSelected].scalepoDetail.push(this.state.selectedScale);
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={editModalColor}
          toggle={this.toggleEditModalColor}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
            toggle={this.toggleImportModal}
          >
            Select Color
          </ModalHeader>
          <ModalBody>
            {
              <Row>
                <Col sm={12}>
                  <Label>Select Color</Label>
                  <Row>
                    <Col sm={12}>
                      <Select
                        placeholder="Select Color"
                        styles={customStyles}
                        options={
                          this.state.colorInBom
                            ? this.state.colorInBom
                            : this.state.colorInBom
                        }
                        onChange={(e) => {
                          this.setState({ selectedColor: e });
                        }}
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>
            }
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => {
                this.toggleEditModalColor();
                this.setState({
                  newStyle: null,
                  colorsInBom: [],
                });
              }}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                this.toggleEditModalColor();
                // this.setState({
                //   newStyle: null,
                //   colorsInBom: [],
                // });
                this.state.poDetail[
                  cardSelected
                ].colorpoDetail = this.state.selectedColor;
              }}
            >
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Card>
          <CardBody>
            <Col>
              <h2>PO CREATOR</h2>
              <hr />
            </Col>
            <Col>
              <Col>
                <FormGroup row>
                  <Label for="exampleEmail" className="text-primary" sm={2}>
                    No. PO:{""}
                    <Input
                      type="text"
                      maxLength="20"
                      disabled = {poValid}
                      onChange={(e) => {
                        this.setState({ poNumber: e.target.value });
                      }}
                    />
                  </Label>
                  <Label for="exampleEmail" className="text-primary" sm={2}>
                    Look for existing POs
                    <Button
                      onClick={() => {
                        this.getPOValid();
                      }}
                      color="info"
                      size="sm"
                    > 
                      Search PO <i className="fa fa-search" />
                    </Button>
                  </Label>
                </FormGroup>
              </Col>
            </Col>
            {poValid === true ? (
              <>
                <Col>
                  <Col>
                    <FormGroup row>
                      
                      <Label for="exampleEmail" className="text-primary" sm={2}>
                        Client:
                        <Select
                          options={clients}
                          isDisabled={this.state.clientId? true : false}
                          onChange={(e) => {
                            // console.log(e.value);
                            this.getDestinationCLient(e.value)
                            this.getScales(e.value)
                            this.setState({ clientId: e.value });
                            this.setState({
                              loadingCard: !this.state.loadingCard
                            })
                            this.getStylesFromBOM(e.value)
                            this.state.poDetail.push({
                              stylepoDetail: "",
                              styleClientpoDetail: "",
                              colorpoDetail: null,
                              colorClientpoDetail: "",
                              exportDatepoDetail: "",
                              exportDateClient: "",
                              scalepoDetail: "",
                              id: 0,
                              status: 'old'
                            });
                          }}
                        />
                      </Label>
                      {this.state.clientId &&
                        <>
                        <Label for="exampleEmail" className="text-primary" sm={2}>
                        Delivery Place:
                        <Select
                          options={places}
                          onChange={(e) => {
                            // console.log(e.value);
                            this.setState({ deliveryPlacePo: e.value });
                          }}
                        />
                      </Label>
                      <Label for="exampleEmail" className="text-primary" sm={2}>
                        Client Delivery Place:
                        <Input
                          type="text"
                          maxLength="20"
                          onChange={(e) => {
                            this.setState({ deliveryPlace: e.target.value });
                          }}
                        />
                      </Label>
                      <Label for="exampleEmail" className="text-primary" sm={2}>
                      Create Place
                      <br></br>
                     <Button
                     color="primary"
                     size="sm"
                     onClick={() => {
                       this.toggleEditModalPlace()
                       this.toggleModalNewPlace()
                     }}><i class="fas fa-plus"></i></Button>
                  </Label>
                      </>}
                    </FormGroup>
                  </Col>
                </Col>
              </>
            ) : (
              <></>
            )}
          </CardBody>
        </Card>
        {
          //  console.log(this.state.styleSelected, 'details')
        }
        {(this.state.clientId )&& (
          <>
            {this.state.poDetail &&
              this.state.poDetail.map((index, key) => (
                <POCard
                  id={key}
                  poDetail={this.state.poDetail}
                  styleName={this.state.styleNameSel}
                  selectedScale={this.state.selectedNameScale}
                  styleId={this.state.styleSelected}
                  clientId={this.state.clientId}
                  clientStyle={this.state.styleName}
                  clientColor={this.state.colorName}
                  selectedColor={this.state.selectedColor}
                  colorStyleId={this.state.styleSelected}
                  colorClient={this.state.colorClient}
                  selectedMonth={this.state.selectedMonth}
                  selectedDay={this.state.selectedDay}
                  selectedYear={this.state.selectedYear}
                  getStyles={this.getStylesFromBOM}
                  getColors={this.getColorsFromStyle}
                  openModal={this.toggleEditModal}
                  openModalStyle={this.toggleEditModalStyle}
                  openModalColor={this.toggleEditModalColor1}
                  openModalColorClient={this.toggleEditModalPrice}
                  openModalScale={this.toggleEditModalScale}
                  openModalPlace={this.toggleEditModalPlace}
                  openModalDateClient={this.toggleEditModalDate}
                  onRemove={this.removeCard}
                  CopyNewCard={this.CopyNewCard}
                  details={index}

                />
              ))}
          </>
        )}
        {this.state.clientId && (
          <>
            <Button
              color="secondary"
              size={"sm"}
              className="float-right"
              onClick={() => {
                this.addNewCard();
                this.toggleLoading()

              }}
            >
              Add New Card
            </Button>

            <Button
              color="primary"
              disabled={isApproved}
              onClick={() => {
                this.approvePo();
              }}
            >
              Create PO
            </Button>
          </>
        )}
      </div>
    );
  }
}
export default PoCreator;
