import React from "react";
import {
  Table
} from "reactstrap";

export default function StylePoResume(props){
  return (<>
    <Table striped size={'sm'} style={{marginBottom: '0rem'}}>
      <tbody>
        <tr>
          <th style={{width: '20%'}}>&nbsp;</th>
          <th>Po Number</th>
          <th>Season</th>
          <th>Client</th>
          <th>State</th>
        </tr>

        <tr>
          <td style={{textAlign: 'center',width: '20%'}}></td>
          <td style={{textAlign: 'center',width: '20%'}}></td>
          <td style={{textAlign: 'center',width: '20%'}}></td>
          <td style={{textAlign: 'center',width: '20%'}}></td>
        </tr>

        

      </tbody>
    </Table>
  </>);
}