import React from "react";
import axios from "axios";
import Select from "react-select";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  CardFooter,
  Label,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  Nav,
  NavItem,
  NavLink,
  Table,
  TabContent,
  TabPane,
} from "reactstrap";
import { Bar, Pie, Line } from 'react-chartjs-2';

import NotificationAlert from "react-notification-alert";

class FabricReport extends React.Component {
  constructor() {
    super();
    this.state = {
      vTabs: "vt1",
      tab: "1",
      loading: false,
      tab: 1,
      selectedLine: null,
      lineList: [],
      line_inspections: [],
      inspection: null,
      dataAccept:[],
      linesPie: [
        {
          label: 'INNOTEX',
          value: 1
        },
        {
          label: 'TEXFOR',
          value: 2
        },
        {
          label: 'TEXSUN',
          value: 3
        },
        {
          label: 'TODOS',
          value: 4
        }
      ],
      errorsByLine: [],
      errorsLine: [],
      errorsPie: [],

    };
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  user_name = localStorage.getItem("user_name");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;
  _loadingErrors = false;
  _loadingErrorsByLine = false


  addFileToList = (event) => {
    this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  changepage = (data, data2) => {
    console.log(this.state.styleSelected);

    const path = "/admin/account_components";
    this.props.history.push({
      pathname: path,
      state: {
        styleSelected: data2,
        stylesCombo: data,
        styles_po: this.state.styles_po,
        flag: true,
      },
    });
  };

  componentDidMount() {
    this._isMounted = true;
    this.getLines();
    //this.getErrorsLine();
    this.getInspection()
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };


  getLines = (line) => {
    axios
      .get(this.costingURL + "/line/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            lineList: data.map((line) => {
              return {
                value: line.id,
                label: line.name,
              };
            })
          });
          this.state.lineList.push({
            value: 0,
            label: "TODOS"
          })
        }.bind(this)
      );
  };

  getErrorsLine = (line) => {
    axios.get(
      process.env.REACT_APP_IP + `/line_inspection/percentage_by_piece_in_line/?name=${line}`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      const clients = response.data.map(set => set.ended_at);
      const totals = response.data.map(set => (set.percentage).toFixed(2));
      this._loadingErrors = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [
            {
              label: '% de piezas malas',
              data: totals,
              borderColor: 'rgb(255, 99, 132)',
              backgroundColor: 'transparent',
            },
          ],
        }
      };
      this._isMounted && this.setState({
        errorsLine: data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getErrorsByLine');
    }.bind(this));
  }
  percentage_accepted_in_line = (line) => {

    if (line === "TODOS") {
      var url = `${process.env.REACT_APP_IP}` + `/line_inspection/percentage_accepted_in_line/`
    } else {
      var url =  `${process.env.REACT_APP_IP}` + `/line_inspection/percentage_accepted_in_line/?name=${line}`
    }
    axios.get(
      url,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      const clients = response.data.map(set => set.ended_at);
      const totals = response.data.map(set => set.percentage);
      this._loadingErrors = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [{
            label: "Total % Accepted",
            borderColor: "#f96332",
            pointBorderColor: "#FFF",
            pointBackgroundColor: "#f96332",
            pointBorderWidth: 2,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 1,
            pointRadius: 4,
            fill: false,
            backgroundColor: gradientFill,
            borderWidth: 2,
            data: totals
          }]
        }
      };
      this._isMounted && this.setState({
        dataAccept: data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getErrorsByLine');
    }.bind(this));
  }
  getErrorsByLine = (line) => {

    if (line === "TODOS") {
      var url = `${process.env.REACT_APP_IP}` + `/line_inspection/errors_by_lines/`
    } else {
      var url =  `${process.env.REACT_APP_IP}` + `/line_inspection/errors_by_line/?name=${line}`
    }
    axios.get(
      url,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      const clients = response.data.map(set => set.defect_code);
      const totals = response.data.map(set => set.total);
      this._loadingErrorsByLine = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [{
            label: "Total Errors",
            borderColor: "#f96332",
            pointBorderColor: "#FFF",
            pointBackgroundColor: "#f96332",
            pointBorderWidth: 2,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 1,
            pointRadius: 4,
            fill: false,
            backgroundColor: gradientFill,
            borderWidth: 2,
            data: totals
          }]
        }
      };
      this._isMounted && this.setState({
        errorsByLine: data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getErrorsByLine');
    }.bind(this));
  }

  getErrorsPie = (line) => {
    axios.get(
      process.env.REACT_APP_IP + '/line_inspection/errors_by_piece/',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      if (line === "TODOS") {
        var result = response.data
      } else {
        var result = response.data.filter(item => item.name.includes(`${line}`));
      }
      const clients = result.map(set => set.name);
      const totals = result.map(set => (set.percentage * 100));
      this._loadingErrors = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [{
            label: "Total Menos Piezas Defectuosas",
            borderColor: "#f96332",
            pointBorderColor: "#FFF",
            pointBackgroundColor: "#f96332",
            pointBorderWidth: 2,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 1,
            pointRadius: 4,
            fill: false,
            backgroundColor: gradientFill,
            borderWidth: 2,
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)',
            ],
            borderColor: [
              'rgba(255, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
              'rgba(235, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
              'rgba(225, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
              'rgba(210, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
              'rgba(205, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
              'rgba(210, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)',
            ],
            borderWidth: 1,
            data: totals
          }]
        }
      };
      this._isMounted && this.setState({
        errorsPie: data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getErrorsByLine');
    }.bind(this));
  }

  getInspection = () => {
    axios
      .get(process.env.REACT_APP_IP + "/line_inspection/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._ClientsLoaded = true;
          const { data } = response;
          this.setState({
            line_inspections: data

          });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );
  };

  getDataAccept = (line) => {
    var result = this.state.line_inspections.filter(item => item.line.name.includes(`${line}`))
    const clients = result.map(set => set.line.name);
      const totals = result.map(set =>  (100  -   (   (set.errorByPiece * 100)  / set.qty))    );
      this._loadingErrorsByLine = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [{
            label: "Total % Aceptacion",
            borderColor: "#f96332",
            pointBorderColor: "#FFF",
            pointBackgroundColor: "#f96332",
            pointBorderWidth: 2,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 1,
            pointRadius: 4,
            fill: false,
            backgroundColor: gradientFill,
            borderWidth: 2,
            data: totals
          }]
        }
      };
      this._isMounted && this.setState({
        dataAccept: data
      });
  }

  options = {
    maintainAspectRatio: true,
    tooltips: {
      bodySpacing: 4,
      mode: "nearest",
      intersect: 0,
      position: "nearest",
      xPadding: 10,
      yPadding: 10,
      caretPadding: 10
    },
    responsive: 1,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true
        }
      }]
    },
  }

 optionsLineChart = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Chart.js Line Chart',
      },
    },
  };

  optionsBar = {
    maintainAspectRatio: true,
    tooltips: {
      bodySpacing: 4,
      mode: "nearest",
      intersect: 0,
      position: "nearest",
      xPadding: 10,
      yPadding: 10,
      caretPadding: 10
    },
    responsive: 1,
    scales: { 
      yAxes: 
      [ 
        { 
          ticks: 
          { 
            suggestedMin: 0, 
            suggestedMax: 100 ,
            beginAtZero: true
          } 
        } 
      ] 
    }
  }


  render() {
    const { loading, tab, selectedLine, lineList, linesPie, dataAccept } = this.state;
    const pageMode = this.checkPageMode();
    const today = new Date();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Card>
          <CardHeader>
            <Nav tabs>
              <NavItem>
                <NavLink
                  className={tab === "1" ? "active" : ""}
                  onClick={() => {
                    this.toggle("1");
                  }}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }>
                  Errores mas comunes por linea
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={tab === "2" ? "active" : ""}
                  onClick={() => {
                    this.toggle("2");
                  }}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }
                >
                % de Piezas Malas
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={tab === "3" ? "active" : ""}
                  onClick={() => {
                    this.toggle("3");
                  }}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }
                >
                  Errores de pieza por Linea
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={tab === "4" ? "active" : ""}
                  onClick={() => {
                    this.toggle("4");
                  }}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }>
                  % de aceptacion por Linea
                </NavLink>
              </NavItem>
            </Nav>
          </CardHeader>
          <CardBody>
            <TabContent activeTab={tab}>
              <TabPane tabId="1">
                <Row>
                  <Col sm={5}>
                    <Select
                      options={lineList}
                      placeholder={'Linea'}
                      onChange={e => {
                        this.getErrorsByLine(e.label)
                        this.setState({ selectedLine: e.value })
                      }
                      }
                    />
                  </Col>
                  <Col>
                    <a href={`${process.env.REACT_APP_IP}/excel_resume_costing/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        color='primary'
                        className={"text-secondary"}
                      >
                        Download Data
                      </Button>
                    </a>
                  </Col>
                </Row>
                {this._loadingErrorsByLine &&
                  <Bar
                    data={this.state.errorsByLine}
                    options={this.options}
                  />
                }
              </TabPane>
              <TabPane tabId="2">
                <Row>
                  <Col>
                  <Select
                      options={lineList}
                      placeholder={'Linea'}
                      onChange={e => {
                        this.getErrorsLine(e.label)
                      }}
                    />
                    <a href={`${process.env.REACT_APP_IP}/excel_resume_costing/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        color='primary'
                        className={"text-secondary"}
                      >
                        Download Data
                      </Button>
                    </a>
                  </Col>
                </Row>
                {this._loadingErrors &&
                  <Line
                    data={this.state.errorsLine}
                    options={this.optionsLineChart}
                  />
                }
              </TabPane>
              <TabPane tabId="3">
                <Row>
                  <Col>
                    <Select
                      options={linesPie}
                      placeholder={'Linea'}
                      onChange={e => {
                        this.getErrorsPie(e.label)
                      }}
                    />
                    <a href={`${process.env.REACT_APP_IP}/excel_resume_costing/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        color='primary'
                        className={"text-secondary"}
                      >
                        Download Data
                      </Button>
                    </a>
                  </Col>
                </Row>
                {this._loadingErrors &&
                  <Pie
                    data={this.state.errorsPie}
                    options={this.options}
                  />
                }
              </TabPane>
              <TabPane tabId="4">
                <Row>
                  <Col>
                    <Select
                      options={lineList}
                      placeholder={'Linea'}
                      onChange={e => {
                        this.percentage_accepted_in_line(e.label)
                      }}
                    />
                    <a href={`${process.env.REACT_APP_IP}/excel_resume_costing/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        color='primary'
                        className={"text-secondary"}
                      >
                        Download Data
                      </Button>
                    </a>
                  </Col>
                </Row>
                {this._loadingErrors &&
                  <Bar
                    data={dataAccept}
                    options={this.optionsBar}
                  />
                }
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>

      </div>
    );
  }
}
export default FabricReport;

