import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  Button,
  Label,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

class clientBalance extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      productOrder: null,
      materialTypes: [],
      materials: [],
      material: null,
      selectedClient: false,
      client: null,
      clientList: [],
      fileList: [],
      flagBuy: false,
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clientsOptions: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getMaterialClient = (id) => {
    this.toggleLoading();
    axios
      .get(
        this.costingURL +
          `/materialp/balance_of_materials_for_Client/?id=${id}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function(response) {
          this._loadingMaterialType = true;
          const { data } = response;
          this.toggleLoading();
          this.setState({
            materials: data.map((material) => {
              return {
                value: material.id,
                label: material.name,
                consumption: material.consumption,
                consumption_ot: material.consumption_ot,
                description: material.description,
                diference: material.diference,
                inventory: material.inventory,
                material_costing_id: material.material_costing_id,
                orders: material.orders,
                type: material.type,
              };
            }),
          });
        }.bind(this)
      );
  };

  getMaterialType = () => {
    axios
      .get(this.costingURL + "/material_type/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadingMaterialType = true;
          const { data } = response;
          this.setState({
            materialTypes: data.map((material) => {
              return {
                value: material.id,
                label: material.name,
              };
            }),
          });
        }.bind(this)
      );
  };

  resetModal = () => {
    const listForm = ["1"];
    this.setState({
      filesForm: listForm,
    });
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getMaterialType();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const { loading, productOrder } = this.state;
    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Row className="justify-content-center mt-5">
          <Col md={{ size: 12, order: 2 }}>
            <Card>
              <CardBody>
                <CardHeader>
                  <Row>
                    <Col sm="9">
                      <h1>Client Balance</h1>
                    </Col>
                    <Col sm="3"></Col>
                    <CardBody>
                      <Row>
                        <Col>
                          <Label>CLIENT</Label>
                          <Select
                            options={this.state.clientsOptions}
                            //value ={this.state.client}
                            isDisabled={this.state.selectedClient}
                            onChange={(o) => {
                              //this.getMaterialClient(o.value);
                              this.setState({
                                client: o.value,
                                selectedClient: !this.state.selectedClient,
                              });
                            }}
                          />
                        </Col>
                         <Col>
                          <Label>TIPO DE MATERIAL</Label>
                          <Select
                            options={this.state.materialTypes}
                            //value={selectedClient ? selectedClient : null}
                            onChange={(o) => {
                              this.setState({
                                material: o.value,
                              });
                            }}
                          />
                        </Col> 
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            disabled={!this.state.selectedClient}
                            className="float-left"
                            onClick={(o) => {
                              this.setState({
                                selectedClient: !this.state.selectedClient,
                              });
                            }}
                          >
                            Clear
                          </Button>
                        </Col>
                        { <Col>
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <h4>&nbsp; &nbsp;Actions</h4>
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                              onClick={() => {
                                this.getPoResume();
                                console.log(this.state.selectedClientPo);
                              }}
                            >
                              <i className="tim-icons icon-cloud-download-93" />
                              &nbsp;Convert to Excel
                            </Button>
                          </FormGroup>
                        </Col> }
                      </Row>
                    </CardBody>
                    <br></br>
                    {this.state.selectedClient && (
                      <>
                        <Card className={`${pageMode.bg} border`}>
                          <CardBody>
                            <ReactTable
                              responsive
                              defaultPageSize={5}
                              data={this.state.materials}
                              filterable
                              showPaginationBottom={true}
                              className="-striped -highlight primary-pagination"
                              columns={[
                                {
                                  Header: "TIPO",
                                  accessor: "type",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["type"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "CODIGO",
                                  accessor: "material_costing_id",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["material_costing_id"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "DESCRIPCION",
                                  accessor: "description",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["description"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "CONSUMO CPO",
                                  accessor: "consumption",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["consumption"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "CONSUMO OT",
                                  accessor: "consumption_ot",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["consumption_ot"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "EXISTENCIA",
                                  accessor: "inventory",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["inventory"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "PEDIDOS",
                                  accessor: "orders",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["orders"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "DIFERENCIAL",
                                  accessor: "diference",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["diference"],
                                    }),
                                  filterAll: true,
                                },
                              ]}
                            />
                          </CardBody>
                        </Card>
                      </>
                    )}
                  </Row>
                </CardHeader>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}
export default clientBalance;
