import React from "react";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import axios from "axios";

import {
  Form,
  Button,
  TabContent,
  TabPane,
  Col,
  Row,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
  CardHeader,
  Nav,
  NavItem,
  NavLink,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  CardTitle
} from "reactstrap";
import Select from "react-select";
import moment from 'moment'

import { matchSorter } from "match-sorter";

class Permissions extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  userName = localStorage.getItem("user_name") || "";
  userID = localStorage.getItem("user_id") || "";
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      loading: false,
      loadingColors: false,
      newModalPlace: false,
      UserModal: false,
      UserModalNew: false,
      ModalProduct: false,
      typelabel: null,
      edit: false,
      cardSelected: null,
      newType: null,
      newName: null,
      newLastName: null,
      newCode: null,
      newHeadquarter: null,
      newArea: null,
      newEmail: null,
      newCompany: null,
      ip: null,
      team_viewer: null,
      selectedProduct: null,
      selecteduserID: null,
      selectedUser: null,
      showHistory: false,
      tab: "1",
      serial_number: null,
      description: null,
      buy_at: moment()
        .locale('en')
        .format('MM-DD-YYYY'),
      headquarter: null,
      area: null,
      type: null,
      user: null,
      brand: null,
      is_assigned: null,
      listUser: [],
      listArea: [],
      listHeadQ: [],
      listEquip: [],
      listBrands: [],
      listIT: [],
      listModel: [],
      listSO: [],
      listDisk: [],
      listOffice: [],
      listProcessor: [],
      history: [],
      ram: null,
      sophos: null,
      hard_drive_capacity: null,
      equipment_name: null,
      processor: null,
      model: null,
      software: null,
      office: null,
      hard_drive_type: null,
      modalSelected: null,
      typeP: null,
      userID: null,
      timeEnd: null,
      timeStart: null,
      listType: [
        { 
          label: "Entrada", value: "E" },
        { label: "Salida", value: "S" },
        //{ label: "Comida", value: "C" },
      ],
      listPermissionE: [
        { label: "IGSS", value: "I",  },
        { label: "Personal", value: "P" },
        { label: "Otros", value: "O" },
        //{ label: "Comida", value: "C" },
      ],
      listPermissionS: [
        { label: "Compras", value: "C",  },
        { label: "Comision", value: "CO" },
        { label: "Otros", value: "O" },
        //{ label: "Comida", value: "C" },
      ],
    };
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  toggleModalNewPlace = (name) => {
    this.setState({
      newModalPlace: !this.state.newModalPlace,
      modalSelected: name,
    });
  };
  toggleModalNewPlace2 = () => {
    this.setState({
      newModalPlace: !this.state.newModalPlace,
    });
  };
  toggleEditModalPlace = (id) => {
    this.setState({
      editModalPlace: !this.state.editModalPlace,
      cardSelected: id,
    });
  };

  toggleUserModal = () => {
    this.setState({
      UserModal: !this.state.UserModal,
    });
  };
  toggleUserModalNew = () => {
    this.setState({
      UserModalNew: !this.state.UserModalNew,
    });
  };
  toggleProduct = () => {
    this.setState({
      ModalProduct: !this.state.ModalProduct,
    });
  };
  loadingProviders = () => {
    this.toggleLoading();
    setTimeout(() => {
      this.setState({
        edit: true,
      })
      this.toggleLoading();
    }, 1000);

  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };
  toggleLoadingWAIT = () => {
    setTimeout(() => {
      this.toggleLoading();
    }, 1000);
    this.toggleLoading()
  };



  getUser = () => {
    axios
      .get(this.costingURL + "/users_it/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            listUser: data.map((provider) => {
              return {
                value: provider.id,
                label: provider.name + ' ' + provider.last_name,
                name: provider.name,
                last_name: provider.last_name,
                headquarter: provider.headquarter.name,
                headquarterID: provider.headquarter.id,
                area: provider.area.name,
                areaID: provider.area.id,
                code: provider.code,
                headphones: provider.headphones
              };
            }),
          });
        }.bind(this)
      );
  };

  getITinventory = () => {
    axios
      .get(this.costingURL + "/inventory_it/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            listIT: data.map((provider) => {
              return {
                value: provider.id,
                id: provider.id,
                serial_number: provider.serial_number,
                description: provider.description,
                is_assigned: provider.is_assigned === 'True' ? 'Assigned' : 'Unassigned',
                is_assignedID: provider.is_assigned === 'True' ? true : false,
                buy_at: provider.buy_at,
                headquarter: provider.user.headquarter && provider.user.headquarter.name,
                headquarterID: provider.user.headquarter && provider.user.headquarter.id,
                area: provider.user.area && provider.user.area.name,
                areaID: provider.user.area && provider.user.area.id,
                type: provider.type.name,
                typeID: provider.type.id,
                user: provider.user.name + ' ' + provider.user.last_name,
                code: provider.user.code,
                userID: provider.user.id,
                brand: provider.brand.name,
                brandID: provider.brand.id,
                ram: provider.ram,
                sophos: provider.sophos,
                hard_drive_capacity: provider.hard_drive_capacity,
                equipment_name: provider.equipment_name,
                processor: provider.processor.name,
                processorID: provider.processor.id,
                model: provider.model.name,
                modelID: provider.model.id,
                software: provider.software.name,
                softwareID: provider.software.id,
                office: provider.office.name,
                officeID: provider.office.id,
                hard_drive_type: provider.hard_drive_type.name,
                hard_drive_typeID: provider.hard_drive_type.id,
                liveTime: provider.liveTime

              };
            }),
          });
        }.bind(this)
      );
  };


 

  createPermission = (today) => {
    this.toggleLoading();
    let process = new FormData();
    process.append("date", today.getDate() + "/" +(today.getMonth() + 1) + "/" + today.getFullYear());
    process.append("type", this.state.type);
    process.append("permission ", this.state.typeP);
    process.append("user", this.state.userID);
    process.append("timeEnd", this.state.timeEnd);
    process.append("timeStart", this.state.timeStart);
    process.append("comment", this.state.description);
    process.append("boss", this.userID);

    axios
      .post(this.costingURL + "/inventory_it/s", process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this.toggleLoading();
          this.notify("tr", "success", "Permission Created!");
          this.getITinventory();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Created Permission");
        }.bind(this)
      );
  };




  componentDidMount() {
    this._isMounted = true;
    this.getUser();
    this.getITinventory();
 
  }

  render() {
    const { tab, loading, newModalPlace, UserModal, UserModalNew, ModalProduct, modalSelected } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";

    const today = new Date();
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <Modal
          isOpen={newModalPlace}
          toggle={this.toggleModalNewPlace2}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Create New {modalSelected}
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Row>
                  <Col sm={12}>
                    <Input
                      type='text'
                      placeholder={modalSelected}
                      onChange={e => { this.setState({ newType: e.target.value }) }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleModalNewPlace('null')}>
              Close
            </Button>
            <Button color="primary" onClick={() => {
              this.createType(modalSelected)
              this.toggleModalNewPlace(modalSelected)
              this.toggleEditModalPlace()
            }
            }>
              Create {modalSelected}
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={UserModal}
          toggle={this.toggleUserModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Edit New User
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Row>
                  <Col sm={12}>
                    <Label>Headphones</Label>
                    <Input
                      type="checkbox"
                      style={{ marginLeft: 20 }}
                      checked={this.state.selectedUser &&
                        this.state.selectedUser.headphones === "True" ? true : false}
                      onChange={(e) => {
                        this.state.selectedUser && (this.state.selectedUser.headphones = e.target.checked)

                        this.toggleLoadingWAIT()
                      }
                      }
                    />
                    <br></br>
                    <Label>
                      Company
                    </Label>
                    <Select
                      options={this.state.listPaga}
                      placeholder={'Company'}
                      onChange={e => this.state.selectedUser.work_at = e.value}
                    />
                    <Label>
                      Headquarter
                    </Label>
                    <Select
                      options={this.state.listHeadQ}
                      placeholder={this.state.selectedUser && this.state.selectedUser.headquarter}
                      onChange={(o) => {
                        this.state.selectedUser.headquarterID = o.value
                      }}
                    />
                    <Label>
                      Area
                    </Label>
                    <Select
                      options={this.state.listArea}
                      placeholder={this.state.selectedUser && this.state.selectedUser.area}
                      onChange={(o) => {
                        this.state.selectedUser.areaID = o.value

                      }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleUserModal()}>
              Close
            </Button>
            <Button color="primary" onClick={() => {
              this.editUser()
              this.toggleUserModal()
              //this.toggleEditModalPlace()
            }
            }>
              Edit User
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={UserModalNew}
          toggle={this.toggleUserModalNew}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Create New User
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Row>
                  <Col sm={12}>
                    <Label>
                      Name
                    </Label>
                    <Input
                      type='text'
                      placeholder="Name"
                      onChange={e => { this.setState({ newName: e.target.value }) }}
                    />
                    <Label>
                      Last  Name
                    </Label>
                    <Input
                      type='text'
                      placeholder="Last Name"
                      onChange={e => { this.setState({ newLastName: e.target.value }) }}
                    />
                    <Label>
                      Email
                    </Label>
                    <Input
                      type='text'
                      placeholder="test@test.com"
                      onChange={e => { this.setState({ newEmail: e.target.value }) }}
                    />
                    <Label>
                      Code
                    </Label>
                    <Input
                      type='text'
                      placeholder="0000"
                      onChange={e => { this.setState({ newCode: e.target.value }) }}
                    />
                    <Label>Headphones</Label>
                    <Input
                      type="checkbox"
                      style={{ marginLeft: 20 }}
                      checked={this.state.selectedProduct &&
                        this.state.selectedProduct.headphones}
                      onChange={(e) => {
                        this.setState({
                          newHeadphones: e.target.checked
                        })

                        this.toggleLoadingWAIT()
                      }
                      }
                    />
                    <Label>
                      Company
                    </Label>
                    <Select
                      options={this.state.listPaga}
                      placeholder={'Company'}
                      onChange={e => { this.setState({ newCompany: e.value }) }}
                    />
                    <Label>
                      Headquarter
                    </Label>
                    <Select
                      options={this.state.listHeadQ}
                      placeholder={'Headquarter'}
                      onChange={e => { this.setState({ newHeadquarter: e.value }) }}
                    />
                    <Label>
                      Area
                    </Label>
                    <Select
                      options={this.state.listArea}
                      placeholder={'Area'}
                      onChange={e => { this.setState({ newArea: e.value }) }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleUserModalNew()}>
              Close
            </Button>
            <Button color="primary" onClick={() => {
              this.createUser()
              this.toggleUserModalNew()
              //this.toggleEditModalPlace()
            }
            }>
              Create User
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={ModalProduct}
          toggle={this.toggleProduct}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Information
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col>
                <Label>
                  Serial Number: {this.state.selectedProduct && this.state.selectedProduct.serial_number}
                </Label>
                <br></br>
                <Label>
                  Type: {this.state.selectedProduct && this.state.selectedProduct.type}
                </Label>
                <br></br>
                <Label>
                  User: {this.state.selectedProduct && this.state.selectedProduct.user}
                </Label>
                <br></br>
                {((this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP' || (this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP') &&
                  <>
                    <Label>
                      Users Name: {this.state.selectedProduct && this.state.selectedProduct.equipment_name}
                    </Label>
                    <br></br>
                  </>
                }
                <Label>
                  Assigned: {this.state.selectedProduct && this.state.selectedProduct.is_assigned}
                </Label>
                <br></br>
                {((this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP' || (this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP') &&
                  <>
                    <Label>
                      Sophos: {this.state.selectedProduct && this.state.selectedProduct.sophos}
                    </Label>
                    <br></br>
                  </>
                }
                {((this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP' || (this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP') &&
                  <>
                    <Label>
                      Ram: {this.state.selectedProduct && this.state.selectedProduct.ram}
                    </Label>
                    <br></br>

                    <Label>
                      Processor: {this.state.selectedProduct && this.state.selectedProduct.processor}
                    </Label>
                    <br></br>
                  </>
                }
              </Col>
              <Col sm={6}>
                <Label>
                  Headquarter: {this.state.selectedProduct && this.state.selectedProduct.headquarter}
                </Label>
                <br></br>
                <Label>
                  Area: {this.state.selectedProduct && this.state.selectedProduct.area}
                </Label>
                <br></br>
                <Label>
                  Brand: {this.state.selectedProduct && this.state.selectedProduct.brand}
                </Label>
                <br></br>
                <Label>
                  Buy at: {this.state.selectedProduct && this.state.selectedProduct.buy_at}
                </Label>
                <br></br>
                <Label>
                  Live Time: {this.state.selectedProduct && this.state.selectedProduct.liveTime.toFixed(2) + " years"}
                </Label>
                <br></br>
                {((this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP' || (this.state.selectedProduct && this.state.selectedProduct.type) === 'LAPTOP') &&
                  <>
                    <Label>
                      Software: {this.state.selectedProduct && this.state.selectedProduct.software}
                    </Label>
                    <br></br>
                    <Label>
                      Office: {this.state.selectedProduct && this.state.selectedProduct.office}
                    </Label>
                    <br></br>
                    <Label>
                      Hard Drive Type: {this.state.selectedProduct && this.state.selectedProduct.hard_drive_type}
                    </Label>
                    <br></br>
                    <Label>
                      Hard Drive Capacity: {this.state.selectedProduct && this.state.selectedProduct.hard_drive_capacity}
                    </Label>
                    <br></br>
                  </>
                }
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>
                  Description: {this.state.selectedProduct && this.state.selectedProduct.description}
                </Label>
                <br></br>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleProduct()}>
              Close
            </Button>
            {/*   <Button color="primary" onClick={() => {
              
              this.toggleProduct()
            }
            }>
              Edit User
            </Button> */}
          </ModalFooter>
        </Modal>
        <Card>
          <CardHeader>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Nav tabs>
                <NavItem>
                  <NavLink
                    className={tab === "1" ? "active" : ""}
                    onClick={() => {
                      this.toggle("1");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                          color: "black",
                          borderColor: "black",
                        }
                        : {}
                    }
                  >
                    Permissions
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={tab === "2" ? "active" : ""}
                    onClick={() => {
                      this.toggle("2");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                          color: "black",
                          borderColor: "black",
                        }
                        : {}
                    }
                  >
                    {"Create Permission"}
                  </NavLink>
                </NavItem>
              </Nav>
              <br></br>
              <TabContent activeTab={tab}>
                <TabPane tabId="2">
                  <h3>Information</h3>
               {this.userRole === 'IT Admin' &&
                    <Row>
                      <Col md={9}>
                        <h2>
                          Un gran poder conlleva una gran responsabilidad!!!
                        </h2>
                      </Col>
                      <Col>
                        <Input
                          type="checkbox"
                          style={{ marginLeft: 20, width: '40px', height: '40px' }}
                          onChange={(e) => {
                            this.setState({
                              ITAdmmin: e.target.checked
                            })
                          }
                          }
                        />
                      </Col>
                    </Row>
                  }
                  <Row>
                    <Col xs={3}>
                      {
                        (
                          <Select
                            options={this.state.listType}
                            //placeholder={this.state.selectedProduct.type}
                            onChange={(o) => {
                              //this.state.selectedProduct.typeID = o.value
                              this.setState({
                                type: o.value,
                                typelabel: o.label
                              });
                            }}
                          />
                        )
                      }
                    </Col>
                    <Col>
                      <CardTitle tag="h2">
                        Fecha:{" "}
                        {today.getDate() +
                          "/" +
                          (today.getMonth() + 1) +
                          "/" +
                          today.getFullYear()}
                      </CardTitle>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs={6}>
                      <FormGroup>
                        <>
                          <Label>Name</Label>

                          <Select
                            options={this.state.listUser}
                            //placeholder={this.state.selectedProduct.type}
                            onChange={(o) => {
                              //this.state.selectedProduct.typeID = o.value
                              console.log(o, 'value')
                              this.setState({
                                userID: o.value,
                              });
                            }}
                          />

                        </>
                      </FormGroup>
                      <FormGroup>
                        <Label>Permission</Label>
                        {this.state.type &&
                          <Select
                          options={this.state.type === 'E'? this.state.listPermissionE : this.state.listPermissionS}
                          //placeholder={this.state.selectedProduct.type}
                          onChange={(o) => {
                            //this.state.selectedProduct.typeID = o.value
                            console.log(o, 'value')
                            this.setState({
                              typeP: o.value,
                            });
                          }}
                        />}
                      </FormGroup>
                    </Col>
                    <Col xs={6}>
                      <FormGroup>
                        <Row>
                          <Col>
                            <FormGroup>
                              <Label>Hora Inicio</Label>
                              <Input
                                type="time"
                                onChange={(e) => {
                                  this.setState({
                                    timeStart: e.target.value,
                                  });
                                }}
                              />
                            </FormGroup>
                          </Col>
                          <Col>
                            <FormGroup>
                              <Label>Hora Fin</Label>
                              <Input
                                type="time"
                                onChange={(e) => {
                                  this.setState({
                                    timeEnd: e.target.value,
                                  });
                                }}
                              />
                            </FormGroup>
                          </Col>
                        </Row>
                       {this.state.typeP === 'O' && <Row>
                          <Col>
                            <FormGroup>
                              <Label>Comentario</Label>
                              <Input
                                type="text"
                                defaultValue={this.state.description}
                                onChange={(e) => {
                                  this.setState({
                                    description: e.target.value,
                                  });
                                }}
                              />
                            </FormGroup>
                          </Col>
                        </Row>}
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Label>
                        Concedido por: {this.userName}
                      </Label>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <Button
                        size="sm"
                        color="info"
                        className="float-left"
                        onClick={() => {
                          this.createPermission(today)
                        }}
                      >
                        Create <i className="tim-icons icon-pencil" />
                      </Button>
                    </Col>
                  </Row>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="1">
                  <Col>
                    {this.getITinventory && (
                      <ReactTable
                        data={this.state.listIT.map((provider) => {
                          return {
                            ...provider,
                            id: provider.value,
                            actions: (
                              <div className="clearfix">
                                <Button
                                  size="sm"
                                  color="info"
                                  className="float-right"
                                  onClick={() => {
                                    this.toggleProduct()
                                    this.setState({
                                      selectedProduct: provider,
                                    });
                                  }}
                                >
                                  <i className="fa fa-search" />
                                </Button>
                                <Button
                                  size="sm"
                                  color="info"
                                  className="float-right"
                                  onClick={() => {
                                    this.loadingProviders()
                                    this.setState({
                                      selectedProduct: provider,
                                      typelabel: provider.type
                                    });
                                    this.toggle("2");
                                  }}
                                >
                                  <i className="tim-icons icon-pencil" />
                                </Button>
                                {<Button
                                  size="sm"
                                  color="danger"
                                  className="float-right"
                                  onClick={() => {
                                    this.deleteProduct(provider.value);
                                    //console.log(provider.value)
                                  }}
                                >
                                  <i className="tim-icons icon-simple-remove" />
                                </Button>}
                              </div>
                            ),
                          };
                        })}
                        filterable
                        defaultPageSize={5}
                        columns={[
                          {
                            Header: "Serial Number",
                            accessor: "serial_number",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["serial_number"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 180,
                          },
                          {
                            Header: "Type",
                            accessor: "type",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["type"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 130,
                          },
                          {
                            Header: "Code",
                            accessor: "code",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["code"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 130,
                          },
                          {
                            Header: "User",
                            accessor: "user",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["user"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 130,
                          },
                          {
                            Header: "Brand",
                            accessor: "brand",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["brand"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 80,
                          },
                          {
                            Header: "Actions",
                            accessor: "actions",
                            sortable: true,
                            filterable: true,
                            width: 140,
                          },
                        ]}
                      />
                    )}
                  </Col>
                </TabPane>
              </TabContent>
            </CardBody>
          </CardHeader>
        </Card>
      </div>
    );
  }
}
export default Permissions;
