  _clientsLoaded = false;
  getClients = () => {
    this.toggleLoading();
    axios
      .get(this.costingURL + "/audit/get_cut_for_audit/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              cuts: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.code,
                };
              }),
            });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

