  componentDidMount() {
    this.userVerification();
    if (this.user_name !== "") {
      const name = this.user_name.split(" ");
      const uname = name[0];
      const first = name[0].split("")[0];
      const second = name[1].split("")[0];
      this.setState({
        initials: first + second,
        name: uname,
      });
    } else if (
      this.user_name === "" ||
      this.user_name === null ||
      this.user_name === undefined ||
      this.user_name === " "
    ) {
      const name = [];
      const uname = name;
      const first = name;
      const second = name;
      this.setState({
        initials: first + second,
        name: uname,
      });
    }
  }

