import React from "react";
import axios from "axios";
import Select from "react-select";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  CardFooter,
  Label,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  Nav,
  NavItem,
  NavLink,
  Table,
  TabContent,
  TabPane,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

class FabricReport extends React.Component {
  constructor() {
    super();
    this.state = {
      vTabs: "vt1",
      loading: false,
      tab: "0",
      deleteError: false,
      lineSelected: false,
      clear: false,
      line_inspections: [],
      line: null,
      dataError: [],
      errors: [],
      columns: [],
      metas: [],
      prod: null,
      meta: null,
      cutFilter: [],
      NewcutFilter: [],
      uniqueArray: [],
      filterCutColor: [],
      cut: [],
      color: null
    };
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  user_name = localStorage.getItem("user_name");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  addFileToList = (event) => {
    this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  changepage = (data, data2) => {
    console.log(this.state.styleSelected);

    const path = "/admin/account_components";
    this.props.history.push({
      pathname: path,
      state: {
        styleSelected: data2,
        stylesCombo: data,
        styles_po: this.state.styles_po,
        flag: true,
      },
    });
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getMetas();
    //this.getMetasLineaLight("INNOTEX A");
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  getDMReport = () => {
    axios
      .get(process.env.REACT_APP_IP + "/fabrics/get_dms_for_report/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._ClientsLoaded = true;
          this._isMounted &&
            this.setState({
              dm: response.data.map((index) => {
                return {
                  value: index.id,
                  label: index.dm,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );
  };

  getClients = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + "/line_inspection/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this.toggleLoading();
          this._ClientsLoaded = true;
          const { data } = response;
          this.setState({
            line_inspections: data,
          });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );
  };

  lineInsID = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + "/line_inspection/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this.toggleLoading();
          this._ClientsLoaded = true;
          const { data } = response;
          this.setState({
            line_inspections: data,
          });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );
  };

  CreateInspection = (id, prod, line, cut) => {
    const objWithIdIndex = this.state.metas.findIndex(
      (obj) => obj.SECCION === line
    );
    this.toggleLoading();
    let process = new FormData();
    //const list = errors.filter(Boolean).join()
    //process.append("errors", list);
    var json = JSON.stringify({
      line: id,
      meta: this.state.metas.meta,
      cut: cut,
      errors: this.state.dataError,
    });
    process.append("data", json);
    axios
      .post(
        this.costingURL + "/line_inspection/update_lineInspection/",
        process,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function (response) {
          this.setState({
            clear: false,
            dataError: []
          });
          this.toggleLoading();
          this.notify("tr", "success", "Error Created!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "Error Created Error");
        }.bind(this)
      );
  };

  CloseCut = (id) => {
    this.toggleLoading();
    let process = new FormData();
    process.append("cut", id);
    process.append("flag", "C");
    axios
      .post(this.costingURL + "/line_inspection/change_status/", process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this.setState({
            lineSelected: false,
          });
          this.toggleLoading();
          this.notify("tr", "success", "Cut Closed!");
          this.getClients();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "Error Closed Cut");
        }.bind(this)
      );
  };

  CloseInspe = (id) => {
    this.toggleLoading();
    let process = new FormData();
    process.append("id", id);
    process.append("flag", "C");
    axios
      .post(this.costingURL + "/line_inspection/change_status/", process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this.setState({
            lineSelected: false,
          });
          this.toggleLoading();
          this.notify("tr", "success", "Cut Closed!");
          this.getClients();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "Error Closed Cut");
        }.bind(this)
      );
  };

  changeErrors = (arr, id, err) => {
    const objWithIdIndex = arr.findIndex(
      (obj) => obj.operation === id && obj.error === err
    );

    if (objWithIdIndex > -1) {
      arr.splice(objWithIdIndex, 1);
    }
    return arr;
  };

  getMetasLinea = (line) => {
    axios
      .get(
        `http://testing.zuntexapparel.com/api/v1/line_goal/get_produtcion_by_line/?line=${line}`,
        {
          headers: {},
        }
      )
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            prod: data,
          });
        }.bind(this)
      );
  };

  getMetasLineaSave = (line, id, lineIID, cut) => {
    axios
      .get(
        this.costingURL + `/line_goal/get_produtcion_by_line/?line=${line}`,
        {
          headers: {},
        }
      )
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            prod: data,
          });
          this.CreateInspection(lineIID, data, line, cut);
        }.bind(this)
      );
  };

  getMetas = () => {
    axios
      .get("http://testing.zuntexapparel.com/api/v1/line_goal/get_all_lines/", {
        headers: {},
      })
      .then(
        function (response) {
          const { data } = response;
          this.setState({
            metas: data,
          });
        }.bind(this)
      );
  };
  setCutShow = (cut) => {
    console.log(cut, "cut", this.state.cutFilter)
    this.state.cut = this.state.NewcutFilter.filter(function (nickname) {
      return parseInt(nickname.id) === parseInt(cut);
    });
    this.setState({
      Color: this.state.cut[0] && this.state.cut[0].color
    })

    console.log(this.state.cut, 'cut')
  };


  setColor = (styleName, cut) => {
    this.setState({
      //filterCutColor: []
    })
    this.state.filterCutColor = this.state.NewcutFilter.filter(function (nickname) {
      return nickname.cut.style === styleName;
    });
    this.setState({
      cut: this.state.filterCutColor
    })
   console.log(this.state.filterCutColor, 'colors')
    
  };


  setCuts = (id) => {
    axios
    .get(this.costingURL + `/line_inspection/${id}/`, {
      headers: {
        Authorization: "Token " + this.userToken,
      },
    })
    .then(
      function (response) {
        this._clientsLoaded = true;
        this._isMounted &&
          this.setState({
            NewcutFilter: response.data.cuts,
            uniqueArray : response.data.cuts.filter((v,i,a)=>a.findIndex(t=>(t.cut.style===v.cut.style))===i)
          });
        //this.toggleLoading();
        //this.state.uniqueArray = response.data.cuts.filter((v,i,a)=>a.findIndex(t=>(t.cut.style===v.cut.style))===i)
        console.log(this.state.uniqueArray.length,'assa')
      }.bind(this)
    )
    .catch(
      function (error) {
        //this.toggleLoading();
        this.handleError(error, "Audits");
      }.bind(this)
    );



 /*    this.setState({
      //NewcutFilter: []
    })
    line.cuts && line.cuts.map((index, key) => (
      this.state.cutFilter.push({ "style": index.cut.style, "color": index.cut.color, "cut": index.id, "cutID": index.cut.code, "errors": index.errors, "operations": index.operations })
    ))
    this.state.NewcutFilter = this.state.cutFilter.filter((value, index, self) =>
      index === self.findIndex((t) => (
        t.style === value.style
      ))
    ) */
  };


  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  getMetasLineaLight = (line) => {
    axios
      .get(
        `http://testing.zuntexapparel.com/api/v1/line_goal/get_produtcion_by_line/?line=${line}`,
        {
          headers: {},
        }
      )
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            prod: data.unidades,
          });

          //console.log(data)
        }.bind(this)
      );
  };

  render() {
    const {
      loading,
      errors,
      lineSelected,
      line_inspections,
      line,
      deleteError,
      dataError,
      tab,
      prod,
    } = this.state;
    const pageMode = this.checkPageMode();
    const today = new Date();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        {lineSelected === false ? (
          <Row>
            <Col className="ml-auto mr-auto" md="10">
              <Card className="card-plain card-subcategories">
                <CardBody>
                  <CardTitle className="text-center mt-3" tag="h4">
                    LINEAS
                  </CardTitle>
                  <Row className="nav-pills-info nav-pills-icons justify-content-center">
                    {this.getClients &&
                      line_inspections.map((index, key) => (
                        <Col xs={6} style={{ display: "flex" }}>
                          <Button
                            style={{
                              background:
                                ((index.qty) / index.meta) < 33
                                  ? "red"
                                  : (index.qty ) / index.meta >
                                    66.0
                                    ? "green"
                                    : (index.qty ) / index.meta >
                                      33.0 &&
                                      (index.qty ) / index.meta <
                                      66.0
                                      ? "gold"
                                      : "",
                              width: "90%",
                            }}
                            //disabled={true}
                            onClick={() => {
                              this.setState({
                                lineSelected: !lineSelected,
                                line: index,
                                meta: index.meta,
                              });
                              this.setCuts(index.id)
                              //this.getMetasLinea(index.line.name);
                              //this.toggleLoading()
                            }}
                          >
                            {index.line.name}
                          </Button>

                          {/*   <div style={{
                            height: '35px',
                            width: '35px',
                            backgroundColor: 'green',
                            borderRadius: '50%',
                            display: 'inline - block',
                            marginTop: '5px'
                          }}></div> */}
                        </Col>
                      ))}
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        ) : (
          <Row>
            <Col className="ml-auto mr-auto" md="12">
              <Card className="card-plain card-subcategories">
                <CardHeader>
                  <Row>
                    <Col md="3">
                      <Label>
                        Style
                      </Label>
                      {this.state.uniqueArray.length > 0 &&
                      
                      <Input
                        type="select"
                        //value={values.sizes}
                        //ref={this.createService}
                        onChange={e => {
                          this.setState({
                            selectCutStyle: e.target.value,
                            //filterCutColor: []
                          })
                          this.setColor(e.target.value)
                          //console.log(e.target.value)
                        }
                        }
                        style={{ height: '40px' }}
                      >
                        { this.state.uniqueArray.map((size, idx) =>
                          <option key={idx} value={size.cut.style} >
                            {size.cut.style}
                          </option>
                        )}
                      </Input>
                      }
                    </Col>

                    <Col md="3">
                      <Label>
                        Color
                      </Label>
                      <Input
                        type="select"
                        //value={values.sizes}
                        //ref={this.createService}
                        placehorder={"Select a Style"}
                        onChange={e => {
                        
                          this.setCutShow(e.target.value)
                        }

                        }
                        style={{ height: '40px' }}
                      >
                        {this.state.filterCutColor.map((size, idx) =>
                          <option key={idx} value={size.id}>
                            {size.cut.color} ({size.cut.code})
                          </option>
                        )}
                      </Input>
                    </Col>
                  </Row>

                  <Row>
                    <Col>
                      <CardTitle tag="h2">Linea: {line.line.name}</CardTitle>
                    </Col>
                    <Col>
                      <CardTitle tag="h2">Persona: {this.user_name}</CardTitle>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <CardTitle tag="h2">
                        Estilo: {this.state.cut[0] && this.state.cut[0].cut.style}
                      </CardTitle>
                    </Col>
                    <Col>
                      <CardTitle tag="h2">
                        Fecha:{" "}
                        {today.getDate() +
                          "/" +
                          (today.getMonth() + 1) +
                          "/" +
                          today.getFullYear()}
                      </CardTitle>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <CardTitle tag="h2">
                        Color: {this.state.cut[0] && this.state.cut[0].cut.color}
                      </CardTitle>
                    </Col>
                    <Col>
                      <CardTitle tag="h2">
                        Corte: {this.state.cut[0] && this.state.cut[0].cut.code}
                      </CardTitle>
                    </Col>
                  </Row>
                </CardHeader>
                <CardBody>
                  <CardTitle className="text-center mt-3" tag="h4">
                    ERRORS
                  </CardTitle>
                  <Row className="nav-pills-info nav-pills-icons justify-content-center">
                    
                        {!loading &&
                            <Table className="" style={{ width: "80vw", display: loading }}>
                              <thead className="text-primary">
                                <tr>
                                  <th style={{ width: "50px" }}>Operacion</th>
                                  {line &&
                                    this.state.cut[0] && this.state.cut[0].errors.map((index, key) => (
                                      <th
                                        style={{
                                          width: "50px",
                                          writingMode: "vertical-lr",
                                          transform: "rotate(180deg)",
                                        }}
                                      >
                                        {index.error.name}
                                      </th>
                                    ))}
                                </tr>
                              </thead>
                              <tbody>
                                {
                                  this.state.cut[0] && this.state.cut[0].operations.map(
                                    (index, key2) => (
                                      <tr>
                                        <td>{index.operation.name}</td>
                                        {
                                          this.state.cut[0] && this.state.cut[0].errors.map(
                                            (err, key) => (
                                              <td style={{ width: "50px" }}>
                                                <Input
                                                  type="checkbox"
                                                  style={{
                                                    marginBottom: "5px",
                                                    marginLeft: 20,
                                                    width: "30px",
                                                    height: "35px",
                                                    marginTop: "-10PX",
                                                  }}
                                                  //checked={this.state.clear}
                                                  onChange={(e) => {
                                                    if (e.target.checked) {
                                                      dataError.push({
                                                        operation: index.id,
                                                        error: err.id,
                                                      });
                                                    } else {
                                                      this.changeErrors(
                                                        dataError,
                                                        index.id,
                                                        err.id
                                                      );
                                                    }
                                                    //this.state.selectedProduct && (this.state.selectedProduct.headphones = e.target.checked)
                                                  }}
                                                />
                                              </td>
                                            )
                                          )}
                                      </tr>
                                    )
                                  )}
                              </tbody>
                            </Table>

                        }

                        
                        
                     
                  </Row>
                </CardBody>
                <CardFooter>
                  <Row>
                    <Col className="text-center mt-3">
                      <Button
                        color="info"
                        onClick={() => {
                          this.CreateInspection(
                            line.id,
                            this.state.meta,
                            line.line.name,
                            //line.id,
                            this.state.cut[0].cut.id
                          );
                          //console.log(dataError);
                        }}
                      >
                        Guardar
                      </Button>
                    </Col>
                  </Row>
                  <Button
                    color="info"
                    onClick={() =>
                      this.setState({
                        lineSelected: !lineSelected,
                      })
                    }
                  >
                    Regresar
                  </Button>
                  <Button
                    color="warning"
                    onClick={() => this.CloseCut(this.state.cut[0].cut.id)}
                  >
                    Cerrar Estilo
                  </Button>
                  <Button
                    color="warning"
                    onClick={() => this.CloseInspe(line.id)}
                  >
                    Cerrar Inspeccion
                  </Button>
                </CardFooter>
              </Card>
            </Col>
          </Row>
        )}
      </div>
    );
  }
}
export default FabricReport;
