import React from "react";
import axios from "axios";
import Select from "react-select";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  CardFooter,
  Label,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Nav,
  NavItem,
  NavLink,
  Table,
  TabContent,
  TabPane,
} from "reactstrap";
import { Bar, Pie, Line } from 'react-chartjs-2';

import NotificationAlert from "react-notification-alert";

class FabricReport extends React.Component {
  constructor() {
    super();
    this.state = {
      vTabs: "vt1",
      tab: "1",
      loading: false,
      selectedLine: null,
      selectedWeek: null,
      selectedClient: null,
      lineList: [],
      line_inspections: [],
      inspection: null,
      dataAccept: [],
      week: null,
      weeks: [],
      weeksF: [],
      flag: null,
      selectedWeekF: null,
      selectedFamily: null,
      selectedPlace: null,
      second: [
        {
          label: 'GLOBALES',
          value: false
        },
        {
          label: 'SEGUNDAS',
          value: true
        },
      ],
      place: [
        {
          label: 'INNOTEX',
          value: 'INNOTEX'
        },
        {
          label: 'TEXFOR',
          value: 'TEXFOR'
        },
        {
          label: 'TEXSUN',
          value: 'TEXSUN'
        },
      ],
      family: [
        {
          label: 'COSTURA',
          value: 'COSTURA'
        },
        {
          label: 'EMBELLECIMIENTO',
          value: 'EMBELLECIMIENTO'
        },
        {
          label: 'EMPAQUE',
          value: 'EMPAQUE'
        },
        {
          label: 'MATERIALES',
          value: 'MATERIALES'
        },
        {
          label: 'TELA',
          value: 'TELA'
        },
        {
          label: 'TODOS',
          value: 'TODOS'
        },
      ],
      errorsByLine: [],
      errorsLine: [],
      errorsPie: [],
      report: [],
      clients: [],
      editModalStyle: false,
    };
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  user_name = localStorage.getItem("user_name");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;
  _loadingErrors = false;
  _loadingErrorsByLine = false


  addFileToList = (event) => {
    this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };



  componentDidMount() {
    this._isMounted = true;
    this.getLines();
    this.getClients();
    this.get_numberWeek(new Date());
    //this.getErrorsLine();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };


  //trae las lineas
  getLines = (line) => {
    axios
      .get(this.costingURL + "/line/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            lineList: data.map((line) => {
              return {
                value: line.id,
                label: line.name,
              };
            })
          });
          this.state.lineList.push(
            {
              value: 0,
              label: "TEXFOR"
            },
            {
              value: 0,
              label: "INNOTEX"
            },
            {
              value: 0,
              label: "TEXSUN"
            },
            {
              value: 0,
              label: "TODOS"
            },
          )
        }.bind(this)
      );
  };

  downloadData = (week) => {
    let wee = ''
    week !== 'TODOS'? wee =`?week=${week}` : wee ='';
    this.toggleLoading()
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/get_audit_data/${wee}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
         // this.componentDidMount();
          this.toggleLoading()
          window.open(
            process.env.REACT_APP_IP +
            `/admin/get_audit_data/${wee}`
          );
          this.notify("tr", "success", "File downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.toggleLoading()
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  getErrorsLine = (line) => {
    axios.get(
      process.env.REACT_APP_IP + `/line_inspection/percentage_by_piece_in_line/?name=${line}`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      const clients = response.data.map(set => set.ended_at);
      const totals = response.data.map(set => (set.percentage * 100).toFixed(2));
      this._loadingErrors = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [
            {
              label: '% de piezas malas',
              data: totals,
              borderColor: 'rgb(255, 99, 132)',
              backgroundColor: 'transparent',
            },
          ],
        }
      };
      this._isMounted && this.setState({
        errorsLine: data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getErrorsByLine');
    }.bind(this));
  }

  //cambiar por el verdadero reporte
  getErrorsByLine = (line, week, flag) => {

    var weekJson = JSON.stringify(week)
    let scaleData = new FormData();
    scaleData.append("line", line);
    scaleData.append("week", weekJson);
    flag && scaleData.append("flag", flag);
    axios
      .post(this.costingURL + "/audit/report/", scaleData, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      }).then(function (response) {
        const clients = response.data.map(set => set.week);
        const totals = response.data.map(set => ((set.a / set.total_line) * 100).toFixed(2));
        this._loadingErrorsByLine = true
        const data = (canvas) => {
          var ctx = canvas.getContext("2d");

          var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
          gradientStroke.addColorStop(0, '#80b6f4');
          gradientStroke.addColorStop(1, '#FFFFFF');

          var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
          gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
          gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
          return {
            labels: clients,
            datasets: [{
              label: "% Aceptacin Por Lnea",
              borderColor: "#f96332",
              pointBorderColor: "#FFF",
              pointBackgroundColor: "#f96332",
              pointBorderWidth: 2,
              pointHoverRadius: 4,
              pointHoverBorderWidth: 1,
              pointRadius: 4,
              fill: false,
              backgroundColor: gradientFill,
              borderWidth: 2,
              data: totals
            }]
          }
        };
        this._isMounted && this.setState({
          errorsByLine: data,
          report: response.data
        });
      }.bind(this)).catch(function (error) {
        this.handleError(error, 'getErrorsByLine');
      }.bind(this));
  }

  getReportFamily = (place, week, family) => {

    //var weekJson = JSON.stringify(week)
    let scaleData = new FormData();
    scaleData.append("line", place);
    scaleData.append("week", week);
    family && scaleData.append("family", family);
    axios
      .post(this.costingURL + "/audit/report_by_family/", scaleData, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      }).then(function (response) {
        const clients = response.data.map(set => set.type);
        const totals = response.data.map(set => ((set.total_type) * 100).toFixed(2));
        this._loadingErrorsByLine = true
        const data = (canvas) => {
          var ctx = canvas.getContext("2d");

          var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
          gradientStroke.addColorStop(0, '#80b6f4');
          gradientStroke.addColorStop(1, '#FFFFFF');

          var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
          gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
          gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
          return {
            labels: clients,

            options: { 
              legend: {
                labels: {
                    // This more specific font property overrides the global property
                    defaultFontColor: 'red'
                }
            }
          }
            ,
            datasets: [{
              label: "% Defectos Por Familia",
              borderColor: "#f96332",
              pointBorderColor: "#FFF",
              pointBackgroundColor: "#f96332",
              pointBorderWidth: 2,
              //pointHoverRadius: 4,
              //pointHoverBorderWidth: 1,
              pointRadius: 4,
              fill: false,
              backgroundColor: gradientFill,
              borderWidth: 2,
              data: totals
            }]
          }
        };
        this._isMounted && this.setState({
          errorsByLine: data,
          report: response.data
        });
      }.bind(this)).catch(function (error) {
        this.handleError(error, 'getErrorsByLine');
      }.bind(this));
  }

  //cambiar por el verdadero reporte de clientes
  getReportByClient = (client, week) => {

    client ? client = `?client=${client}` : client = ''
    week ? client != '' ? week = `&week=${week}` : week = `?week=${week}` : week = ''
    var url = `${process.env.REACT_APP_IP}` + `/audit/report/${client}${week}`
    axios.get(
      url,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      /* Formato para el reporte
        [
          {
            name: "Linea A",
            total: 30,
            defectuosas: ### (colocar los valores del excel que devuelve)
            ... (colocar los valores del excel que devuelve)
            ...(colocar los valores del excel que devuelve)
            ...(colocar los valores del excel que devuelve)
          }
        ]
      */
      //cambiar defect_code por el campo del nombre de las lineas (name)
      const clients = response.data.map(set => set.defect_code);
      //cambiar id por el campo del nombre de los % (total)
      const totals = response.data.map(set => set.id);
      this._loadingErrorsByLine = true
      const data = (canvas) => {
        var ctx = canvas.getContext("2d");

        var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
        gradientStroke.addColorStop(0, '#80b6f4');
        gradientStroke.addColorStop(1, '#FFFFFF');

        var gradientFill = ctx.createLinearGradient(0, 500, 0, 50);
        gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
        gradientFill.addColorStop(1, "rgba(249, 99, 59, 0.40)");
        return {
          labels: clients,
          datasets: [{
            label: "% Aceptacin Por Lnea",
            borderColor: "#f96332",
            pointBorderColor: "#FFF",
            pointBackgroundColor: "#f96332",
            pointBorderWidth: 2,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 1,
            pointRadius: 4,
            fill: false,
            backgroundColor: gradientFill,
            borderWidth: 2,
            data: totals
          }]
        }
      };
      {/* meter el response del reporte en report para agarrar el valor en los datos de la derecha de la grafica */ }
      this._isMounted && this.setState({
        errorsByLine: data,
        report: response.data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getErrorsByLine');
    }.bind(this));
  }

  getClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/get_all_clients/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._ClientsLoaded = true;
      const { data } = response;
      data.map((line) => {
        this.state.clients.push({
          value: line.id,
          label: line.name
        })

      })
      /*  this.setState({
         clients: data.map((line) => {
           return {
             value: line.id,
             label: line.name,
           };
         })
       }); */
      this.state.clients.push({
        value: 0,
        label: "TODOS"
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getClients');
    }.bind(this));
  }

  get_numberWeek = (d) => {

    // Copy date so don't modify original
    d = new Date(Date.UTC(d.getFullYear(), d.getMonth(), d.getDate()));
    // Set to nearest Thursday: current date + 4 - current day number
    // Make Sunday's day number 7
    d.setUTCDate(d.getUTCDate() + 4 - (d.getUTCDay() || 7));
    // Get first day of year
    var yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
    // Calculate full weeks to nearest Thursday
    var weekNo = Math.ceil((((d - yearStart) / 86400000) + 1) / 7);
    // Return array of year and week number
    this.setState({
      week: weekNo
    })
    for (let index = 1; index <= weekNo; index++) {
      this.state.weeks.push({
        value: index,
        label: index.toString(),
      });
      this.state.weeksF.push({
        value: index,
        label: index.toString(),
      });
    }
    this.state.weeks.push({
      value: 0,
      label: "TODOS"
    })

    return [d.getUTCFullYear(), weekNo];

  }

  options = {
    maintainAspectRatio: true,
    scaleShowValues: true,
    tooltips: {
      enabled: false,
      bodySpacing: 4,
      mode: "nearest",
      intersect: 0,
      position: "nearest",
      xPadding: 10,
      yPadding: 10,
      caretPadding: 10
    },
    legend: {
      labels: {
          // This more specific font property overrides the global property
          //fontColor: 'yellow'
      }
  },
    //responsive: 1,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true,
          fontSize:12
        }
      }],
      xAxes: [{
        ticks: {
          beginAtZero: true,
          fontSize:12,
          display:true,
          autoSkip: false
        },
        
      }]
    },
    
  }
  optionsBar = {
    maintainAspectRatio: false,
    
    legend: {
      labels: {
          // This more specific font property overrides the global property
          fontColor: 'red'
      }
  },
    scales: {
        yAxes:{
            grid: {
                drawBorder: true,
                color: '#FFFFFF',
            },
            ticks:{
                beginAtZero: true,
                color: 'white',
                fontSize: 12,
            }
        },
        xAxes: {
            grid: {
                drawBorder: true,
                color: '#FFFFFF',
            },
            ticks:{
                beginAtZero: true,
                color: 'white',
                fontSize: 12,
            }
        },
    }
}

toggleEditModalStyle = (id) => {
  this.setState({
    editModalStyle: !this.state.editModalStyle,
    cardSelected: id,
  });
};


  render() {
    const {week,selectedWeekF, editModalStyle,loading, tab, selectedLine, lineList, weeks, clients, second, place, family, weeksF } = this.state;
    const pageMode = this.checkPageMode();
    const today = new Date();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">{console.log(this.state.weeks, 'week', clients)}
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Modal
          isOpen={editModalStyle}
          toggle={this.toggleEditModalStyle}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader
            className="justify-content-center"
             toggle={this.toggleImportModal}
          >
            Audits Data
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Select week:</Label>
                <Row>
                  <Col sm={12}>
                  {
                      weeks.length > 0 &&
                      <Select
                        options={weeks}
                        placeholder={'Semana'}
                        //isMulti
                        onChange={e => {
                          console.log(e)
                          //this.getErrorsByLine(this.state.selectedLine,e.label)
                          this.setState({ selectedWeekF: e.value })
                        }
                        }
                      />
                    }
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button
              color="secondary"
              onClick={() => this.toggleEditModalStyle()}
            >
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                //  this.poolProcessModal();
                 this.downloadData(selectedWeekF);
                this.toggleEditModalStyle();
              }}
            >
              Download
            </Button>
          </ModalFooter>
        </Modal>
        <Card>
          <CardHeader>
            <Nav tabs>
              <NavItem>
                <NavLink
                  className={tab === "1" ? "active" : ""}
                  onClick={() => {
                    this.toggle("1");
                    this.setState({
                      _loadingErrorsByLine: !this._loadingErrorsByLine
                    })
                  }}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }>
                  % Aceptacin Por Lnea - Lnea
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={tab === "2" ? "active" : ""}
                  onClick={() => {
                    this.toggle("2");
                    this.setState({
                      _loadingErrorsByLine: !this._loadingErrorsByLine
                    })
                  }}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }>
                  % Defectos Por Familia
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  className={tab === "3" ? "active" : ""}
                  onClick={() => {
                    this.toggleEditModalStyle()}}
                  style={
                    pageMode.bg === "bg-ligh"
                      ? {
                        color: "black",
                        borderColor: "black",
                      }
                      : {}
                  }>
                  Download Data
                </NavLink>
              </NavItem>
            </Nav>
          </CardHeader>
          <CardBody>
            <TabContent activeTab={tab}>
              <TabPane tabId="1">
                <Row>
                  <Col sm={3}>
                    <Select
                      options={lineList}
                      placeholder={'Linea'}
                      onChange={e => {
                        //this.getErrorsByLine(e.label, this.state.selectedWeek)
                        this.setState({ selectedLine: e.label })
                      }
                      }
                    />
                  </Col>
                  <Col sm={3}>
                    {
                      weeks.length > 0 &&
                      <Select
                        options={weeks}
                        placeholder={'Semana'}
                        isMulti
                        onChange={e => {
                          console.log(e)
                          //this.getErrorsByLine(this.state.selectedLine,e.label)
                          this.setState({ selectedWeek: e })
                        }
                        }
                      />
                    }
                  </Col>
                  <Col sm={3}>
                    <Select
                      options={second}
                      placeholder={'Globales'}
                      onChange={e => {
                        //this.getErrorsByLine(e.label, this.state.selectedWeek)
                        this.setState({ flag: e.value })
                      }
                      }
                    />
                  </Col>
                  <Col>
                    {/* //cambiar url por el verdadero */}
                    <Button
                      color='primary'
                      onClick={() => this.getErrorsByLine(this.state.selectedLine, this.state.selectedWeek, this.state.flag)}
                      className={"text-secondary"}
                    >
                      Get Data
                    </Button>
                  </Col>
                  {/*    <Col>
                    <a href={`${process.env.REACT_APP_IP}/excel_resume_costing/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        color='primary'
                        className={"text-secondary"}
                      >
                        Download Data
                      </Button>
                    </a>
                  </Col> */}
                </Row>
                <Row>
                  <Col sm='7'>
                    {this._loadingErrorsByLine &&
                      <Bar
                        data={this.state.errorsByLine}
                        options={this.options}
                      />
                    }
                  </Col>
                  {/* cabiar cada uno de los defect_code por el valor correspondiente */}
                  {this._loadingErrorsByLine &&
                    <Col>
                      <Row>
                        <Col>
                          CANTIDAD REVISADA: {this.state.report[0] && this.state.report[0].total_qty_rev}
                        </Col>
                        <Col>
                          CANTIDAD DEFECTUOSAS: {this.state.report[0] && this.state.report[0].total_qty_def}
                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          % DE DEFECTUOSAS: {this.state.report[0] && ((this.state.report[0].total_qty_rev / this.state.report[0].total_qty) * 100).toFixed(2)}%
                        </Col>
                        <Col>
                          PPM: {this.state.report[0] && this.state.report[0].defect_code}
                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          AUDITORIAS RECHAZADAS: {this.state.report[0] && this.state.report[0].r}
                        </Col>
                        <Col>
                          AUDITORIAS REALIZADAS: {this.state.report[0] && this.state.report[0].total}
                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          AUDITORIAS ACEPTADAS: {this.state.report[0] && this.state.report[0].a}
                        </Col>
                        <Col>
                          NIVEL SIGMA: {this.state.report[0] && this.state.report[0].defect_code}

                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          % ACEPTACIN AUDITORIA: {this.state.report[0] && ((this.state.report[0].total_approval) * 100).toFixed(2)}%
                        </Col>
                      </Row>
                    </Col>
                  }
                </Row>
              </TabPane>
              <TabPane tabId="2">
                <Row>
                  <Col sm={3}>
                    <Select
                      options={place}
                      placeholder={'Place'}
                      onChange={e => {
                        //this.getErrorsByLine(e.label, this.state.selectedWeek)
                        this.setState({ selectedPlace: e.label })
                      }
                      }
                    />
                  </Col>
                  <Col sm={3}>
                    {
                      weeksF.length > 0 &&
                      <Select
                        options={weeksF}
                        placeholder={'Semana'}
                        //isMulti
                        onChange={e => {
                          console.log(e)
                          //this.getErrorsByLine(this.state.selectedLine,e.label)
                          this.setState({ selectedWeekF: e.value })
                        }
                        }
                      />
                    }
                  </Col>
                  <Col sm={3}>
                    <Select
                      options={family}
                      placeholder={'Familia'}
                      onChange={e => {
                        //this.getErrorsByLine(e.label, this.state.selectedWeek)
                        this.setState({ selectedFamily: e.label })
                      }
                      }
                    />
                  </Col>
                  <Col>
                    {/* //cambiar url por el verdadero */}
                    <Button
                      color='primary'
                      onClick={() => this.getReportFamily(this.state.selectedPlace, this.state.selectedWeekF, this.state.selectedFamily)}
                      className={"text-secondary"}
                    >
                      Get Data
                    </Button>
                  </Col>
                  {/*    <Col>
                    <a href={`${process.env.REACT_APP_IP}/excel_resume_costing/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        color='primary'
                        className={"text-secondary"}
                      >
                        Download Data
                      </Button>
                    </a>
                  </Col> */}
                </Row>
                <Row>
                  <Col sm='12'>
                    {this._loadingErrorsByLine &&
                      <Bar
                        data={this.state.errorsByLine}
                        options={this.options}
                      />
                    }
                  </Col>
                  {/* {this._loadingErrorsByLine &&
                    <Col>
                      <Row>
                        <Col>
                          CANTIDAD REVISADA: {this.state.report[0] && this.state.report[0].total_qty_rev}
                        </Col>
                        <Col>
                          CANTIDAD DEFECTUOSAS: {this.state.report[0] && this.state.report[0].total_qty_def}
                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          % DE DEFECTUOSAS: {this.state.report[0] && ((this.state.report[0].total_qty_rev / this.state.report[0].total_qty) * 100).toFixed(2)}%
                        </Col>
                        <Col>
                          PPM: {this.state.report[0] && this.state.report[0].defect_code}
                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          AUDITORIAS RECHAZADAS: {this.state.report[0] && this.state.report[0].total_rejected}
                        </Col>
                        <Col>
                          AUDITORIAS REALIZADAS: {this.state.report[0] && this.state.report[0].total}
                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          AUDITORIAS ACEPTADAS: {this.state.report[0] && this.state.report[0].total_approval}
                        </Col>
                        <Col>
                          NIVEL SIGMA: {this.state.report[0] && this.state.report[0].defect_code}

                        </Col>
                      </Row>
                      <br></br>
                      <Row>
                        <Col>
                          % ACEPTACIN AUDITORIA: {this.state.report[0] && ((this.state.report[0].total_approval / this.state.report[0].total) * 100).toFixed(2)}%
                        </Col>
                      </Row>
                    </Col>
                  } */}
                </Row>
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>

      </div>
    );
  }
}
export default FabricReport;

