import React from "react";
import ReactTable from 'react-table';
import axios from 'axios';
import Select from "react-select";
import { matchSorter } from 'match-sorter';
import { downloadFileFromS3 } from '../helpers/awsS3';

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  CardText,
  CardImg,
  CardFooter,
  Label,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter
} from "reactstrap";
import NotificationAlert from "react-notification-alert";
import { element } from "prop-types";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: '#e14eca'
  })
}

const nameComponent =
{
  'S': 'SELF',
  'C1': 'CONTRAST1',
  'C2': 'CONTRAST2',
  'C3': 'CONTRAST3',
  'C4': 'CONTRAST4',
  'C5': 'CONTRAST5',
  'R': 'RIB',
  'R1': 'RIB1',
  'R2': 'RIB2',
  'R3': 'RIB3',
  'R4': 'RIB4',
  'J': 'JERSEY',
  'J1': 'JERSEY1',
  'J2': 'JERSEY2',
  'J3': 'JERSEY3',
  'W': 'WOVEN',
  'W1': 'WOVEN1',
  'WA': 'WATTA',
  'WA1': 'WATTA1',
  'M': 'MESH',
  'M1': 'MESH1',
  'A': 'APLIQUE',
  'A1': 'APLIQUE1',
  'I1': 'INTERLINING',
  'I2': 'INTERLINING2',
  'I3': 'INTERLINING3',
  'WAF': 'WAFFLE',
  'TRI': 'TRICOT',
  'TAF': 'TAFFETA',
  'TA1': 'TAFFETA1'
}

class PoClient extends React.Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      styleOptions: [],
      style: {},
      description: [],
      styles_po: [],
      costingSketch: null,
      techpack: null,
      combos: [],
      colorFabric: null,
      styleSelected: null,
      stylesCombo: [],
      comboc: [],
      dmsArray: [],
      stylesArray: [],
      bomstyle: 0,
      colorStArray: [[]],
      cstarr: [],
      colorFabsArray: [],
      colorFbArray: [],
      cfabs: [],
      fabrics: [],
      Components: [],
    }
  }
  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };


  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }


  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }


  changepage = (data, data2) => {
    const path = "/admin/create_combocomponent";
    this.props.history.push({
      pathname: path,
    });
  };


  getStyle = () => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/bom_header/get_account_approve_list/?flag=D`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({
        styleOptions: response.data.map(client => {
          return {
            value: client.id,
            idBom: client.bom_style.id,
            label: `${client.bom_style.costing} `
          }
        })
      })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'error Loading');
    }.bind(this))

  }


  changeFlag = (id) => {
    const formData = new FormData();
    formData.append("combo", id);
    (
      this.props.location.state.flag === 'N' ?
        formData.append("flag", 'A')
        :
        this.props.location.state.flag === 'A' ?
          formData.append("flag", 'C')
          :
          this.props.location.state.flag === 'C' ?
            formData.append("flag", 'F')
            :
            formData.append("flag", 'D')
    )
    this.toggleLoading();
    axios.post(
      this.costingURL + '/combos/change_status/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      // this._isMounted && this.notify("tr", "success", "APPROVED!");
      this.componentDidMount();
      let path
      (
        this.props.location.state.flag === 'A' ?
          path = "/admin/review_component"
          :
          this.props.location.state.flag === 'N' ?
            path = "/admin/create_combocomponent"
            :
            path = "/admin/consumption_component"
      )

      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "handleApprove");
    }.bind(this))
  }
  approveCombo = (id) => {
    let updateJSON = {
      "components": [
        "id",
        "color_fabric",
        "description",
        //"dm_name"
      ],
      "combo": id,
    };
    this.toggleLoading();
    updateJSON.components = this.props.location.state.styleSelected.components.map((el => {
      return {
        "id": el.id,
        "color_fabric": el.color_fabric_id,
        "description": el.description,
        //"dm_name": el.dm_name
      }
    }))
    axios.post(
      this.costingURL + `/combos/junior_approve/`,
      updateJSON,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      this._isMounted && this.notify("tr", "success", "SAVED!");
      this.componentDidMount();
      const path = "/admin/create_combocomponent"
      this.props.history.push({
        pathname: path
      })
      this.changeFlag(id)
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.notify("tr", "warning", "error");
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  accountApprove = (id, donde) => {
    this.toggleLoading();
    axios.post(
      this.costingURL + '/combos/account_approve/',
      { "combo": id },
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      this._isMounted && this.notify("tr", "success", "APPROVED!");
      this.componentDidMount();
      const path = "/admin/review_component"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  approveConsumptions = (id) => {
    this.toggleLoading();
    let updateJSON = {
      "components": [
        "id",
        "name",
        "consumption"
      ],
      "combo": id,
    };
    updateJSON.components = this.state.dmsArray.map((el => {
      return {
        "id": el.dm_name,
        "name": el.component_name,
        "consumption": el.consumption
      }
    }))
    console.log(updateJSON.components)
    axios.post(
      this.costingURL + '/combos/consumptions_approve/',
      updateJSON,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      this._isMounted && this.notify("tr", "success", "APPROVED!");
      this.componentDidMount();
      const path = "/admin/review_component"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "handleApprove");
    }.bind(this))

  }

  fabricsApproveConsumptions = (id) => {
    this.toggleLoading();
    let updateJSON = {
      "components": [
        "id",
        "old_si_code",
        "name",
        "consumption"
      ],
      "combo": id,
    };
    updateJSON.components = this.state.dmsArray.map((el => {
      return {
        "id": el.dm_name,
        "name": el.component_name,
        "old_si_code": el.old_si_code,
        "consumption": el.consumption
      }
    }))
    axios.post(
      this.costingURL + '/combos/fabrics_approve/',
      updateJSON,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      this._isMounted && this.notify("tr", "success", "APPROVED!");
      this.componentDidMount();
      const path = "/admin/review_component"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "handleApprove");
    }.bind(this))
  }


  orderCombos = (combo, id) => {
    let order = { 'combos': null };
    let orde;
    let ids;
    this.toggleLoading();
    ids = id.flatMap(x => parseInt(x.split(' ')[0]));
    orde = combo.flatMap(x => x.flatMap(y => [y]));
    order.combos = orde
    order = order + ids
    axios.post(
      this.costingURL + '/combos_components/',
      order = {
        "styles": ids,
        "combos": orde
      },
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      this._isMounted && this.notify("tr", "success", "SAVED!");
      // this.componentDidMount();
      const path = "/admin/create_combocomponent"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  validateCombos = (combo) => {
    let order = { 'combos': null };
    let orde;
    let countValidate = 0
    orde = combo.flatMap(x => x.flatMap(y => [y]));
    order.combos = orde
    this.approveCombo(this.state.selectedStyle.id)

  }

  _clientsLoaded = false;
  getStyleInfo = (id) => {
    //this.toggleLoading();
    const formData = new FormData();
    formData.append("styles", id);
    this.toggleLoading();
    axios.post(
      this.costingURL + '/combos_components/get_info_style/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
          "Content-Type": "multipart/form-data"
        }
      }
    ).then(function (response) {
      this._clientsLoaded = true;
      this.toggleLoading();
      this._isMounted &&
        this.setState({
          styles_po: response.data
        })
      this.downloadSketch(this.state.styles_po.costing && this.state.styles_po.costing.pathToSketch)

      //this.toggleLoading();
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'clients');
    }.bind(this));
  }

  getColorFabric = () => {
    //this.toggleLoading();
    axios.get(
      this.costingURL + '/colors_fabrics/',
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function (response) {
      this._clientsLoaded = true;
      this._isMounted &&
        this.setState({
          colorFabric: response.data.map(client => {
            return {
              value: client.id,
              label: `${client.color} `
            }
          })
        })

      //this.toggleLoading();
    }.bind(this)).catch(function (error) {
      //this.toggleLoading();
      this.handleError(error, 'clients');
    }.bind(this));
  }



  addEvent = () => {
    const listForm = [...this.state.filesForm]
    listForm.push('1')
    this.setState({
      filesForm: listForm
    })
  }

  handleChange = (parameter, value) => {
    this.setState({
      style: { ...this.state.style, [parameter]: value }
    });
  }

  _sketchLoaded = false;
  downloadSketch = (file) => {
    const url = this.costingURL + '/costingHeader/download/?key=' + file;
    const headers = {
      params: { "key": file },
      headers: { "Authorization": "Token " + this.userToken }
    }
    axios.get(
      url,
      headers
    ).then(function (response) {
      this._sketchLoaded = true
      this.setState({
        costingSketch: response.data.url
      })
    }.bind(this));
  }

  //_sketchLoaded = false;
  downloadTechPack = (filekye) => {
    //event.preventDefault();
    let url = "";
    url = process.env.REACT_APP_IP + '/costingHeader/download/?key=' + filekye;
    //url = process.env.REACT_APP_IP + '/bom_header/download/?key=' + filekye;
    let config = {
      params: { "key": filekye },
      headers: {
        "Authorization": "Token " + this.userToken
      }
    }
    downloadFileFromS3(url, config).then(function (response) {
      const result = { file: response };
      window.open(result.file);
      this.notify("tr", "success", " downloaded!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'download file');
    }.bind(this));

  }
  rejectCombo = (id) => {
    const formData = new FormData();
    formData.append("combo", id)
    switch (this.props.location.state.flag) {
      case 'A':
        formData.append("flag", 'N');
        break;
      case 'F':
        formData.append("flag", 'C');
        break;
    }
    axios.post(
      this.costingURL + '/combos/return_combo/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.componentDidMount();
      this._isMounted && this.notify("tr", "success", "REJECTED!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  orderCombos = (combo) => {
    let order = { 'combos': null };
    let orde;
    orde = combo.flatMap(x => x.flatMap(y => [y]));
    this.toggleLoading();
    order.combos = orde
    axios.post(
      this.costingURL + '/combos_components/',
      order,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this.toggleLoading();
      this._isMounted && this.notify("tr", "success", "SAVED!");
      this.componentDidMount();
      const path = "/admin/create_combocomponent"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  componentDidMount() {
    this._isMounted = true;
    //this.getFabrics()
    this.getColorFabric()
    this.downloadSketch(this.props.location.state.stylesCombo && this.props.location.state.stylesCombo.pathtoTechpack)
    this.setState({
      stylesCombo: this.props.location.state.stylesCombo
    })

  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  handleConsumptions = (consumo, component, dm) => {
    console.log(this.state.dmsArray)
    this.state.dmsArray.map((el) => {
      if (el.dm_id === dm && el.component_name === component) {
        el.consumption = consumo
      }
    })
  }

  getFabrics = () => {
    axios.get(
      process.env.REACT_APP_IP + '/fabrics/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.toggleLoading();
      this._loadingFabric = true;
      this._isMounted &&
        this.setState({
          fabrics: response.data.map(client => {
            return {
              value: client.id,
              label: `${client.dm}`
            }
          })
        });
      this.toggleLoading();

    }.bind(this)).catch(function (error) {
      this.handleError(error, 'get fabrics');
    }.bind(this));
  }

  changeComponent = (id) => {
    this.toggleLoading()
    const formData = new FormData();
    formData.append("component", id)
    formData.append("flag", 'D')
    axios.post(
      this.costingURL + '/combos/change_status_component/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      /* this.componentDidMount();
      const path = "/admin/create_combocomponent";
      this.props.history.push({
        pathname: path,
      }); */
      this.toggleLoading()
      this._isMounted && this.notify("tr", "success", "DELETED!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleApprove");
      this.toggleLoading()
    }.bind(this))
  }

  selectColorFabric = (option, key, styleKey) => {
    this.props.location.state.styleSelected.components[styleKey].color_fabric_id = option.value;
    //este console log ayuda a que se se le haga set al estado correctamente **N O  R E M O V E R**
    console.log(this.props.location.state.styleSelected.components[styleKey].color_fabric_id)
  }

  selectDM = (option, key, dmKey) => {

    this.props.location.state.styleSelected.components[dmKey].dm_name = option.label;
    let dmN = this.props.location.state.styleSelected.components[dmKey].dm_name
    console.log(dmN)
  }

  render() {

    const {
      loading,
      uploadPO,
      selectedFile,
      activePo,
      clients,
      filesForm,
      styleOptions,
      styles_po,
      costingSketch,
      styleSelected,
      list,
    } = this.state;

    const pageMode = this.checkPageMode();
    let id = 1
    let combo = (
      styles_po.colors && styles_po.colors.map((index, ids) => (
        styles_po.dms && styles_po.dms.map((dm, key) => (
          {
            'dm': dm.id,
            'color': index.id,
            'style': styles_po.style.id,
            'description': '',
            'color_fabric': ''
          }
        ))))
    )

    let array = []
    let array2 = []
    let colors = (
      this.props.location.state.stylesCombo && this.props.location.state.stylesCombo.components.map((index, current) => {
        for (let cont = 0; cont < (this.state.stylesCombo.components && (this.state.stylesCombo.components.length)); cont++) {
          if (this.state.stylesCombo && (this.state.stylesCombo.components[cont].dm_id === index.dm_id)) {

            array.push(this.state.stylesCombo && (this.state.stylesCombo.components[cont].color_bom_name))
            array.push(' ; ')
            this.state.stylesCombo && (this.state.stylesCombo.components[current].style_colors = index.color_bom_name)
          }
        }
        this.state.stylesCombo.components && (this.state.stylesCombo.components[current].style_colors = array)
        array = []
      },

        this.props.location.state.stylesCombo && this.props.location.state.stylesCombo.components.map((index, current) => {
          this.state.stylesCombo.components && (this.state.stylesCombo.components[current].color_fabric_id = (index.color_fabric && index.color_fabric.id))

          for (let cont = 0; cont < (this.state.stylesCombo.components && (this.state.stylesCombo.components.length)); cont++) {
            if (this.state.stylesCombo && (this.state.stylesCombo.components[cont].dm_id === index.dm_id)) {

              array2.push(this.state.stylesCombo && (this.state.stylesCombo.components[cont].color_fabric && this.state.stylesCombo.components[cont].color_fabric.color))
              array2.push(' ; ')
              this.state.stylesCombo && (this.state.stylesCombo.components[current].cfabrics = (index.color_fabric && index.color_fabric.color))
            }
          }
          this.state.stylesCombo.components && (this.state.stylesCombo.components[current].cfabrics = array2)
          array2 = []
        }
        )))




    let dmcomp = (
      this.state.dmsArray = this.props.location.state.stylesCombo && this.props.location.state.stylesCombo.components.reduce((acc, current) => {
        { }

        const x = acc.find(item => (item.component_name === current.component_name) && (item.dm_name === current.dm_name));

        if (!x) {

          return acc.concat([current]);

        } else {

          return acc;

        }
      },
        [])
    )

    let style = styleOptions.map(client => {
      return {
        value: client.id,
        // idBom: client.bom_style.id,
        label: `${client.costing} `
      }
    })
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Row className="justify-content-center mt-5">
          <Col md={{ size: 11, order: 1 }}>
            <Row>
              {this.props.location.state.stylesCombo && (
                <>
                  <Col sm="6">
                    <Card>
                      <CardHeader>CostSheet Info</CardHeader>
                      <CardBody>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            className="text-info"
                            sm={4}
                          >
                            Client
                          </Label>
                          <Col sm={8}>
                            {this.props.location.state.stylesCombo.client &&
                              this.props.location.state.stylesCombo.client}
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            className="text-info"
                            sm={4}
                          >
                            Costing Style
                          </Label>
                          <Col sm={8}>
                            {this.props.location.state.stylesCombo
                              .costing_style &&
                              this.props.location.state.stylesCombo
                                .costing_style}
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            className="text-info"
                            sm={4}
                          >
                            Season
                          </Label>
                          <Col sm={8}>
                            {this.props.location.state.stylesCombo &&
                              this.props.location.state.stylesCombo
                                .costing_season}
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            className="text-info"
                            sm={4}
                          >
                            BOM Style
                          </Label>
                          <Col sm={8}>
                            {this.props.location.state.stylesCombo &&
                              this.props.location.state.stylesCombo
                                .styles}
                          </Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            className="text-info"
                            sm={4}
                          >
                            Techpack
                          </Label>
                          <Button
                            color="info"
                            className="btn-simple"
                            size="sm"
                            onClick={() =>
                              this.downloadTechPack(
                                this.props.location.state.stylesCombo &&
                                this.props.location.state.stylesCombo
                                  .pathtoTechpack
                              )
                            }
                          >
                            <i className="tim-icons icon-cloud-download-93" />
                            TECHPACK
                          </Button>
                        </FormGroup>
                      </CardBody>
                    </Card>
                  </Col>
                  <Col sm="6">
                    <Card>
                      <CardBody>
                        <FormGroup row>
                          <Label
                            for="exampleEmail"
                            className="text-info"
                            sm={4}
                          >
                            Sketch
                          </Label>
                          <Col sm={8}>
                            <CardImg
                              top
                              src={costingSketch && costingSketch}
                              alt="skecth"
                              style={{
                                maxWidth: "300px",
                                maxHeight: "300px",
                              }}
                            />
                          </Col>
                        </FormGroup>
                      </CardBody>
                    </Card>
                  </Col>
                  <>
                    {this.props.location.state.flag === "F" ||
                      this.props.location.state.flag === "C"
                      ? this.state.dmsArray.map((index, key) => (
                        <Col sm="4">
                          <Card>
                            <CardBody>
                              <FormGroup row>
                                <Label
                                  for="exampleEmail"
                                  className="text-info"
                                  sm={12}
                                >
                                  {`DM:` + ` `}
                                  <Label>{index.dm_name}</Label>
                                </Label>
                                <Label
                                  for="exampleEmail"
                                  className="text-info"
                                  sm={12}
                                >
                                  {"Component Name:" + " "}
                                  <Label>{index.component_name}</Label>
                                </Label>
                                <Label
                                  for="exampleEmail"
                                  className="text-info"
                                  sm={12}
                                >
                                  {"Color Styles:" + " "}
                                  <Label>{index.style_colors}</Label>
                                </Label>
                                <Label
                                  for="exampleEmail"
                                  className="text-info"
                                  sm={12}
                                >
                                  {"Color Fabrics:" + " "}
                                  <Label>
                                    {this.props.location.state.flag === "A" ||
                                      this.props.location.state.flag === "C" ? (
                                      index.cfabrics
                                    ) : this.props.location.state.flag ===
                                      "F" ||
                                      this.props.location.state.flag ===
                                      "C" ? (
                                      <>
                                        {
                                          //this.state.stylesCombo.components && (this.state.stylesCombo.components[key].color_fabric_id = index.color_fabric.id)
                                          index.cfabrics
                                        }
                                      </>
                                    ) : (
                                      this.state.colorFabric &&
                                      <Select
                                        options={
                                          this.state
                                            .colorFabric
                                        }
                                        onChange={(e) => {
                                          this.state.dmsArray[
                                            key
                                          ].color_fabric = index.value;
                                        }}
                                      />
                                    )}
                                  </Label>
                                </Label>

                                <Label
                                  for="exampleText"
                                  sm={12}
                                  className="text-info"
                                >
                                  Description
                                </Label>
                                <Col sm={12}>
                                  <Label>
                                    {this.props.location.state.flag === "A" ||
                                      this.props.location.state.flag === "F" ||
                                      this.props.location.state.flag === "C" ? (
                                      index.description
                                    ) : (
                                      <Input
                                        type="textarea"
                                        // value={this.state.stylesCombo.components && (this.state.stylesCombo.components[key].description != null? this.state.stylesCombo.components[key].description: '')}

                                        onChange={(e) => {
                                          this.state.stylesCombo.components[
                                            key
                                          ] &&
                                            (this.state.stylesCombo.components[
                                              key
                                            ].description = e.target.value);
                                        }}
                                      />
                                    )}
                                  </Label>
                                </Col>
                                {this.props.location.state.flag === "C" && (
                                  <>
                                    <Label
                                      for="exampleText"
                                      sm={12}
                                      className="text-primary"
                                    >
                                      CONSUMPTION
                                    </Label>
                                    <Col sm="12">
                                      <Input
                                        type="input"
                                        // value={this.state.stylesCombo.components && this.state.stylesCombo.components[key].description}

                                        onChange={(e) => {
                                          //this.state.stylesCombo.components[key] && (this.state.stylesCombo.components[key].consumption = e.target.value)
                                          this.handleConsumptions(
                                            e.target.value,
                                            index.component_name,
                                            index.dm_id
                                          );
                                        }}
                                      />
                                    </Col>
                                  </>
                                )}
                                {this.props.location.state.flag === "F" && (
                                  <>
                                    <Label
                                      for="exampleText"
                                      sm={12}
                                      className="text-primary"
                                    >
                                      Fabric Internal Code
                                    </Label>
                                    <Col sm="12">
                                      <Input
                                        type="input"
                                        placeholder={index.old_si_code}
                                        onChange={(e) => {
                                          this.state.dmsArray[
                                            key
                                          ].old_si_code = e.target.value;
                                        }}
                                      />
                                    </Col>
                                    <Label
                                      for="exampleText"
                                      sm={12}
                                      className="text-primary"
                                    >
                                      CONSUMPTION
                                    </Label>
                                    <Col sm="12">
                                      <Input
                                        type="input"
                                        placeholder={index.consumption}
                                        onChange={(e) => {
                                          this.state.dmsArray[
                                            key
                                          ].consumption = parseFloat(
                                            e.target.value
                                          );
                                        }}
                                      />
                                    </Col>
                                  </>
                                )}
                                {

                                }
                              </FormGroup>
                            </CardBody>
                          </Card>
                        </Col>
                      ))
                      : this.props.location.state.stylesCombo &&
                      this.props.location.state.stylesCombo.components.map(
                        (index, key) => (
                          <Col sm="4">
                            <Card>
                              <CardBody>
                                <FormGroup row>
                                  <Label
                                    for="exampleEmail"
                                    className="text-info"
                                    sm={12}
                                  >
                                    {`DM: ` + ` `}
                                    <Label sm={4}>
                                      {this.props.location.state.flag ===
                                        "N" ? (
                                        // <>
                                        //   <Select
                                        //     options={
                                        //       this.state.fabrics &&
                                        //       this.state.fabrics
                                        //     }
                                        //     placeholder={index.dm_name}
                                        //     onChange={(option) => {
                                        //       this.selectDM(
                                        //         option,
                                        //         index.dm_name,
                                        //         key
                                        //       );
                                        //     }}
                                        //   ></Select>
                                        // </>
                                        <><Label>{index.dm_name}</Label></>
                                      ) : (
                                        <><Label>{index.dm_name}</Label></>
                                      )}
                                    </Label>
                                  </Label>
                                  <Label
                                    for="exampleEmail"
                                    className="text-info"
                                    sm={12}
                                  >
                                    {"Component Name:" + " "}
                                    <Label>{index.component_name}</Label>
                                  </Label>
                                  <Label
                                    for="exampleEmail"
                                    className="text-info"
                                    sm={12}
                                  >
                                    {"Color Styles:" + " "}
                                    <Label>{index.color_bom_name}</Label>
                                  </Label>
                                  <Label
                                    for="exampleEmail"
                                    className="text-info"
                                    sm={12}
                                  >
                                    {"Color Fabrics:" + " "}
                                    <Label sm="8">
                                      {this.props.location.state.flag ===
                                        "A" ? (
                                        index.color_fabric.color
                                      ) : this.props.location.state.flag ===
                                        "F" ? (
                                        <>
                                          {
                                            // this.state.stylesCombo.components && (this.state.stylesCombo.components[key].color_fabric = index.color_fabric.color)
                                            index.color_fabric.color
                                          }
                                        </>
                                      ) : (
                                        this.state.colorFabric &&
                                        <Select
                                          options={
                                            this.state
                                              .colorFabric
                                          }
                                          onChange={(option) =>
                                            this.selectColorFabric(
                                              option,
                                              index.id,
                                              key
                                            )
                                          }
                                        />
                                      )}
                                    </Label>
                                  </Label>

                                  <Label
                                    for="exampleText"
                                    sm={12}
                                    className="text-info"
                                  >
                                    Description:
                                  </Label>
                                  <Col sm={12}>
                                    {this.props.location.state.flag === "A" ||
                                      this.props.location.state.flag === "F" ? (
                                      index.description
                                    ) : (
                                      <Input
                                        type="textarea"
                                        // value={this.state.stylesCombo.components && (this.state.stylesCombo.components[key].description != null? this.state.stylesCombo.components[key].description: '')}

                                        onChange={(e) => {
                                          this.state.stylesCombo.components[
                                            key
                                          ] &&
                                            (this.state.stylesCombo.components[
                                              key
                                            ].description = e.target.value);
                                          this.props.location.state.styleSelected.components[
                                            key
                                          ].description = e.target.value;
                                        }}
                                      />
                                    )}
                                  </Col>
                                  {this.props.location.state.flag === "F" && (
                                    <>
                                      <Label
                                        for="exampleText"
                                        sm={12}
                                        className="text-primary"
                                      >
                                        CODE
                                      </Label>
                                      <Col sm="12">
                                        <Input
                                          type="input"
                                          // value={this.state.stylesCombo.components && this.state.stylesCombo.components[key].description}

                                          onChange={(e) => {
                                            this.state.stylesCombo.components[
                                              key
                                            ] &&
                                              (this.state.stylesCombo.components[
                                                key
                                              ].code = e.target.value);
                                          }}
                                        />
                                      </Col>
                                      <Label
                                        for="exampleText"
                                        sm={12}
                                        className="text-primary"
                                      >
                                        CONSUMPTION
                                      </Label>
                                      <Col sm="12">
                                        <Input
                                          type="input"
                                          // value={this.state.stylesCombo.components && this.state.stylesCombo.components[key].description}

                                          onChange={(e) => {
                                            //this.state.stylesCombo.components[key] && (this.state.stylesCombo.components[key].consumption = e.target.value)
                                            this.consumptions(e);
                                          }}
                                        />
                                      </Col>
                                    </>
                                  )}
                                  {this.props.location.state.flag === "N" &&
                                    <Button
                                      //className="btn"
                                      size="sm"
                                      className="ml-3"
                                      color="warning"
                                      type="button"
                                      disabled={index.status === 'D' ? true : false}
                                      onClick={(e) => {
                                        index.status = 'D'
                                        this.changeComponent(index.id)
                                      }}
                                    >
                                      Delete
                                    </Button>
                                  }
                                </FormGroup>
                              </CardBody>
                            </Card>
                            
                          </Col>
                        )
                      )}{" "}
                  </>
                  {this.props.location.state.flag === "A" && (
                    <>
                      <Col sm={{ size: "2", offset: 7 }}>
                        <Button
                          className="btn-fill"
                          color="warning"
                          type="button"
                          onClick={() => {
                            this.rejectCombo(
                              this.props.location.state.styleSelected.id
                            );
                          }}
                        >
                          REJECT
                        </Button>
                      </Col>
                      <Col sm={{ size: "3", offset: 0 }}>
                        <Button
                          className="btn-fill"
                          color="success"
                          type="button"
                          onClick={() => {
                            this.accountApprove(
                              this.props.location.state.styleSelected.id,
                              1039
                            );
                          }}
                        >
                          APPROVE
                        </Button>
                      </Col>
                    </>
                  )}
                  {this.props.location.state.flag === "F" ? (
                    <>
                      <Col sm={{ size: "2", offset: 7 }}>
                        <Button
                          className="btn-fill"
                          color="warning"
                          type="button"
                          onClick={(event) => {
                            this.rejectCombo(
                              this.props.location.state.styleSelected &&
                              this.props.location.state.styleSelected.id
                            );
                          }}
                        >
                          REJECT
                        </Button>
                      </Col>
                      <Col sm={{ size: "3", offset: 0 }}>
                        <Button
                          className="btn-fill"
                          color="success"
                          type="button"
                          onClick={(event) => {
                            this.fabricsApproveConsumptions(
                              this.props.location.state.styleSelected.id
                            );
                          }}
                        >
                          APPROVE
                        </Button>
                      </Col>
                    </>
                  ) : (
                    <></>
                  )}
                  {this.props.location.state.flag === "C" && (
                    <>
                      <Col sm={{ size: "2", offset: 7 }}></Col>

                      <Col sm={{ size: "3", offset: 0 }}>
                        <Button
                          className="btn-fill"
                          color="success"
                          type="button"
                          onClick={(event) => {
                            this.approveConsumptions(
                              this.props.location.state.styleSelected.id
                            );
                          }}
                        >
                          SEND FOR APPROVE
                        </Button>
                      </Col>
                    </>
                  )}
                  {this.props.location.state.flag === "N" && (
                    <>
                      <Col sm={{ size: "2", offset: 4 }}></Col>
                      <Col sm={{ size: "3", offset: 0 }}>
                        <Button
                          className="btn-fill"
                          color="info"
                          type="button"
                          onClick={(event) => {
                            this.changepage()
                          }}
                        >
                          SAVE
                        </Button>
                      </Col>
                      <Col sm={{ size: "3", offset: 0 }}>
                        <Button
                          className="btn-fill"
                          color="success"
                          type="button"
                          onClick={(event) => {
                            this.approveCombo(
                              this.props.location.state.styleSelected.id
                            );
                          }}
                        >
                          SEND FOR APPROVE
                        </Button>
                      </Col>
                    </>
                  )}
                </>
              )}
            </Row>
          </Col>
        </Row>
      </div>
    );
  }
}
export default PoClient;




