import React from "react";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import axios from "axios";

import {
  Form,
  Button,
  TabContent,
  TabPane,
  Col,
  Row,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
  CardHeader,
  Nav,
  NavItem,
  NavLink,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  CardTitle
} from "reactstrap";
import Select from "react-select";
import moment from 'moment'

import { matchSorter } from "match-sorter";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

class Permissions extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  userName = localStorage.getItem("user_name") || "";
  userID = localStorage.getItem("user_id") || "";
  userRole = localStorage.getItem("role") || "";

  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;


  
  constructor() {
    super();
    this.state = {
      loading: false,
      tab: "1",
      code: "",
      dateS: "",
      dateF: "",
      texsun: "",
      texfor: "",
      innotex: "",
      date: "",
      db: [
        {
          'label': 'TEXSUN',
          'value': 'TEXSUN', 
        },
        {
          'label': 'TEXFOR',
          'value': 'TEXFOR', 
        },
        {
          'label': 'INNOTEX',
          'value': 'INNOTEX', 
        },
      ],
      dbSelected: null,
      payroll: null,
    };
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };
  toggleLoadingWAIT = () => {
    setTimeout(() => {
      this.toggleLoading();
    }, 1000);
    this.toggleLoading()
  };

  deleteSuspension = (code, dateS, dateF) => {
    axios
      .get(this.costingURL + `/users_it/delete_suspencion/?code=${code}&dateS=${dateS}&dateF=${dateF}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          console.log(data, 'data')
          this.notify("tr", "success", "Suspension Deleted!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Deleted Suspension");
        }.bind(this)
      );
  };

  updateSuspension = (code, dateS, dateF, param) => {
    axios
      .get(this.costingURL + `/users_it/update_suspencion/?code=${code}&dateS=${dateS}&dateF=${dateF}&flag=${param}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          console.log(data, 'data')
          this.notify("tr", "success", "Suspension Edited!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Edited Suspension");
        }.bind(this)
      );
  };

  extraHours = (date, db, payroll) => {
    axios
      .get(this.costingURL + `/users_it/extra_hours/?init_date=${date}&db=${db}&planilla=${payroll}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  Deletevacations = (date, db, payroll, code) => {
    axios
      .get(this.costingURL + `/users_it/delete_vacations/?date=${date}&db=${db}&planilla=${payroll}&code=${code}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };


  componentDidMount() {
    this._isMounted = true;

  }

  render() {
    const { tab, loading, code, dateF, dateS, texsun, texfor, innotex, date, dbSelected, payroll } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    var valid = function( current ){
      return current.day() !== 0 && current.day() !== 6 && current.day() !== 1 && current.day() !== 2 && current.day() !== 4 && current.day() !== 5;
  };
    const today = new Date();
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <Card>
          <CardHeader>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Nav tabs>
                {<NavItem>
                  <NavLink
                    className={tab === "1" ? "active" : ""}
                    onClick={() => {
                      this.toggle("1");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                          color: "black",
                          borderColor: "black",
                        }
                        : {}
                    }
                  >
                    Delete Suspension
                  </NavLink>
                </NavItem>}
                {
                  <NavItem>
                    <NavLink
                      className={tab === "2" ? "active" : ""}
                      onClick={() => {
                        this.toggle("2");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"Update Suspension"}
                    </NavLink>
                  </NavItem>}
              
              </Nav>
              <br></br>
              <TabContent activeTab={tab}>
                <TabPane tabId="2">
                  <h3>Information</h3>
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Fecha Inicio YYYY-MM-DD</Label>
                        <Input
                          type="text"
                          placeholder={'YYYY-MM-DD'}
                          onChange={(e) => {
                            this.setState({
                              dateS: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Fecha Fin YYYY-MM-DD (Change)</Label>
                        <Input
                          type="text"
                          placeholder={'YYYY-MM-DD'}
                          onChange={(e) => {
                            this.setState({
                              dateF: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.updateSuspension(code, dateS, dateF, 0)
                  }
                  }>
                    Update Suspension
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="1">
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input
                          type="text"
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Fecha Inicio YYYY-MM-DD</Label>
                        <Input
                          type="text"
                          placeholder={'YYYY-MM-DD'}
                          onChange={(e) => {
                            this.setState({
                              dateS: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Fecha Fin YYYY-MM-DD</Label>
                        <Input
                          type="text"
                          placeholder={'YYYY-MM-DD'}
                          onChange={(e) => {
                            this.setState({
                              dateF: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.deleteSuspension(code, dateS, dateF)
                  }
                  }>
                    Delete Suspension
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="3">
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>TEXSUN</Label>
                        <Input
                          type="text"
                          placeholder={'CODE'}
                          defaultValue={this.state.description}
                          onChange={(e) => {
                            this.setState({
                              texsun: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>TEXFOR</Label>
                        <Input
                          type="text"
                          placeholder={'CODE'}
                          onChange={(e) => {
                            this.setState({
                              texfor: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>INNOTEX</Label>
                        <Input
                          type="text"
                          placeholder={'CODE'}
                          onChange={(e) => {
                            this.setState({
                              innotex: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.enablePlanilla(texsun, texfor, innotex, '1')
                  }
                  }>
                    Enable Payroll
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="4">
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>DATE</Label>
                        <ReactDatetime
                      inputProps={{
                        className: "form-control",
                      }}
                      isValidDate={ valid }
                      timeFormat={false}
                      value={this.state.date}
                      onChange={(value) => {
                        this.setState({
                          date: `${value._d.getFullYear()}-${value._d.getMonth() +
                            1}-${value._d.getDate()}`,
                        });
                      }}
                      formNoValidate
                    />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Payroll</Label>
                        <Input
                          type="text"
                          placeholder={'payroll'}
                          defaultValue={this.state.payroll}
                          onChange={(e) => {
                            this.setState({
                              payroll: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                    <FormGroup>
                        <Label>COMPANY</Label>
                    <Select
                            options={this.state.db}
                            onChange={(o) => {
                              this.setState({
                                dbSelected: o.value,
                              });
                            }}
                          />
                          </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.extraHours(date, dbSelected, payroll)
                  }
                  }>
                   Reset Extras Hours
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="5">
                  <Row>
                    <Col md={3}>
                      <FormGroup>
                        <Label>DATE</Label>
                        <ReactDatetime
                      inputProps={{
                        className: "form-control",
                      }}
                      isValidDate={ valid }
                      timeFormat={false}
                      value={this.state.date}
                      onChange={(value) => {
                        this.setState({
                          date: `${value._d.getFullYear()}-${value._d.getMonth() +
                            1}-${value._d.getDate()}`,
                        });
                      }}
                      formNoValidate
                    />
                      </FormGroup>
                    </Col>
                    <Col md={3}>
                      <FormGroup>
                        <Label>Code</Label>
                        <Input
                          type="text"
                          placeholder={'Code'}
                          defaultValue={this.state.code}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={3}>
                      <FormGroup>
                        <Label>Payroll</Label>
                        <Input
                          type="text"
                          placeholder={'payroll'}
                          defaultValue={this.state.payroll}
                          onChange={(e) => {
                            this.setState({
                              payroll: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={3}>
                    <FormGroup>
                        <Label>COMPANY</Label>
                    <Select
                            options={this.state.db}
                            onChange={(o) => {
                              this.setState({
                                dbSelected: o.value,
                              });
                            }}
                          />
                          </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.Deletevacations(date, dbSelected, payroll, code)
                  }
                  }>
                   Reset Extras Hours
                  </Button>
                </TabPane>
              </TabContent>
            </CardBody>
          </CardHeader>
        </Card>
      </div>
    );
  }
}
export default Permissions;
