import React from "react";
import { 
  Row,
  Col,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Button,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";
import NewCostingForm from '../components/CostingCustomComponents/NewCostingForm';
import CostingHistory from '../components/CostingCustomComponents/CostingHistory';
import axios from "axios";
import {
  CloneCosting
} from '../helpers/costing';
import CostSheetResume from '../components/AdminPanelComponents/CostSheetResume';
import CostingCalculations from '../helpers/CostingCalculations';

class Costing extends React.Component {
  _isMounted = false;
  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';
  Access = JSON.parse(localStorage.getItem('permissions') || '{}');
  user_id = localStorage.getItem('user_id') || '0';
  _loadingHeaders = false;
  _loadingClients = false;
  _loadingSeasons = false;

  _formHeader = { headers: {
    "Authorization": "Token " + this.userToken,
    "Content-Type": "multipart/form-data"
  } }

  _jsonHeader = { headers: {
    "Authorization": "Token " + this.userToken,
    "Content-Type": "application/json"
  } }

  constructor(props) {
    super(props);
    this.state = {
      dataTable: [],
      clients: {},
      seasons: {},
      formData: {},
      families: [],
      action: 'insert',
      loading: true,
      ApprovalModal: false,
      detailCalculation: {},
      showStyle: '',
      deleteModal: false,
      toDeleteHeader: 0,
    }
  }

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  nauticaCase = () =>{
    const uID = localStorage.getItem("user_id")
    let {formData} = this.state;
    if((uID === 33 || uID === 45 || uID === 39) && formData.client === "NAUTICA")
      return false
  }

  handleError = (error,task='unknown') => {
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    //console.log(response,message,task)
    if (response.status!==undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors=message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getFamilies();
    this.getSeasons();  
    this.costingHistory();
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: ( <div><div>{msg}</div></div> ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 5
    };    
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  costingHistory = () => {
    axios.get(
      process.env.REACT_APP_IP + '/costingHeader/', 
      { headers:{
        "Authorization": "Token " + this.userToken
      } }
    ).then(function (response) {
      this._loadingHeaders=true;
      this._isMounted && this.setState({dataTable: response.data, loading: false});
    }.bind(this)).catch(function(error){
      this.handleError(error,'costing History');
    }.bind(this));
  }

  getFamilies = () => {
    axios.get(
      process.env.REACT_APP_IP + '/costingHeader/get_family_type/', 
      {headers: {
        "Authorization":"Token " + this.userToken
      }}
    ).then( function (response) {
      let idNull = {id : 0, name: 'SIN FAMILIA'}
      this.setState({
        families: response.data
      })
      this.state.families.push(idNull)
      console.log(this.state.families, 'hola')
      
    }.bind(this)).catch(function(error){
      this.handleError(error);
    }.bind(this));
  }

  getClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/', 
      {headers: {
        "Authorization":"Token " + this.userToken
      }}
    ).then( function (response) {
      this._loadingClients = true;
      this._isMounted && this.setState({clients: response.data});
    }.bind(this)).catch(function(error){
      this.handleError(error);
    }.bind(this));
  }

  getSeasons = () => {
    axios.get(process.env.REACT_APP_IP + '/seasons/', {
      headers:{
        "Authorization":"Token " + this.userToken
      }
    }).then( function (response) {
      this._loadingSeasons = true;
      this._isMounted && this.setState({seasons: response.data});
    }.bind(this)).catch(function(error){
      this.handleError(error);
    }.bind(this));
  }

  checkPageMode = () => {
    let pageMode=document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    let modalClass;
    switch(pageMode){
      case true:
        bgclass="bg-ligh";
        headerclass="";
        modalClass="modal-white";
      break;
      default:
        bgclass="bg-dark";
        headerclass="text-primary";
        modalClass="modal-black";
      break;
    }
    return {bg: bgclass,hc: headerclass, mc: modalClass};
  }

  componentWillUnmount(){
    this._isMounted=false;
    this._loadingClients=false;
    this._loadingSeasons=false;
    this._loadingHeaders=false;
  }

  formHelper = (key,value) => {
    this.setState({
      formData: {...this.state.formData, [key] : value}
    });
  }

  handleSubmit = () => {
    let {formData} = this.state;
    if( 
      formData.style !== "" && formData.client !== null &&
      formData.season !== null && formData.techpack !== undefined && 
      formData.techpack.file !== null
    ){
      this.setState({loading: true});
      const headerData = new FormData();
      headerData.append('client', formData.client.value);
      headerData.append('file', formData.techpack.file);
      headerData.append('season', formData.season.value);
      headerData.append('style', formData.style);
      headerData.append('family', formData.family);
      headerData.append('flag', 1);
      axios.post(
        process.env.REACT_APP_IP + '/costingHeader/',
        headerData,
        this._formHeader
      ).then( function() {
        this._isMounted && this.setState({loading: false, formData:{} });
        this.componentDidMount();
        this.notify("tr", "success", "Costing sheet added");
      }.bind(this)).catch( function(error) {
        this._isMounted && this.setState({loading: false, formData:{} });
        this.componentDidMount();
        this.handleError(error,"header data post");
      }.bind(this));
    }else{
      this.notify("tr", "warning", "Complete form to continue.");
    }
  }

  handleQuickCost = () => {
    let {formData} = this.state;
    if( 
      formData.client !== null && formData.season !== null &&
      formData.client !== undefined && formData.season !== undefined &&
      formData.style !== null && formData.style !== undefined
    ){
      this.setState({loading: true});
      let techpack=null;
      if(
        formData.techpack!==null &&
        formData.techpack!==undefined
      ){
        techpack=formData.techpack.file
      }
      const headerData = new FormData();
      headerData.append('client', formData.client.value);
      headerData.append('file', techpack);
      headerData.append('season', formData.season.value);
      headerData.append('style', formData.style);
      headerData.append('flag', 0);
      axios.post(
        process.env.REACT_APP_IP + '/costingHeader/',
        headerData,
        this._formHeader
      ).then( function() {
        this._isMounted && this.setState({loading: false, formData:{} });
        this.componentDidMount();
        this.notify("tr", "success", "QuickCosting sheet added");
      }.bind(this)).catch( function(error) {
        this._isMounted && this.setState({loading: false, formData:{} });
        this.componentDidMount();
        this.handleError(error,"header data post");
      }.bind(this));
    }else{
      this.notify("tr", "warning", "Complete form to continue.");
    }
  }

  cleanDetail = async () => {
    return await axios.post(
      process.env.REACT_APP_IP+'/costingDetail/new_detail/',
      {}, this._jsonHeader
    )
  }

  /**
   * @param {number} detailId costing header id 
   */
  costingDetail = async (detailId) => {
    return await axios.get(
      process.env.REACT_APP_IP + '/costingDetail/' + detailId + '/',
      this._jsonHeader
    );
  }

  CloneProto = (headerId) => {
    this.setState({loading: true});
    CloneCosting(headerId,0).then(function(){
      this.notify("tr","success","CostSheet Copied")
      this.componentDidMount();
      this.setState({loading: false});
    }.bind(this));
  }

  OpenSheet = (headerId,qcFlag) => {
    const path = qcFlag ? "/admin/quick_costing" : "/admin/quotation"
    this.props.history.push({
      pathname: path,
      state: { costingData: headerId }
    })
  }

  ReleaseSheet=(prop)=>{  
    this.setState({loading: true});
    let data = new FormData();
    data.append('id',prop.id)
    data.append('user',1);
    axios.post(
      process.env.REACT_APP_IP + '/administration/return/',
      data,
      this._formHeader
    ).then(function(){
      this.setState({loading: false});
      this._isMounted && this.notify("tr", "success", "Costsheet released!");
      this.componentDidMount();
    }.bind(this)).catch(function(error){
      this.setState({loading: false});
      this.handleError(error,"returnFlag");
    }.bind(this))
  }

  showResume=(data)=>{
    this.setState({loading: true});
    this.costingDetail(data.detail_id).then(function(response){
      let calculations = CostingCalculations(response.data)
      //console.log("calcs",calculations)
      //console.log("data",response.data)
      this.setState({
        showStyle: data.style,
        detailCalculation: calculations,
        loading: false
      });
      this.toggleApprovalModal()
    }.bind(this))
  }

  toggleApprovalModal = () => {
    this.setState({
      ApprovalModal: !this.state.ApprovalModal
    });
  }
  
  toggleDeleteModal = () => {
    this.setState({
      deleteModal: !this.state.deleteModal
    });
  }

  handleRemoveCosting = (headerId) => {
    if (headerId > 0) {
      axios.delete(
        process.env.REACT_APP_IP + '/costingHeader/' + headerId + '/',
        { headers:{
          "Authorization": "Token " + this.userToken
        } }
      ).then(function(){
        this.notify("tr", "success", "CostSheet Removed!");
        this.toggleDeleteModal();
        this.componentDidMount();
      }.bind(this)).catch(function(error){
        this.toggleDeleteModal();
        this.handleError(error,'remove costing header');
      }.bind(this));
    }
  }

  render() {
    const pageMode=this.checkPageMode();
    let inverted = pageMode.bg==="bg-ligh" ? 'inverted' : '';
    return (<>
      <div className="blackdiv" id="blackdiv" 
        style={{display:this.state.loading?'flex':'none'}}
      >
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content">
              <div className="ui text loader">Loading</div>
            </div>
          </div>
          <div style={{minHeight:400}}><p>&nbsp;</p></div>
        </div>
      </div>
      
      <Modal isOpen={this.state.ApprovalModal}
        toggle={this.toggleApprovalModal}
        size={'lg'}
        style={{margin: '1vh auto 0px'}}
      >
        <ModalHeader toggle={this.toggleApprovalModal} tag={'h2'}>
          {this.state.showStyle}
        </ModalHeader>
        <ModalBody>
          <CostSheetResume 
            detailCalculation={this.state.detailCalculation}
            resume={true}
          />
        </ModalBody>
      </Modal>

      <Modal isOpen={this.state.deleteModal}
        toggle={this.toggleDeleteModal}
        size={'sm'}
        style={{margin: '1vh auto 0px'}}
      >
        <ModalHeader toggle={this.toggleDeleteModal} tag={'h2'}>
          {this.state.showStyle}
        </ModalHeader>
        <ModalBody>
          Are you sure you want to remove the CostSheet?
        </ModalBody>
        <ModalFooter>
          <Button 
            color="primary"
            className="float-right" 
            onClick={
              () => this.handleRemoveCosting(this.state.toDeleteHeader)
            }
          >
            Confirm
          </Button>{' '}
          <Button 
            color="secondary" 
            onClick={this.toggleDeleteModal}
            className="float-right" 
          >
            Cancel
          </Button>
        </ModalFooter>
      </Modal>

      <div className="content">

        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Row>
          <Col md={{size: 10, order: 1, offset: 1}}>
            {this._loadingClients && this._loadingSeasons && 
            this._loadingHeaders &&  this.Access.costing.create &&
            <NewCostingForm
              clients={this.state.clients}
              seasons={this.state.seasons}
              formData={this.state.formData}
              familiesData={this.state.families}
              handleForm={this.formHelper}
              onSubmit={this.handleSubmit}
              QuickCost={this.handleQuickCost}
            />}
          </Col>
        </Row>
        <Row>
          <Col md={{size: 10, order: 2, offset: 1}}>
            {this.Access.costing.view && this.nauticaCase() ?
            <CostingHistory 
              dataTable={this.state.dataTable}
              editCosting={this.handleEditCosting}
              removeCosting={(headerId)=>{
                this.setState({
                  toDeleteHeader: headerId
                });
                this.toggleDeleteModal()
              }}
              cloneCosting={this.CloneProto}
              OpenSheet={this.OpenSheet}
              ReleaseSheet={this.ReleaseSheet}
              showResume={this.showResume}
            /> :
            <CostingHistory 
              dataTable={this.state.dataTable}
              removeCosting={(headerId)=>{
                this.setState({
                  toDeleteHeader: headerId
                });
                this.toggleDeleteModal()
              }}
              cloneCosting={this.CloneProto}
              OpenSheet={this.OpenSheet}
              ReleaseSheet={this.ReleaseSheet}
              showResume={this.showResume}
            />}
          </Col>
        </Row>

      </div>
    </>);
  }
}

export default Costing;
