import React from "react";
import ReactTable from "react-table";
import download from "downloadjs";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  Label,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";
import { downloadFileFromS3 } from "../helpers/awsS3";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};
const CheckStyle = {
  opacity: 100,
  visibility: "visible",
};

class PoClient extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      productOrder: null,
      styles_po: null,
      filesForm: ["1"],
      selectedFile: null,
      uploadPO: false,
      uploadAllPO: false,
      resumePO: false,
      msd: false,
      clientList: [],
      fileList: [],
      flagBuy: false,
      flagSMS : false,
      week: 0
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  uploadChangeFilePO = (event) => {
    this.toggleLoading();
    for (const i in this.state.fileList) {
      const formData = new FormData();
      formData.append("file", this.state.fileList[0]);
      formData.append(
        "clients",
        this.state.flagBuy ? "-1" : this.state.flagSMS ? "-2" : this.state.clientList[i] ,
        
      );
      formData.append('week', this.state.week)
      axios
        .post(process.env.REACT_APP_IP + `/po/create_po/`, formData, {
          headers: {
            Authorization: "Token " + this.userToken,
            "Content-Type": "multipart/form-data",
          },
        })
        .then(
          function() {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "success", "Upload File PO ");
          }.bind(this)
        )
        .catch(
          function(error) {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "warning", "Bad document format ");
          }.bind(this)
        );
    }
    this.toggleUploadPOModal();
    this.state.selectedClientPo.label = "";
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  openPo = (po_id) => {
    const path = "/admin/resume_po";
    this.props.history.push({
      pathname: path,
      state: {
        poId: po_id,
      },
    });
  };

  downloadMSDynamics = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/get_atx_pos_file/?client=${this.state.selectedClientPo}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
              `/admin/get_atx_pos_file/?client=${this.state.selectedClientPo}`
          );
          this.notify("tr", "success", "File downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  getPOS = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + `/po/get_my_po/?flag=U`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            productOrder: response.data.map((pos) => {
              return {
                client_id: pos.client_id,
                create_date: pos.created_at.split(" ", 1),
                delivery_place: pos.delivery_place,
                po_number: pos.po_number,
                po_id: pos.id,
                status: pos.status,
                actions: (
                  <>
                    <Button
                      color="info"
                      size="sm"
                      className="btn-icon btn-link"
                      onClick={() => {
                        this.openPo(pos.id);
                      }}
                    >
                      <i className="fas fa-search" />
                    </Button>
                    <Button
                      color="danger"
                      size="sm"
                      className="btn-icon btn-link"
                      onClick={() => {
                        this.deletePO(pos.id);
                      }}
                    >
                      <i className="fas fa-trash-alt" />
                    </Button>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  getPoResume = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
              `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`
          );
          this.notify("tr", "success", "Resume downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  deletePO = (id) => {
    this.toggleLoading();
    let poF = new FormData();
    poF.append("po_id", id);
    axios
      .post(this.costingURL + "/po/clients_delete_po/", poF, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function() {
          this.notify("tr", "success", "Po Deleted!");
          this.toggleLoading();
          this.componentDidMount();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Deleted");
          this.toggleLoading();
        }.bind(this)
      );
  };

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  approveAllPos = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/po/approve_all_pos/?client=${this.state.selectedClientPo.value}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function() {
          this.notify("tr", "success", "Pos Approved!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Approved");
        }.bind(this)
      );
  };

  toggleUploadAllPOsModal = () => {
    this.setState({
      uploadAllPO: !this.state.uploadAllPO,
    });
  };

  toggleUploadPOModal = () => {
    this.setState({
      uploadPO: !this.state.uploadPO,
    });
  };

  toggleGetResumeModal = () => {
    this.setState({
      resumePO: !this.state.resumePO,
    });
  };

  toggleGetMSDModal = () => {
    this.setState({
      msd: !this.state.msd,
    });
  };

  addClientToList = (clientId) => {
    const list = [...this.state.clientList];
    list.push(clientId.value);
    this.setState({
      clientList: list,
    });
  };

  addFileToList = (event) => {
    this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  addEvent = () => {
    const listForm = [...this.state.filesForm];
    listForm.push("1");
    this.setState({
      filesForm: listForm,
    });
  };

  resetModal = () => {
    const listForm = ["1"];
    this.setState({
      filesForm: listForm,
    });
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getPOS();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const {
      loading,
      uploadPO,
      resumePO,
      selectedFile,
      activePo,
      clients,
      filesForm,
      productOrder,
      msd,
      list,
    } = this.state;
    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Modal
          isOpen={uploadPO}
          toggle={this.toggleUploadPOModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <h3>Upload PO Files</h3>
            <>
              <Row>
                {filesForm &&
                  filesForm.map((newFile) => {
                    return (
                      <>
                        <Col sm={7}>
                          <Select
                            placeholder="Select Client"
                            styles={customStyles}
                            options={clients}
                            onChange={(e) => {
                              this.setState({
                                selectedClientPo: e,
                              });
                              this.addClientToList(e);
                            }}
                          />
                        </Col>
                        <Col sm={3}>
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                            >
                              <input
                                type="file"
                                onChange={this.addFileToList}
                                name="pathToCP_POS"
                                id="input_pathToTechpack"
                                multiple="multiple"
                              />
                              <i className="tim-icons icon-cloud-upload-94" />
                              &nbsp;Po File
                            </Button>
                          </FormGroup>
                        </Col>
                        <Col sm={7}>
                        <Input
                      type='text'
                      placeholder={'Weeks'}
                      onChange={e => { this.setState({ week: e.target.value }) }}
                    />
                        </Col>
                      </>
                    );
                  })}
                <Col sm={2}>
                  {this.state.selectedClientPo &&
                    this.state.selectedClientPo.label === "VANS" && (
                      <Button
                        className="btn-simple"
                        color="primary"
                        size="sm"
                        onClick={this.addEvent}
                      >
                        <i class="fas fa-plus"></i>
                      </Button>
                    )}
                </Col>
                <Col sm={2}>
                  {this.state.selectedClientPo &&
                    this.state.selectedClientPo.label === "THE NORTH FACE" && (
                      <FormGroup check>
                        <Label check>
                          <Input
                            type="checkbox"
                            style={CheckStyle}
                            checked={this.state.flagBuy}
                            onChange={() =>
                              this.setState({
                                flagBuy: !this.state.flagBuy,
                              })
                            }
                          />{" "}
                          BUY
                        </Label>
                      </FormGroup>
                    )}
                  {this.state.selectedClientPo &&
                    this.state.selectedClientPo.label === "VANS" && (
                      <FormGroup check>
                        <Label check>
                          <Input
                            type="checkbox"
                            style={CheckStyle}
                            checked={this.state.flagSMS}
                            onChange={() =>
                              this.setState({
                                flagSMS: !this.state.flagSMS,
                              })
                            }
                          />{" "}
                          SMS
                        </Label>
                      </FormGroup>
                    )}
                    
                </Col>
              </Row>
            </>
            <ModalFooter>
              <Button
                color="secondary"
                onClick={() => {
                  this.toggleUploadPOModal();
                  this.resetModal();
                }}
              >
                Close
              </Button>
              <Button
                color="primary"
                onClick={(event) => this.uploadChangeFilePO(event)}
              >
                Save Changes
              </Button>
            </ModalFooter>
          </ModalBody>
        </Modal>

        <Modal
          isOpen={resumePO}
          toggle={this.toggleGetResumeModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <h3>Download PO Resume</h3>
            <>
              <Row>
                {filesForm &&
                  filesForm.map((newFile) => {
                    return (
                      <>
                        <Col sm={7}>
                          <Select
                            placeholder="Select Client"
                            styles={customStyles}
                            options={clients}
                            onChange={(e) => {
                              this.setState({
                                selectedClientPo: e.value,
                              });
                              this.addClientToList(e);
                            }}
                          />
                        </Col>
                        <Col sm={3}>
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                              onClick={() => {
                                this.getPoResume();
                              }}
                            >
                              <i className="tim-icons icon-cloud-download-93" />
                              &nbsp;Download
                            </Button>
                          </FormGroup>
                        </Col>
                      </>
                    );
                  })}
              </Row>
            </>
            <ModalFooter>
              <Button
                color="secondary"
                onClick={() => {
                  this.toggleGetResumeModal();
                  this.resetModal();
                }}
              >
                Close
              </Button>
            </ModalFooter>
          </ModalBody>
        </Modal>

        {/* <Modal
          isOpen={msd}
          toggle={this.toggleGetMSDModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <h3>Download MSDynamics</h3>
            <>
              <Row>
                {filesForm &&
                  filesForm.map((newFile) => {
                    return (
                      <>
                        <Col sm={7}>
                          <Select
                            placeholder="Select Client"
                            styles={customStyles}
                            options={clients}
                            onChange={(e) => {
                              this.setState({
                                selectedClientPo: e.value,
                              });
                              this.addClientToList(e);
                            }}
                          />
                        </Col>
                        <Col sm={3}>
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                              onClick={() => {
                                this.downloadMSDynamics();
                              }}
                            >
                              <i className="tim-icons icon-cloud-download-93" />
                              &nbsp;Download
                            </Button>
                          </FormGroup>
                        </Col>
                      </>
                    );
                  })}
              </Row>
            </>
            <ModalFooter>
              <Button
                color="secondary"
                onClick={() => {
                  this.toggleGetMSDModal();
                  this.resetModal();
                }}
              >
                Close
              </Button>
            </ModalFooter>
          </ModalBody>
        </Modal> */}

        <Row className="justify-content-center mt-5">
          <Col md={{ size: 10, order: 2 }}>
            <Card>
              <CardBody>
                <Row>
                  <Col sm="9">
                    <h1>All POs</h1>
                  </Col>
                  <Col sm="3">
                    <FormGroup className="text-center">
                      <h4>&nbsp; &nbsp;Download PO Format</h4>
                      <a href={`${process.env.REACT_APP_IP}/admin/get_po_format/`} target="_blank" rel="noopener noreferrer" download>
                      <Button
                        className="btn-simple"
                        color="info"
                        size="sm"
                        //onClick={() => this.toggleGetMSDModal()}
                      >
                        <i className="tim-icons icon-cloud-download-93" />
                        &nbsp; Download PO
                      </Button>
                      </a>
                    </FormGroup>
                  </Col>
                  <Col sm="3">
                    <FormGroup
                      id="formGroup_pathToTechpack"
                      className="text-center"
                    >
                      <h4>&nbsp; &nbsp;Add File for a new PO</h4>
                      <Button
                        className="btn-simple"
                        color="info"
                        size="sm"
                        onClick={() => this.toggleUploadPOModal()}
                      >
                        {/*<input
                          type="file"
                          onChange={this.uploadChangeFilePO}
                          name="pathToCP_POS"
                          id="input_pathToTechpack"
                        />*/}
                        <i className="tim-icons icon-cloud-upload-94" />
                        &nbsp; Upload PO File
                      </Button>
                      <Button
                        className="btn-simple"
                        color="info"
                        size="sm"
                        onClick={() => this.toggleGetResumeModal()}
                      >
                        {/*<input
                          type="file"
                          onChange={this.uploadChangeFilePO}
                          name="pathToCP_POS"
                          id="input_pathToTechpack"
                        />*/}
                        <i className="tim-icons icon-cloud-download-93" />
                        &nbsp; Get PO Resume
                      </Button>
                    </FormGroup>
                  </Col>

                  <br></br>
                  {productOrder && (
                    <>
                      <Card className={`${pageMode.bg} border`}>
                        <CardBody>
                          <ReactTable
                            responsive
                            defaultPageSize={5}
                            data={productOrder}
                            filterable
                            showPaginationTop
                            showPaginationBottom={false}
                            className="-striped -highlight primary-pagination"
                            columns={[
                              {
                                Header: "PO Number",
                                accessor: "po_number",
                                sortable: true,
                                filterMethod: (filter, row) =>
                                  matchSorter(row, filter.value, {
                                    keys: ["po_number"],
                                  }),
                                filterAll: true,
                              },
                              {
                                Header: "Delivery Place",
                                accessor: "delivery_place",
                                sortable: true,
                                filterMethod: (filter, row) =>
                                  matchSorter(row, filter.value, {
                                    keys: ["delivery_place"],
                                  }),
                                filterAll: true,
                              },
                              {
                                Header: "Status",
                                accessor: `status`,
                                sortable: true,
                                filterMethod: (filter, row) =>
                                  matchSorter(row, filter.value, {
                                    keys: ["status"],
                                  }),
                                filterAll: true,
                              },
                              {
                                Header: "Created At",
                                accessor: "create_date",
                                sortable: true,
                                filterMethod: (filter, row) =>
                                  matchSorter(row, filter.value, {
                                    keys: ["create_date"],
                                  }),
                                filterAll: true,
                              },
                              {
                                Header: "Actions",
                                accessor: "actions",
                                sortable: false,
                                filterable: false,
                                width: 100,
                              },
                            ]}
                          />

                          {this.Role === "IT Admin" && (
                            <>
                              <Button
                                sm={5}
                                color="primary"
                                onClick={() => {
                                  this.toggleUploadAllPOsModal();
                                }}
                              >
                                Approve All POS
                              </Button>
                              <Modal
                                isOpen={this.state.uploadAllPO}
                                toggle={this.toggleUploadAllPOsModal}
                                modalClassName={this.checkPageMode().mc}
                              >
                                <ModalBody>
                                  <h3>Approve All Pos</h3>
                                  <>
                                    <Row>
                                      <Col sm={7}>
                                        <Select
                                          placeholder="Select Client"
                                          styles={customStyles}
                                          options={this.state.clients}
                                          onChange={(e) => {
                                            this.setState({
                                              selectedClientPo: e,
                                            });
                                          }}
                                        />
                                      </Col>
                                      <Col sm={3}>
                                        <FormGroup
                                          id="formGroup_pathToTechpack"
                                          className="text-center"
                                        >
                                          <Button
                                            className="btn-simple"
                                            color="info"
                                            size="sm"
                                            onClick={this.approveAllPos}
                                          >
                                            Approve
                                          </Button>
                                        </FormGroup>
                                      </Col>
                                    </Row>
                                  </>
                                  <ModalFooter>
                                    <Button
                                      color="secondary"
                                      onClick={() => {
                                        this.toggleUploadAllPOsModal();
                                        this.resetModal();
                                      }}
                                    >
                                      Close
                                    </Button>
                                  </ModalFooter>
                                </ModalBody>
                              </Modal>
                            </>
                          )}
                        </CardBody>
                      </Card>
                    </>
                  )}
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}
export default PoClient;
