import React from "react";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import axios from "axios";

import {
  Form,
  Button,
  TabContent,
  TabPane,
  Col,
  Row,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
  CardHeader,
  Nav,
  NavItem,
  NavLink,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  CardTitle
} from "reactstrap";
import Select from "react-select";
import moment from 'moment'

import { matchSorter } from "match-sorter";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};
const CheckStyle = {
  opacity: 100,
  visibility: "visible",
};

class Permissions extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  userName = localStorage.getItem("user_name") || "";
  userID = localStorage.getItem("user_id") || "";
  userRole = localStorage.getItem("role") || "";

  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;



  constructor() {
    super();
    this.state = {
      loading: false,
      tab: "1",
      code: "",
      dateS: "",
      dateF: "",
      texsun: "",
      texfor: "",
      innotex: "",
      date: "",
      uploadPO: false,
      filesForm: null,
      fileList: [],
      dates: [],
      selectedDate: null,
      db: [
        {
          'label': 'TEXSUN',
          'value': 'TEXSUN',
        },
        {
          'label': 'TEXFOR',
          'value': 'TEXFOR',
        },
        {
          'label': 'INNOTEX',
          'value': 'INNOTEX',
        },
      ],
      dbSelected: null,
      payroll: null,
      dbNOMINA: [
        {
          'label': 'TEXSUN',
          'value': 'TEXSUN',
        },
        {
          'label': 'TEXFOR',
          'value': 'TEXFOR',
        },
        {
          'label': 'INNOTEX',
          'value': 'INNOTEX',
        },
        {
          'label': 'VISIONTEX',
          'value': 'VISIONTEX',
        },
        {
          'label': 'ESTEC',
          'value': 'ESTEC',
        },
      ],
      dbNOMINAselected: null
    };
  }

  getCallendar = () => {
    let days = [];
    let months = [];
    let years = [];
    for (let step = 1; step < 32; step++) {
      days.push({
        label: step,
      });
    }
    for (let step = 1; step < 13; step++) {
      months.push({
        label: step,
      });
    }
    for (let step = 2023; step < 2030; step++) {
      years.push({
        label: step,
      });
    }
    this.state.days = days;
    this.state.months = months;
    this.state.years = years;
  };

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };
  toggleLoadingWAIT = () => {
    setTimeout(() => {
      this.toggleLoading();
    }, 1000);
    this.toggleLoading()
  };

  deleteSuspension = (code, dateS, dateF) => {
    axios
      .get(this.costingURL + `/users_it/delete_suspencion/?code=${code}&dateS=${dateS}&dateF=${dateF}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          console.log(data, 'data')
          this.notify("tr", "success", "Suspension Deleted!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Deleted Suspension");
        }.bind(this)
      );
  };

  updateSuspension = (code, dateS, dateF, param) => {
    axios
      .get(this.costingURL + `/users_it/update_suspencion/?code=${code}&dateS=${dateS}&dateF=${dateF}&flag=${param}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          console.log(data, 'data')
          this.notify("tr", "success", "Suspension Edited!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Edited Suspension");
        }.bind(this)
      );
  };

  extraHours = (date, db, payroll) => {
    axios
      .get(this.costingURL + `/users_it/extra_hours/?init_date=${date}&db=${db}&planilla=${payroll}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  Deletevacations = (date, db, payroll, code) => {
    axios
      .get(this.costingURL + `/users_it/delete_vacations/?date=${date}&db=${db}&planilla=${payroll}&code=${code}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  enablePlanilla = (payroll, db) => {
    axios
      .get(this.costingURL + `/rrhh/enablePlanilla/?db=${db}&planilla=${payroll}`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.notify("tr", "success", "Success!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error ");
        }.bind(this)
      );
  };

  getDates = () => {
    axios
      .get(this.costingURL + "/rrhh/year_month_list/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          const { data } = response;
          this.setState({
            dates: data.map((provider) => {
              return {
                value: provider.date,
                label: provider.date,
              };
            }),
          });
        }.bind(this)
      );
  };


  componentDidMount() {
    this._isMounted = true;
    this.getDates();
  }

  toggleUploadPOModal = () => {
    this.setState({
      uploadPO: !this.state.uploadPO,
    });
  };

  addFileToList = (event) => {
    this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  uploadChangeFileIGGS = (month, db) => {
    this.toggleLoading();
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/download_iggs_file/?date=${month}&db=${db}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          console.log('response')  
          //this.componentDidMount();
          this.notify("tr", "success", "Iggs downloaded!");
          this.toggleLoading();
          //this.toggleAddMaterial();
          window.open(
            process.env.REACT_APP_IP +
            `/admin/download_iggs_file/?date=${month}&db=${db}`
          );
        //console.log(response)  
        }.bind(this)

      )
      .catch(
        function(error) {
          this.handleError(error, "Report");
          this.toggleLoading();
        }.bind(this)
      );
  };

  render() {
    const { tab, loading, code, date, dbSelected, payroll, uploadPO, dbNOMINA, dates } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    var valid = function (current) {
      return current.day() !== 0 && current.day() !== 6 && current.day() !== 1 && current.day() !== 2 && current.day() !== 4 && current.day() !== 5;
    };
    const today = new Date();
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <Modal
          isOpen={uploadPO}
          toggle={this.toggleUploadPOModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <h3>Download IGSS Files</h3>
            <>
              <Row>
                <Col sm={4}>
                  <Select
                    placeholder="Company"
                    styles={customStyles}
                    options={dbNOMINA}
                    onChange={(e) => {
                      this.setState({
                        dbNOMINAselected: e.value,
                      });
                    }}
                  />
                </Col>
                <Col sm={4}>
                  <Select
                    placeholder="mm"
                    styles={customStyles}
                    options={dates}
                    onChange={(e) => {
                      this.setState({ selectedDate: e.value });
                    }}
                  />
                </Col>
              </Row>
            </>
            <ModalFooter>
              <Button
                color="secondary"
                onClick={() => {
                  this.toggleUploadPOModal();
                  //this.resetModal();
                }}
              >
                Close
              </Button>
              <Button
                color="primary"
                onClick={(event) => this.uploadChangeFileIGGS(this.state.selectedDate, this.state.dbNOMINAselected)}
              >
                Download File
              </Button>
            </ModalFooter>
          </ModalBody>
        </Modal>
        <Card>
          <CardHeader>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Nav tabs>

                {
                  <NavItem>
                    <NavLink
                      className={tab === "3" ? "active" : ""}
                      onClick={() => {
                        this.toggle("3");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"Enable Payroll"}
                    </NavLink>
                  </NavItem>}
                {
                  <NavItem>
                    <NavLink
                      className={tab === "4" ? "active" : ""}
                      onClick={() => {
                        this.toggle("4");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"Extra Hours"}
                    </NavLink>
                  </NavItem>}
                {
                  <NavItem>
                    <NavLink
                      className={tab === "2" ? "active" : ""}
                      onClick={() => {
                        this.toggle("2");
                      }}
                      style={
                        pageMode.bg === "bg-ligh"
                          ? {
                            color: "black",
                            borderColor: "black",
                          }
                          : {}
                      }
                    >
                      {"IGSS"}
                    </NavLink>
                  </NavItem>}
              </Nav>
              <br></br>
              <TabContent activeTab={tab}>
                <TabPane tabId="2">
                  <Row>

                    <Col md={4}>
                      <Button
                        className="btn-simple"
                        color="info"
                        size="sm"
                        onClick={() => this.toggleUploadPOModal()}
                      >
                        {/*<input
                          type="file"
                          onChange={this.uploadChangeFilePO}
                          name="pathToCP_POS"
                          id="input_pathToTechpack"
                        />*/}
                        <i className="tim-icons icon-cloud-download-93" />
                        &nbsp; Download IGSS File
                      </Button>
                      {/*    <Button
                        className="btn-simple"
                        color="info"
                        size="sm"
                        onClick={() => this.toggleGetResumeModal()}
                      >
                    
                        <i className="tim-icons icon-cloud-download-93" />
                        &nbsp; Download IGSS
                      </Button> */}
                    </Col>
                  </Row>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="3">
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>COMPANY</Label>
                        <Select
                          options={this.state.db}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>PAYROLL</Label>
                        <Input
                          type="text"
                          placeholder={'CODE'}
                          onChange={(e) => {
                            this.setState({
                              payroll: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.enablePlanilla(payroll, dbSelected, '1')
                  }
                  }>
                    Enable Payroll
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="4">
                  <Row>
                    <Col md={4}>
                      <FormGroup>
                        <Label>DATE</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              date: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>Payroll</Label>
                        <Input
                          type="text"
                          placeholder={'payroll'}
                          defaultValue={this.state.payroll}
                          onChange={(e) => {
                            this.setState({
                              payroll: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={4}>
                      <FormGroup>
                        <Label>COMPANY</Label>
                        <Select
                          options={this.state.db}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.extraHours(date, dbSelected, payroll)
                  }
                  }>
                    Reset Extras Hours
                  </Button>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="5">
                  <Row>
                    <Col md={3}>
                      <FormGroup>
                        <Label>DATE</Label>
                        <ReactDatetime
                          inputProps={{
                            className: "form-control",
                          }}
                          isValidDate={valid}
                          timeFormat={false}
                          value={this.state.date}
                          onChange={(value) => {
                            this.setState({
                              date: `${value._d.getFullYear()}-${value._d.getMonth() +
                                1}-${value._d.getDate()}`,
                            });
                          }}
                          formNoValidate
                        />
                      </FormGroup>
                    </Col>
                    <Col md={3}>
                      <FormGroup>
                        <Label>Code</Label>
                        <Input
                          type="text"
                          placeholder={'Code'}
                          defaultValue={this.state.code}
                          onChange={(e) => {
                            this.setState({
                              code: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={3}>
                      <FormGroup>
                        <Label>Payroll</Label>
                        <Input
                          type="text"
                          placeholder={'payroll'}
                          defaultValue={this.state.payroll}
                          onChange={(e) => {
                            this.setState({
                              payroll: e.target.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                    <Col md={3}>
                      <FormGroup>
                        <Label>COMPANY</Label>
                        <Select
                          options={this.state.db}
                          onChange={(o) => {
                            this.setState({
                              dbSelected: o.value,
                            });
                          }}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                  <Button color="info" onClick={() => {
                    this.Deletevacations(date, dbSelected, payroll, code)
                  }
                  }>
                    Reset Extras Hours
                  </Button>
                </TabPane>
              </TabContent>
            </CardBody>
          </CardHeader>
        </Card>
      </div>
    );
  }
}
export default Permissions;
