import React from "react";
import axios from "axios";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import { matchSorter } from "match-sorter";
import Select from "react-select";

import {
  Button,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Label,
  Col,
  Row,
  FormGroup,
  Input,
  Card,
  CardBody,
} from "reactstrap";

class CutRequisition extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      loading: false,
      bomsHeaders: undefined,
      clients: [],
      user: {},
      resumeModal: false,
      tab: "1",
    };
  }


  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  toggleresumeModal = () => {
    this.setState({
      resumeModal: !this.state.resumeModal,
    });
  };

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };
  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  getPoResume = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
        `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function () {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
            `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`
          );
          this.notify("tr", "success", "Resume downloaded!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };


  _loadedMaterials = false;
  getProductionMaterials = () => {
    this.toggleLoading();
    axios
      .get(this.costingURL + "/inventory/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadedMaterials = true;
          this.toggleLoading()
          this.setState({
            materials: response.data.map((material) => {
              return {
                type: material.material_type,
                code: material.material_code,
                desc: material.material_desc,
                unit: material.material_unit,
                quants: material.quantity,
              }
            })
          });

        }.bind(this)
      );
  };

  componentDidMount() {
    this._isMounted = true;
    this.getProductionMaterials();
  }

  render() {
    const { tab, loading } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Nav tabs>
          <NavItem>
            <NavLink
              className={tab === "1" ? "active" : ""}
              onClick={() => {
                this.toggle("1");
              }}
              style={
                pageMode.bg === "bg-ligh"
                  ? {
                    color: "black",
                    borderColor: "black",
                  }
                  : {}
              }
            >
              Consulta de Requisiones
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={tab === "2" ? "active" : ""}
              onClick={() => {
                this.toggle("2");
              }}
              style={
                pageMode.bg === "bg-ligh"
                  ? {
                    color: "black",
                    borderColor: "black",
                  }
                  : {}
              }
            >
              Requisicion
            </NavLink>
          </NavItem>
        </Nav>
        {this._loadedMaterials && (
          <TabContent activeTab={tab}>
            <TabPane tabId="1">
              <h1>Consulta de Requisicion</h1>
              <Card>
                <CardBody>
                  <Row>
                    <Col sm="8">
                      <Row>
                        <Col>
                          <Label>Requisicion</Label>
                          <Select
                            //value={selectedClient ? selectedClient : null}
                            onChange={(o) => {

                            }}
                          />
                        </Col>
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                            }}
                            className="float-left"
                          >
                            Search <i className="fa fa-search"></i>
                          </Button>
                        </Col>
                      </Row>
                      <Row>
                        <Col>
                          <Label>Fecha</Label>
                          <Input
                            onChange={(e) => {
                            }}
                          />
                        </Col>
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                            }}
                            className="float-left"
                          >
                            Refresh <i className="tim-icons icon-refresh-01"></i>
                          </Button>
                        </Col>
                      </Row>
                      <Row>
                        <Col  sm="6">
                          <Label>Corte</Label>
                          <Input
                            onChange={(o) => {
                            }}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                  <br></br>
                  <ReactTable
                    responsive
                    defaultPageSize={1}
                    //data={this.state.materials}
                    filterable
                    className="-striped -highlight primary-pagination"
                    showPaginationBottom={false}
                    columns={[
                      {
                        Header: "CPO",
                        accessor: "type",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["type"], //cambiar keys
                          }),
                        filterAll: true,
                        width: 200,
                      },
                      {
                        Header: "ESTILO",
                        accessor: "desc",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["desc"], //cambiar keys
                          }),

                        filterAll: true,
                        width: 200,
                      },
                      {
                        Header: "COLOR",
                        accessor: "code",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["code"], //cambiar keys
                          }),

                        filterAll: true,
                        width: 200,
                      },
                      {
                        Header: "XS",
                        accessor: "unit",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["unit"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "S",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "M",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "L",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "XL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "2XL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "3XL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "4XL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "5XL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "6XL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },
                      {
                        Header: "TOTAL",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                      },

                    ]}
                  />
                  <br></br>
                  <br></br>
                  <hr></hr>
                  <br></br>
                  <br></br>
                  <ReactTable
                    responsive
                    defaultPageSize={5}
                    //data={this.state.materials}
                    filterable
                    className="-striped -highlight primary-pagination"

                    columns={[
                      {
                        Header: "CODIGO",
                        accessor: "type",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["type"], //cambiar keys
                          }),

                        filterAll: true,
                        width: 150,
                      },
                      {
                        Header: "DESCRIPCION",
                        accessor: "desc",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["desc"], //cambiar keys
                          }),

                        filterAll: true,
                      },
                      {
                        Header: "CONSUMO",
                        accessor: "code",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["code"], //cambiar keys
                          }),

                        filterAll: true,
                        width: 150,
                      },
                      {
                        Header: "DESPACHADAS",
                        accessor: "unit",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["unit"], //cambiar keys
                          }),
                        filterAll: true,
                        width: 150,
                      },
                      {
                        Header: "PENDIENTES",
                        accessor: "quants",
                        filterMethod: (filter, row) =>
                          matchSorter(row, filter.value, {
                            keys: ["quants"], //cambiar keys
                          }),
                        filterAll: true,
                        width: 150,
                      },
                    ]}
                  />
                </CardBody>
              </Card>
            </TabPane>
          </TabContent>
        )}
        {this._loadedMaterials && (
          <TabContent activeTab={tab}>
            <TabPane tabId="2">
              <h1>Requisicion</h1>
              <Card>
                <CardBody>
                  <Row>
                    <Col sm="8">
                      <Row>
                        <Col>
                          <Label>Corte</Label>
                          <Select
                            //value={selectedClient ? selectedClient : null}
                            onChange={(o) => {

                            }}
                          />
                        </Col>
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                            }}
                            className="float-left"
                          >
                            Search <i className="fa fa-search"></i>
                          </Button>
                        </Col>
                      </Row>
                      <Row>
                        <Col>
                          <Label>Estilo</Label>
                          <Input
                            onChange={(e) => {
                            }}
                          />
                        </Col>
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                            }}
                            className="float-left"
                          >
                            Refresh <i className="tim-icons icon-refresh-01"></i>
                          </Button>
                        </Col>
                      </Row>
                      <Row>
                        <Col  sm="8">
                          <Label>Color</Label>
                          <Input
                            onChange={(o) => {
                            }}
                          />
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </TabPane>
          </TabContent>
        )}
      </div>
    );
  }
}
export default CutRequisition;
