import React from "react";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import axios from "axios";

import {
  Form,
  Button,
  TabContent,
  TabPane,
  Col,
  Row,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
  CardHeader,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import Select from "react-select";
import { matchSorter } from "match-sorter";

class Suppliers extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      loading: false,
      loadingColors: false,
      edit: false,
      selectedProvider: null,
      providers: [],
      tab: "1",
      code: null,
      name: null,
      abbreviation: null,
      address: null,
      phone: null,
      contact: null,
      time: null,
      company: null,
      currency: null,
      terms: null,
      origin: null,
      paga: null,
      listCompany: [],
      listCurrency: [{ label: "Q", value: "Q" }, { label: "$", value: "$" }],
      listOrigin: [
        { label: "Importado", value: "I" },
        { label: "Nacional", value: "N" },
      ],
      listPaga: [
        { label: "Texsun", value: "Z" },
        { label: "J&T", value: "J" },
        { label: "Texfor", value: "TX" },
      ],
    };
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  loadingProviders = () => {
    this.toggleLoading();
    setTimeout(() => {
      this.setState({
        edit: true,
      })
      this.toggleLoading();
    }, 1000);

  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };
  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  getCompany = () => {
    axios
      .get(this.costingURL + "/company/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            listCompany: data.map((provider) => {
              return {
                value: provider.abbreviation,
                label: provider.name,
              };
            }),
          });
        }.bind(this)
      );
  };
  _loadingProviders = false;
  getProductionProvider = () => {
    axios
      .get(this.costingURL + "/provider/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            providers: data.map((provider) => {
              return {
                value: provider.id,
                label: provider.name,
                abbreviation: provider.abbreviation,
                address: provider.address,
                phone: provider.phone,
                contact: provider.contact,
                time: provider.time,
                currency: provider.currency,
                company: provider.company.name,
                companyAbre: provider.company.abbreviation,
                status: provider.status,
                code: provider.code,
                origin: provider.origin === "I" ? "Importado" : "Nacional",
                terms: provider.terms,
                last_code: provider.last_code,
                paid_by: provider.paid_by
              };
            }),
          });
        }.bind(this)
      );
  };
  deleteProvider = (id) => {
    this.toggleLoading();
    axios
      .delete(this.costingURL + `/provider/${id}/`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function() {
          this.notify("tr", "success", "Provider Deleted!");
          this.toggleLoading();
          this.componentDidMount();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.handleError(error, "Provider not Deleted");
          this.toggleLoading();
        }.bind(this)
      );
  };
  createProvider = () => {
    this.toggleLoading();
    console.log(this.state.providers[0].last_code, 'code')
    let process = new FormData();
    process.append("code", (parseInt(this.state.providers[0].last_code))+ 1);
    process.append("name", this.state.name);
    process.append("abbreviation", this.state.abbreviation);
    process.append("address", this.state.address);
    process.append("phone", this.state.phone);
    process.append("terms", this.state.terms);
    process.append("contact", this.state.contact);
    process.append("time", this.state.time);
    process.append("company", this.state.company);
    process.append("currency", this.state.currency);
    process.append("origin", this.state.origin);
    process.append("paid_by", this.state.paga);

    axios
      .post(this.costingURL + "/provider/", process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this.toggleLoading();
          this.notify("tr", "success", "Provider Created!");
          this.getProductionProvider();
          this._isMounted &&
            this.setState({ newProcessSelected: response.data });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Error Created Provider");
        }.bind(this)
      );
  };
  editProvider = () => {
    this.toggleLoading();
    let process = new FormData();
    process.append("id", this.state.selectedProvider.value);
    //process.append("code", this.state.selectedProvider.code);
    //process.append("name", this.state.selectedProvider.label);
    //process.append("abbreviation", this.state.selectedProvider.abbreviation.toUpperCase());
    //process.append("address", this.state.selectedProvider.address);
    process.append("phone", this.state.selectedProvider.phone);
    process.append("terms", this.state.selectedProvider.terms);
    process.append("contact", this.state.selectedProvider.contact);
    process.append("time", this.state.selectedProvider.time);
    //process.append("company", this.state.selectedProvider.companyAbre);
    //process.append("currency", this.state.selectedProvider.currency);
    //process.append("origin",this.state.selectedProvider.origin === "Importado" ? "I" : "N");
    axios
      .post(this.costingURL + `/provider/update_provider/`, process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this.toggleLoading();
          this.notify("tr", "success", "Provider Edited!");
          this.getProductionProvider();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Error Created Provider");
        }.bind(this)
      );
  };

  componentDidMount() {
    this.getProductionProvider();
    this._isMounted = true;
    this.getCompany();
  }

  render() {
    console.log(this.state.selectedProvider, "currency");
    const { tab, loading } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <Card>
          <CardHeader>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Nav tabs>
                <NavItem>
                  <NavLink
                    className={tab === "1" ? "active" : ""}
                    onClick={() => {
                      this.toggle("1");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                            color: "black",
                            borderColor: "black",
                          }
                        : {}
                    }
                  >
                    Suppliers Catalog
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={tab === "2" ? "active" : ""}
                    onClick={() => {
                      this.toggle("2");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                            color: "black",
                            borderColor: "black",
                          }
                        : {}
                    }
                  >
                    {this.state.edit ? "Edit Provider" : "Create Provider"}
                  </NavLink>
                </NavItem>
              </Nav>
              <br></br>
              <TabContent activeTab={tab}>
                <TabPane tabId="2">
                  <h3>Provider Information</h3>
                  <Row>
                    <Col xs={6}>
                      <FormGroup>
                        <Label>Code</Label>
                        {this.state.edit ? (
                          <Input
                            type="text"
                            maxLength="25"
                            disabled={true}
                            value={
                              this.state.selectedProvider.code
                            }
                            onChange={(e) => {
                              this.setState({
                                code:
                                  parseInt(
                                    this.state.selectedProvider.last_code
                                  ) + 1,
                              });
                            }}
                          />
                        ) : (
                          <Input
                            type="text"
                            maxLength="25"
                            disabled={true}
                             value={
                               parseInt(
                                 this.state.providers[0] && (parseInt(this.state.providers[0].last_code) + 1)
                               ) 
                             }
                        
                          />
                        )}

                        <Label>Name</Label>
                        {this.state.edit ? (
                          <Input
                            type="text"
                            disabled={true}
                            // defaultValue={
                            //   this.state.selectedProvider &&
                            //   this.state.selectedProvider.label
                            // }
                            value={this.state.selectedProvider &&
                              this.state.selectedProvider.label}
                            onChange={(e) => {
                              this.state.selectedProvider.label =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                          {/* <Label>
                          {this.state.label}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.label}
                            onChange={(e) => {
                              this.setState({
                                name: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}

                        <Label>Abbreviation</Label>
                        {this.state.edit ? (
                          <Input
                            type="text"
                            disabled={true}
                            defaultValue={
                              this.state.selectedProvider &&
                              this.state.selectedProvider.abbreviation
                            }
                            onChange={(e) => {
                              this.state.selectedProvider.abbreviation =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                          {/* <Label>
                          {this.state.abbreviation}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.abbreviation}
                            onChange={(e) => {
                              this.setState({
                                abbreviation: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}
                      </FormGroup>
                      <FormGroup>
                        <Label>Address</Label>
                        {this.state.edit ? (
                          <Input
                          disabled={true}
                            defaultValue={
                              this.state.selectedProvider &&
                              this.state.selectedProvider.address
                            }
                            type="text"
                            onChange={(e) => {
                              this.state.selectedProvider.address =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                           {/* <Label>
                          {this.state.address}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.address}
                            onChange={(e) => {
                              this.setState({
                                address: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}
                      </FormGroup>
                      <FormGroup>
                        <Label>Phone</Label>
                        {this.state.edit ? (
                          <Input
                            defaultValue={
                              this.state.selectedProvider &&
                              this.state.selectedProvider.phone
                            }
                            type="text"
                            onChange={(e) => {
                              this.state.selectedProvider.phone =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                           {/* <Label>
                          {this.state.phone}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.phone}
                            onChange={(e) => {
                              this.setState({
                                phone: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}
                      </FormGroup>
                      <FormGroup>
                        <Label>Terms</Label>
                        {this.state.edit ? (
                          <Input
                            type="text"
                            defaultValue={
                              this.state.selectedProvider &&
                              this.state.selectedProvider.terms
                            }
                            onChange={(e) => {
                              this.state.selectedProvider.terms =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                           {/* <Label>
                          {this.state.terms}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.terms}
                            onChange={(e) => {
                              this.setState({
                                terms: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}
                      </FormGroup>
                    </Col>
                    <Col xs={6}>
                      <FormGroup>
                        <Label>Contact</Label>
                        {this.state.edit ? (
                          <Input
                            defaultValue={
                              this.state.selectedProvider &&
                              this.state.selectedProvider.contact
                            }
                            type="text"
                            onChange={(e) => {
                              this.state.selectedProvider.contact =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                           {/* <Label>
                          {this.state.contact}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.contact}
                            onChange={(e) => {
                              this.setState({
                                contact: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}
                      </FormGroup>
                      <FormGroup>
                        <Label>Time</Label>
                        {this.state.edit ? (
                          <Input
                            type="text"
                            defaultValue={
                              this.state.selectedProvider &&
                              this.state.selectedProvider.time
                            }
                            onChange={(e) => {
                              this.state.selectedProvider.time =
                                e.target.value;
                            }}
                          />
                        ) : (
                          <>
                           {/* <Label>
                          {this.state.time}
                          </Label> */}
                          <Input
                            type="text"
                            defaultValue={this.state.time}
                            onChange={(e) => {
                              this.setState({
                                time: e.target.value,
                              });
                            }}
                          />
                          </>
                        )}
                      </FormGroup>
                      <FormGroup>
                        <Label>Company</Label>
                        {this.state.edit ? (
                          <>
                            <br></br>
                            <Input
                            type="text"
                            disabled={true}
                            defaultValue={this.state.selectedProvider &&
                              this.state.selectedProvider.company}
                          />
                          </>
                        ) : (
                          <Select
                            options={this.state.listCompany}
                            onChange={(o) => {
                              this.setState({
                                company: o.value,
                              });
                            }}
                          />
                        )}
                      </FormGroup>
                      <FormGroup>
                        <Label>Currency</Label>
                        {this.state.edit ? (
                          <>
                            <br></br>
                            <Input
                            type="text"
                            disabled={true}
                            defaultValue={this.state.selectedProvider &&
                              this.state.selectedProvider.currency}
                          />
                          </>
                        ) : (
                          <Select
                            options={this.state.listCurrency}
                            onChange={(o) => {
                              this.setState({
                                currency: o.value,
                              });
                            }}
                          />
                        )}

                        <FormGroup>
                          <Label>Origin</Label>
                          {this.state.edit ? (
                            <>
                              <br></br>
                              <Input
                            type="text"
                            disabled={true}
                            defaultValue={this.state.selectedProvider &&
                              this.state.selectedProvider.origin}
                          />
                            </>
                          ) : (
                            <Select
                              options={this.state.listOrigin}
                              onChange={(o) => {
                                this.setState({
                                  origin: o.value,
                                });
                              }}
                            />
                          )}
                        </FormGroup>
                        <FormGroup>
                          <Label>Paid By</Label>
                          {this.state.edit ? (
                            <>
                              <br></br>
                              <Input
                            type="text"
                            disabled={true}
                            defaultValue={this.state.selectedProvider &&
                              this.state.selectedProvider.paid_by}
                          />
                            </>
                          ) : (
                            <Select
                              options={this.state.listPaga}
                              onChange={(o) => {
                                this.setState({
                                  paga: o.value,
                                });
                              }}
                            />
                          )}
                        </FormGroup>
                        {this.state.edit ? (
                          <Button
                            type="button"
                            //size="sm"
                            color="info"
                            className="float-left"
                            onClick={() => {
                              this.editProvider();
                            }}
                          >
                            Edit Provider
                          </Button>
                        ) : (
                          <Button
                            type="button"
                            //size="sm"
                            color="info"
                            className="float-left"
                            onClick={() => {
                              this.createProvider();
                            }}
                          >
                            Create Provider
                          </Button>
                        )}
                        {this.state.edit && (
                          <Button
                            type="button"
                            //size="sm"
                            color="info"
                            className="float-left"
                            onClick={() => {
                              //this.createProvider();
                              this.toggle("2");
                              this.setState({
                                code: null,
                                name: '',
                                label: '',
                                abbreviation: '',
                                address: '',
                                phone: '',
                                contact: '',
                                time: '',
                                company: '',
                                currency: null,
                                terms: null,
                                origin: null,
                                paga: null,
                                edit: !this.state.edit
                              });
                            }}
                          >
                            Create New Provider?
                          </Button>
                        )}
                      </FormGroup>
                    </Col>
                  </Row>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="1">
                  <Col>
                    {this.getProductionProvider && (
                      <ReactTable
                        data={this.state.providers.map((provider) => {
                          return {
                            ...provider,
                            id: provider.value,
                            actions: (
                              <div className="clearfix">
                                {/* <Button
                                  size="sm"
                                  color="danger"
                                  className="float-right"
                                  onClick={() => {
                                    this.deleteProvider(provider.value);
                                    //console.log(provider.value)
                                  }}
                                >
                                  <i className="tim-icons icon-simple-remove" />
                                </Button> */}
                                <Button
                                  size="sm"
                                  color="info"
                                  className="float-right"
                                  onClick={() => {
                                    console.log(provider);
                                    this.loadingProviders()
                                    this.setState({
                                      selectedProvider: provider,
                                    });
                                    this.toggle("2");
                                  }}
                                >
                                  <i className="tim-icons icon-pencil" />
                                </Button>
                              </div>
                            ),
                          };
                        })}
                        filterable
                        defaultPageSize={5}
                        columns={[
                          {
                            Header: "Code",
                            accessor: "code",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["code"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 80,
                          },
                          {
                            Header: "Name",
                            accessor: "label",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["label"],
                              }),
                            filterAll: true,
                          },
                          {
                            Header: "Currency",
                            accessor: "currency",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["currency"],
                              }), //cambiar key
                            filterAll: true,
                            width: 80,
                          },
                          {
                            Header: "Company",
                            accessor: "company",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["company"],
                              }), //cambiar key
                            filterAll: true,
                            width: 150,
                          },
                          {
                            Header: "Time",
                            accessor: "time",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["time"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 80,
                          },
                          {
                            Header: "Terms",
                            accessor: "terms",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["terms"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 80,
                          },
                          {
                            Header: "Phone",
                            accessor: "phone",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["phone"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 130,
                          },
                          {
                            Header: "Origin",
                            accessor: "origin",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["origin"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 130,
                          },
                          {
                            Header: "Contact",
                            accessor: "contact",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["contact"],
                              }),
                            filterAll: true,
                            disabled: false,
                            width: 170,
                          },
                          {
                            Header: "Address",
                            accessor: "address",
                            sortable: true,
                            filterMethod: (filter, row) =>
                              matchSorter(row, filter.value, {
                                keys: ["address"],
                              }),
                            filterAll: true,
                            disabled: false,
                          },
                          {
                            Header: "Actions",
                            accessor: "actions",
                            sortable: true,
                            filterable: true,
                            width: 140,
                          },
                        ]}
                      />
                    )}
                  </Col>
                </TabPane>
              </TabContent>
            </CardBody>
          </CardHeader>
        </Card>
      </div>
    );
  }
}
export default Suppliers;
